@media (max-width: 1699.98px) {
  header {
    .container {
      .main_logo {
        position: static;
        margin-left: 20px;
        margin-right: 20px;
      }

      >.row {
        justify-content: space-between;
        width: 100vw;

        .nav {
          flex-grow: 1;
          justify-content: center;

          .nav-item {
            padding: 68px 4px 30px;

            .nav-link {
              font-size: 13px;
            }
          }

          &:last-of-type {
            justify-content: flex-end;

            .nav-item {
              &:last-child {
                padding-right: 0;

                .nav-link {
                  padding-right: 0;
                }
              }
            }
          }
        }
      }

      .search_burger {
        position: relative;
        left: 0;
        top: 0;

        >div {
          top: -20px;

          >div {
            top: 18px;
            min-width: 55px;
          }
        }

        .search {
          margin-right: 35px;
        }
      }
    }

    #mainMenu {
      [class^=col] {
        &:first-child {
          .title {
            >span {
              display: none;
            }
          }

          .mainMenu {
            margin-top: 160px;
          }
        }
      }
    }
  }
}

@media (max-width: 1279px) {
  .prices {
    >[class^=col] {
      >div {
        .title {
          font-size: 21px;
        }
      }
    }
  }
}

@include media-breakpoint-down(lg) {
  header {
    .container {
      .row {
        .nav {
          &:first-of-type {
            display: none;
          }
        }

      }
    }

    #mainMenu {
      [class^=col] {
        &:first-child {
          .mainMenu {
            >div {
              >div {
                font-size: 17px;
              }

              ul {
                li {
                  a {
                    font-size: 15px;
                  }
                }
              }
            }
          }
        }

        &:last-child {
          .mail {
            .clink {
              font-size: 15px;
            }
          }

          >div {
            .social {
              a {
                margin-right: 15px;

                &:last-of-type {
                  margin-right: 0;
                }
              }
            }
          }

        }
      }
    }
  }

  .prices {
    margin-top: 60px;
    margin-left: calc(50% - 495px);
    margin-bottom: 170px;

    >[class^=col] {
      >div {
        padding: 30px;

        .text {
          font-size: 17px;

          ul {
            li {
              font-size: 15px;
            }
          }
        }
      }
    }

    .btn {
      min-width: 270px;
      left: calc(50% - 135px);
      font-size: 15px;
    }
  }

  .accordion {
    .card {
      .card-header {
        .btn {
          font-size: 17px;
          line-height: 146%;
          padding: 30px 0 20px;

          &.collapsed {
            padding: 30px 40px 30px 0;
          }
        }
      }

      .card-body {
        font-size: 15px;
        line-height: 146%;
      }
    }
  }

  footer {
    .row {
      [class^=col] {
        .bottomContact {
          grid-column-gap: 25px;
          grid-template-columns: auto auto;

          .mail {
            .clink {
              font-size: 14px;
            }
          }

          .clink {
            white-space: nowrap;
          }
        }
      }

      &.copy {
        padding: 0;
        margin-bottom: 40px;

        [class^=col] {
          >a {
            margin-bottom: 50px;
          }
        }
      }
    }
  }
}

@include media-breakpoint-down(md) {
  header {
    .container {
      .row {
        .search_burger {
          padding-right: 30px;
        }
      }
    }
  }

  .breadcrumbs {
    display: none;
  }

  .top_title {
    font-size: 27px;
    line-height: 135%;
    margin-bottom: 60px;
  }

  .top_image {
    min-height: 435px;
  }

  .block_title {
    font-size: 21px;
    line-height: 135%;

    br {
      display: none;
    }
  }

  .link {
    font-size: 15px;
    line-height: 148%;
  }

  .prices {
    margin-bottom: 140px;
  }

  #archiv {
    .slider {
      #slidewrapper {
        width: calc(50% + 50vw);
        overflow-x: auto;
      }

      #slidewrapper::-webkit-scrollbar {
        width: 0;
      }
    }
  }

  #row_slider {
    .row_wrapp {
      width: 100%;
      pointer-events: auto;
    }

    .archiv__left,
    .archiv__right {
      display: none;
    }

    .archiv_btn {
      display: none;
    }
  }

  #see_also {
    margin-bottom: 140px;

    .small_title {
      font-size: 21px;
      font-weight: 400;
      line-height: 135%;
      text-transform: none;

      >br {
        display: none;
      }
    }

    .slider__scroll {
      width: calc(50% + 50vw);
      overflow-x: auto;
      overflow-y: hidden;
    }

    .slider {

      margin-top: 60px;
      margin-left: -15px;

      [class^=col] {
        padding: 0 15px;
        min-width: 370px;
      }
    }

    .slider__scroll::-webkit-scrollbar {
      width: 0;
    }
  }

  .dlink {
    font-size: 17px;
    line-height: 146%;

    span {
      font-size: 13px;
      line-height: 146%;
    }
  }

  footer {
    .dark {
      .row {
        padding: 40px 0;

        [class^=col] {
          &:first-child {
            margin-bottom: 40px;
          }
        }
      }
    }

    .row {
      [class^=col] {
        .bottomMenu {
          margin-top: 40px;
          grid-column-gap: 70px;
        }

        .bottomContact {
          margin-bottom: 40px;
          grid-row-gap: 35px;

          .mail {
            .clink {
              font-size: 17px;
            }
          }

          .clink {
            font-size: 17px;
            margin-bottom: 12px;
          }

          .social {
            >div {
              &:first-child {
                justify-content: flex-start;

                >a {
                  margin-right: 25px;

                  &:last-child {
                    margin-right: 0;
                  }
                }
              }
            }
          }

          &::after {
            content: "";
            position: absolute;
            width: calc(100% - 40px);
            height: 2px;
            background-color: rgba(255, 255, 255, .2);
            bottom: 0;
            left: 20px;
          }
        }
      }
    }
  }
}

@include media-breakpoint-down(sm) {
  header {
    .container {
      .row {

        .nav {
          .nav-item {
            &:last-child {
              display: none;
            }
          }
        }

        .search_burger {
          padding-right: 20px;

          .search {
            margin-right: 20px;
            display: none;
          }

          .burger {
            >div {
              display: none;
            }
          }
        }
      }
    }

    #mainMenu {
      [class^=col] {
        &:first-child {
          overflow: hidden;

          .mainMenu {
            grid-template-columns: 1fr;
            grid-template-rows: repeat(3, auto);
            position: relative;

            .mMenuBack {
              display: block;
              position: absolute;
              font-size: 15px;
              line-height: 148%;
              text-transform: uppercase;
              top: 63px;
              left: 100%;
              margin-top: 0;
              width: 100%;
              background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M13.209 21l-10.83-8.316c-.506-.303-.506-1.065 0-1.369L13.209 3M8.475 12H22' stroke='%237DE2F4' stroke-width='1.6' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
              background-repeat: no-repeat;
              background-position: 20px center;
              padding-left: 120px;
            }

            >div {
              margin-top: 40px;

              >div {
                font-size: 15px;
                background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10.791 21l10.83-8.316c.506-.303.506-1.065 0-1.369L10.791 3M15.525 12H2' stroke='%237DE2F4' stroke-width='1.6' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
                background-repeat: no-repeat;
                background-position: calc(100% - 20px) center;
              }

              ul {
                top: 130px;
                left: calc(100% + 120px);
                right: 20%;
                position: absolute;
                margin-top: 0;
                display: none;

                li {
                  a {
                    font-size: 17px;
                    color: #fff;
                  }
                }

                &.mobileShow {
                  display: block;
                }
              }

              &:first-child {
                margin-top: 60px;
              }
            }

          }
        }

        &:last-child {
          overflow: hidden;

          >div {
            margin-left: 20px;
          }
        }
      }
    }
  }

  footer {
    .row {
      padding: 40px 0;

      [class^=col] {
        .bottomMenu {
          ul {
            li {
              a {
                font-size: 15px;
              }
            }
          }
        }

        .bottomContact {
          font-size: 13px;
        }
      }

      &.organizers {
        display: flex;
        justify-content: space-between;

        [class^=col] {
          a {
            span {
              font-size: 15px;
            }
          }
        }
      }

      &.copy {
        [class^=col] {
          >a {
            font-size: 15px;
            margin: 0 0 10px 0;

            &:first-child {
              margin-top: 40px;
            }

            &:last-child {
              margin-bottom: 40px;
            }
          }
        }
      }
    }
  }
}

@include media-breakpoint-down(xs) {
  header {
    .container {
      .row {
        .nav {
          .nav-item {
            &:first-child {
              // display: none;
            }
          }
        }

        .search_burger {
          .search {
            display: none;
          }

          .burger {
            padding-left: 32px;

            >div {
              display: none;
            }
          }
        }
      }
    }

    #mainMenu {
      &::before {
        display: none;
      }

      [class^=col] {
        min-height: 50vh;

        &:first-child {
          .mainMenu {
            margin-top: 156px;
            margin-bottom: 40px;

            >div {
              &:first-child {
                margin-top: 40px;
              }

              >div {
                font-size: 19px;
                background-position: right center;
              }

              ul {
                top: 90px;
              }
            }

            .mMenuBack {
              font-size: 19px;
              top: 40px;
            }
          }
        }

        &:last-child {
          position: relative;

          >div {
            margin-top: 40px;

            .clink {
              font-size: 17px;
            }

            .mail {
              .clink {
                font-size: 17px;
              }
            }
          }

          &::before {
            position: absolute;
            content: '';
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background-color: #222633;
            opacity: .5;
            z-index: -1;
          }
        }
      }
    }
  }

  .top_title {
    font-size: 21px;
    line-height: 130%;
    margin-bottom: 40px;
  }

  .top_image {
    min-height: 350px;
  }

  .block_title {
    font-size: 21px;
    line-height: 130%;

    br {
      display: none;
    }
  }

  .link {
    font-size: 13px;
  }

  .prices {
    margin-top: 20px;
    flex-wrap: wrap;
    margin-left: -20px;
    margin-bottom: 100px;

    >[class^=col] {
      min-width: auto;
      margin-top: 60px;

      >div {
        padding: 30px 10px;

        .title {
          font-size: 19px;
          margin-bottom: 30px;
        }

        .text {
          margin-bottom: 70px;

          >span {
            font-size: 15px;
            margin: 30px 0;
          }
        }

        .btn {
          width: 300px;
          // max-width: 100%;
          left: calc(50% - 150px);
          font-size: 13px;
          bottom: 30px;
        }
      }

    }

  }

  .accordion {
    margin-bottom: 60px;

    .card {

      .card-header {

        .btn {
          font-size: 15px;
          line-height: 146%;
          padding: 30px 0 20px;
          padding-right: 60px;

          &.collapsed {
            padding: 30px 40px 30px 0;
          }
        }
      }

      .card-body {
        font-size: 13px;
        line-height: 146%;
      }
    }
  }

  #see_also {
    margin-bottom: 100px;

    .small_title {
      font-weight: bold;
      font-size: 19px;
      line-height: 135%;
      text-transform: uppercase;

      >br {
        display: inline;
      }
    }

    .slider {
      margin-top: 60px;
      padding-left: 20px;
      margin-left: -20px;

      [class^=col] {
        min-width: calc(43vw + 10px);
        padding: 0 10px 0 0;
      }

      span {
        bottom: 15px;
        left: 15px;
      }
    }
  }

  footer {
    .dark {
      .row {
        [class^=col] {
          &:first-child {
            font-weight: bold;
            font-size: 17px;
            line-height: 148%;
          }
        }
      }
    }

    .row {
      [class^=col] {
        .bottomContact {
          grid-template-rows: repeat(4, auto);
          grid-template-columns: auto;
          // justify-content: center;

          .clink {
            font-size: 15px;
          }

          .mail {
            .clink {
              font-size: 15px;
            }
          }
        }

        .bottomMenu {
          grid-template-columns: repeat(2, 1fr);
          grid-template-rows: repeat(2, auto);
          grid-row-gap: 30px;
          grid-column-gap: 70px;

          >div {
            >div {
              font-size: 15px;
            }

            &:last-child {
              grid-column: 1/span 2;
              display: grid;
              grid-template-columns: repeat(2, 1fr);
              grid-column-gap: 70px;

              >div {
                grid-column: 1/span 2;
              }
            }
          }

          ul {
            li {
              margin-top: 2px;

              a {
                font-size: 13px;
              }
            }
          }
        }
      }

      &.organizers {
        [class^=col] {
          >a {
            margin-bottom: 40px;

            >div {
              width: 100px;
              text-align: center;

              img {
                max-width: 100%;
              }
            }

            >span {
              font-size: 13px;
            }
          }

          &:last-child {
            >a {
              margin-bottom: 0;
            }
          }
        }
      }

      &.copy {
        font-size: 13px;

        [class^=col] {
          >a {
            font-size: 13px;
          }
        }
      }
    }
  }
}

@media (max-width: 480px) {
  header {
    > .container {
      .row {
        flex-wrap: nowrap;

        .main_logo {
          width: 150px;
          width: 110px;
          margin-right: 0;
          margin-top: 20px;
          margin-bottom: 18px;
        }

        .nav {
          padding-right: 0;

          &:first-of-type {
            display: none;
          }
        }

        .search_burger {
          padding-left: 0;
        }
      }

    }
  }
}