#archive-foto, #archive-categories{
	h1{
		font-family: $tk-aktiv-grotesk;
		font-style: normal;
		font-weight: normal;
		font-size: 33px;
		line-height: 1.15;
		color: $dark;
		@media(max-width: 1279px){
    	font-size: 27px;
    	}
    	@media(max-width: 767px){
    	font-size: 21px;
    	}
	}
	.top_title{
		margin-top: 31px;
		margin-bottom: 80px;
		 @media(max-width: 1279px){
    	margin-top: 60px;
		 margin-bottom: 40px;

    }
	}
}

#archive-categories .return-link{
	margin-bottom: 60px;
	font-size: 17px;
	line-height: 1.48;

	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	a{color: $aquagrey;}
   span{margin-right: 18px;}
   &:hover{
   	a{color: $blue;}
   	span svg path{stroke: $blue;}
   }
   @media(max-width: 1279px){
   	display: none;
   }

}
#archive-categories .top_title{
	@media(max-width: 1279px){
		margin-bottom: 60px;
	}
}
#archive-categories .archive-categories__content{
	padding-bottom: 160px;
	@media(max-width: 1279px){
		padding-bottom: 140px;
	}
	@media(max-width: 767px){
		padding-bottom: 100px;
	}
	.filters-block{
		padding: 40px 20px;
		background-color: $grey;
		@media(max-width: 1279px){
			padding: 0;
		}
	}

	.filters-drop-item +.filters-drop-item{
		margin-top: 40px;
	}

	.filters-drop-submenu .custom-control + .custom-control{
		margin-top: 20px;
	}

	.nav-item[aria-selected="true"]{
		color: $blue;

	}
	.nav-item{

		border:0;
		-webkit-border-radius: 0;
		        border-radius: 0;
		background-color: transparent;
		font-size: 19px;
	    text-transform: uppercase;
	    cursor:pointer;
	    padding: 0;
	    &--current{color: $blue;}
	    &:hover{color: $blue;}
	}


	.archive-tabs__foto{
		a{
			display: block;
			max-width: 480px;
			margin-left: auto;
			margin-right: auto;
			
		}
		.row> div:nth-child(n+3) {margin-top: 40px;}
		p{
			 display: -webkit-box;
			  -webkit-box-orient: vertical;
			  -webkit-line-clamp: 2;
			  overflow: hidden;
			  @include bodytext;
			  color: $black;
			  padding-top: 20px;
		}
		@media(max-width: 1279px){
			margin-top: 60px;
		}
		@media(max-width: 767px){
		   .row> div:nth-child(n+2) {margin-top: 40px;}
		}
	}
}

//-filters
#archive-categories{
	.archive-tabs{
		display: block;
		border-bottom: 0;
	}

}
//VIDEO CATEGORY
#archive-categories{
	.archive-links{
		margin-top: 120px;
		@media(max-width: 1279px){
			margin-top: 40px;
			padding-top: 20px;
			padding-bottom: 20px;
		}
		@media(max-width: 1279px){margin-top: 60px;}
	}
	img{
		max-width: 100%;
	}
	.video-item{
		position: relative;
		cursor:pointer;
		max-width: 783px;
		margin-left: auto;
		margin-right: auto;
		@media(max-width: 1279px){
			max-width: 675px;
		}
		}
	.video-item:after{
		content:"";
		transition: .5s;
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		background-color: rgba(34, 38, 51, .5);
		z-index: 1;
	}
	.video-item:hover:after{
			background-color:  rgba(0, 79, 203, .7);
		}
	.video-item__text{
		position: absolute;
		top: 50%;
		left: 50%;
		width: 373px;
		transform: translate(-50%, -27%);
		z-index: 2;
		display: flex;
		flex-direction: column;
		-webkit-align-items: center;
		        align-items: center;

		font-family: $tk-aktiv-grotesk;
		  font-style: normal;
		  font-weight: normal;
		  font-size: 17px;
		  line-height: 146%;
		color: #fff;
		text-align: center;
		p{
			padding-top: 20px;
			margin: 0;
			width: 100%;
			}

		@media(max-width: 1279px){

			max-width: 270px;
		}
		@media(max-width: 767px){
			font-size: 15px;

		}
		@media(max-width: 359px){
			font-size: 13px;
			width: 280px;
		}

	}
	.archive-tabs__video{
		@media(max-width: 1279px){
			padding-top: 60px;
		}
	}
	.archive-tabs__video .col-12 +.col-12{
		margin-top: 40px;
	}
	.video-item{
		max-height: 450px;
		@media(max-width: 1279px){
			overflow: hidden;
			height: 389px;
			&>img{
				max-width: unset;
				max-height: 100%;
				width: auto;
			}
		}
		@media(max-width: 767px){
			height: 300px;
		}
	}
}

// DOCUMENTS TABLE
#content-table{
	padding-top: 0;
	@media(max-width: 1279px){padding-top: 45px;}
	@media(max-width: 767px){padding-top: 40px;}
	.content-table__row{
		-webkit-justify-content: space-between;
		        justify-content: space-between;
		align-items: center;
		padding-top: 40px;
		padding-bottom: 40px;
		border-bottom: 1px solid rgba(144, 148, 161, .6);
		font-size: 17px;
		line-height: 1.48;
		font-family: $tk-aktiv-grotesk;
		font-style: normal;
		font-weight: normal;
		color: $black;
		p{
			margin: 0;
		}
		@media(max-width: 767px){
			font-size: 13px;
			
			padding-top: 25px;
			padding-bottom: 25px;
			align-items: flex-end;
			&:nth-child(2){border-top: 1px solid rgba(144, 148, 161, .6);}
		}
	}
	.content-table__row:first-child{
		padding-top: 0;
		@media(max-width: 1279px){
				padding-bottom: 20px;;
			}
			@media(max-width: 767px){
				display: none!important;
			}
		}
	.content-table__row:first-child, .content-table__row:first-child .content-table__col:last-child{
		color: $aquagrey;
	}
	.content-table__row:last-child{
		@media(max-width: 1279px){
			border: 0;
		}
	}
	.content-table-block{
		width: 83%;
		flex-shrink: 0;
		-webkit-justify-content: space-between;
		        justify-content: space-between;
		-webkit-align-items: center;
		        align-items: center;
		@media(max-width: 767px){
			flex-wrap: wrap;
			width: 70%;
		}

	}
	.content-table__col:first-child{
		width: 200px;
		@media(max-width: 767px){
			width: 100%;
			margin-bottom: 20px;
		}
	}
	.content-table__col:nth-child(n+2){
		width: 100px;
		@media(max-width: 1279px){
			font-size: 15px;
		}
		@media(max-width: 767px){
			font-size: 13px;
		}
	}
	.content-table__col:last-child{
		font-size: 15px;
		line-height: 1.48;
		letter-spacing: -0.01em;
		// color: $black;
		@media(max-width: 1279px){
			font-size: 13px;

		}
	}
	.content-table-icons{
		width: 88px;
		display: flex;

		-webkit-justify-content: space-between;
		        justify-content: space-between;
		-webkit-align-items: center;
		        align-items: center;
		@media(max-width: 767px){
			width: 70px;
		}
		a{
			display: inline-block;
			width: 24px;
			height: 24px;

		}
		a:hover svg path {
			stroke: $darklightblue !important;
			
		}
        a.ic-see:hover svg path {
        	fill: $darklightblue !important;
        }
	}
	.content-table__row{
		padding-left: 10px;
		padding-right: 10px;
		
	}
	.content-table__row:not(:first-child):hover {
		background-color: $blue;
		color: #ffffff ;
	}
	.content-table__row:not(:first-child):hover  svg path{
	
		stroke: #ffffff;
	}
	.content-table__row:not(:first-child):hover a.ic-see svg path {
		fill: #ffffff;
	}
}
.archive-foto__picture{
	position: relative;
	overflow: hidden;
}
.archive-foto__picture:after{
	content: '';
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	position: absolute;
	background-color: rgba(0, 79, 203, .7);
	opacity: 0;
	transition: .3s linear;
}
.archive-tabs__foto a:hover .archive-foto__picture:after{
		opacity: 1;
}
.archive-tabs__foto a:hover p {
	color: rgba(0, 79, 203, 1) !important;
}