#tnf-online {

  .container {
    padding-left: 0;
    padding-right: 0;
  }

  @include media-breakpoint-down(lg) {
    .container {
      padding-left: 20px;
      padding-right: 20px;
    }

    #broadcast_of_events {

      .articles > div {
        grid-template-columns: 2fr 1fr;
      }
    }
  }

  @include media-breakpoint-down(md) {
    .top_title {
      font-size: 27px;
      line-height: 135%;
      margin-bottom: 60px;
      width: 260px;
    }

    #fulcrum {
      padding: 100px 0;

      .block_title {
        font-size: 21px;
        margin-bottom: 40px;
      }
    }

    #broadcast_of_events {
      padding: 140px 0;

      .pfilter {
        margin-top: 60px;
        justify-content: flex-start;
        margin-bottom: 60px;
        overflow-x: auto;
        overflow-y: hidden;
      }
      .pfilter::-webkit-scrollbar{width: 0%;}

      .row.justify-content-between{
        margin-bottom: 0;
      }

      .articles>div {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr repeat(4, auto);
        row-gap: 20px;
        margin-bottom: 60px;

        .date {
          font-size: 15px;

          span {
            font-size: 32px;
          }
        }

        .location {
          font-size: 15px;
        }

        .title {
          font-size: 17px;
        }

        .intro {
          font-size: 15px;
          padding-bottom: 20px;
        }

        .link{
          height: 15px;
          display: inline-block;
          background-size: contain;
          padding-right: 20px;
        }
      }
    }
  }

  @include media-breakpoint-down(xs) {

    .top_title {
      font-size: 21px;
      line-height: 115%;
      margin-bottom: 60px;

      br {
        display: none;
      }
    }

    .block_title {
      font-size: 21px;
    }

    #fulcrum {
      font-size: 15px;
      line-height: 135%;
      padding: 80px 0;
      letter-spacing: -0.01em;

      .btn-wrapper {
        display: flex;
        justify-content: space-between;
        padding-top: 40px;

        .btn.btn-primary,
        .btn.btn-primary-reverse {
          min-width: 151px;
          padding: 14px 0;
          font-size: 13px;
          font-weight: 400;
        }

        .button-separate {
          padding-right: 0;
        }
      }
    }

    #broadcast_of_events {
      padding: 80px 0 100px 0;

      .order-2{
        padding-right: 0;
      }

      a {
        font-size: 13px;
        line-height: 148%;
        white-space: nowrap;
      }

      .articles>div {

        img {
          height: 300px;
          -o-object-fit: cover;
          object-fit: cover;
          grid-row: 1;
        }

        .date {
          font-size: 13px;

          span {
            font-size: 27px;
          }
        }

        .location {
          font-size: 13px;

          span {
            padding-left: 20px;

            &::before {
              width: 16px;
              height: 16px;
            }
          }
        }

        .title {
          padding-top: 20px;
        }
      }

      .pagination {
        margin-top: 20px;
      }
    }
  }
}