#program {
  .top_image {
    background-image: url(../img/program_top.jpg);
  }

  #timetable {
    margin: 120px 0 0;

    .title {
      margin-bottom: 20px;
    }

    .filters {
      background-color: #F6F6F6;
      color: $black;
      font-size: 17px;
      padding: 0 20px;
      max-height: none;

      > form {
        padding: 40px 0 0 0;
        > div {
          .pidpys {
            font-size: 19px;
            line-height: 18px;
            text-transform: uppercase;
            margin-top: 40px;
          }
          &:first-child {
            .pidpys {
              margin-top: 0;
            }
          }
        } 
        .custom-control-input, .custom-control-label {
          cursor: pointer;
        }
      }

      .btn {
        font-weight: 400;
        font-size: 17px;
        line-height: 150%;
        margin: 0 auto 40px;
        width: 100%;
      }
    }

    #accordionTimetable {
      .card {
        border-radius: 0;

        .card-header {
          .btn {
            background-color: $blue;
            color: #fff;
            border-radius: 0;
            height: auto;
            // position: relative;
            padding: 30px 100px 30px 0;

            .program-collapse-grid {
              display: grid;
              grid-template-columns: 50px 165px calc(100% - 90px);
              white-space: nowrap;

              div {

                &:nth-child(2),
                &:nth-child(5) {
                  text-align: right;
                  margin-right: 40px;
                }

                &:nth-child(2),
                &:nth-child(3) {
                  margin-bottom: 10px;
                }

                &:nth-child(6) {
                  white-space: normal;
                  padding-right: 90px;
                }

                &:last-child {
                  margin-top: 30px;
                }
              }

              .calendar {
                background-repeat: no-repeat;
                background-position: 13px top;
                background-image: url("data:image/svg+xml,%3Csvg width='22' height='22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M18.162 20.998c1.4 0 2.534-1.201 2.534-2.683V7.138c0-1.482-1.135-2.683-2.534-2.683H3.972c-1.4 0-2.534 1.2-2.534 2.683v11.177c0 1.482 1.134 2.683 2.533 2.683h9.545M6.969 1.738v6.227M15.168 1.738v6.227M1.438 11.154h18.446' stroke='%23fff' stroke-width='1.9' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
              }

              .small-text {
                font-size: 15px;
                line-height: 146%;
                color: #fff;

                &-blue {
                  color: $lightblue;
                  font-size: 15px;
                  line-height: 146%;
                }
              }

              .text {
                font-size: 19px;
                line-height: 135%;
                color: #fff;
              }

              span {
                padding: 0 40px 0 16px;
                background-repeat: no-repeat;
                background-position: left center;
                background-image: url("data:image/svg+xml,%3Csvg width='7' height='6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='.207' width='6' height='6' rx='.5' fill='%23032663'/%3E%3C/svg%3E");

                &:first-child {
                  background-image: url("data:image/svg+xml,%3Csvg width='7' height='6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='.207' width='6' height='6' rx='.5' fill='%237DE2F4'/%3E%3C/svg%3E");
                }

                &:last-child {
                  background-image: url("data:image/svg+xml,%3Csvg width='7' height='6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='.207' width='6' height='6' rx='.5' fill='%23fff'/%3E%3C/svg%3E");
                }
              }
            }

            &::after {
              display: none;
            }

            &::before {
              display: none;
            }

            .cross {
              @include lightblue_cross;
              right: 20px;
              background-size: 26px;
              width: 26px;
              height: 26px;
              top: 0;
              margin-top: 80.5px;
              z-index: 99;
              transition: transform .2s linear, top .3s;
            }

            &.collapsed {
              background-color: #fff;
              color: $black;
              padding: 40px 100px 40px 0;

              .calendar {
                background-image: url("data:image/svg+xml,%3Csvg width='22' height='22' viewBox='0 0 22 22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M18.1618 20.9971C19.5613 20.9971 20.6957 19.796 20.6957 18.3146V7.13704C20.6957 5.65525 19.5613 4.45415 18.1618 4.45415H3.97136C2.5719 4.45415 1.4375 5.65525 1.4375 7.13704V18.3146C1.4375 19.796 2.5719 20.9971 3.97136 20.9971H13.5161' stroke='%23222633' stroke-width='1.9' stroke-miterlimit='10' stroke-linecap='round'/%3E%3Cpath d='M6.96875 1.73734V7.96509' stroke='%23222633' stroke-width='1.9' stroke-miterlimit='10' stroke-linecap='round'/%3E%3Cpath d='M15.168 1.73734V7.96509' stroke='%23222633' stroke-width='1.9' stroke-miterlimit='10' stroke-linecap='round'/%3E%3Cpath d='M1.4375 11.1541H19.8843' stroke='%23222633' stroke-width='1.9' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E%0A");
              }

              .program-collapse-grid {
                .small-text {
                  color: $black;

                  &-blue {
                    color: $blue;
                  }
                }

                .text {
                  color: $black;
                }

                div {
                  &:last-child {
                    border-bottom: none;
                  }
                }
              }

              .cross {
                @include black_cross;
                background-size: 26px;
                width: 26px;
                height: 26px;
              }

              span {
                &:first-child {
                  background-image: url("data:image/svg+xml,%3Csvg width='7' height='6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='.207' width='6' height='6' rx='.5' fill='%237DE2F4'/%3E%3C/svg%3E");
                }

                &:last-child {
                  background-image: url("data:image/svg+xml,%3Csvg width='7' height='6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='.207' width='6' height='6' rx='.5' fill='%23222633'/%3E%3C/svg%3E");
                }
              }
            }
          }
        }

        .card-body {
          background-color: $blue;
          color: #fff;
          padding: 0 80px 40px 0;
          margin-bottom: 40px;

          .program-collapse-content {
            margin-left: 215px;
            font-size: 15px;
            line-height: 146%;
            color: #FFFFFF;
            position: relative;

            .content-title {
              color: #7DE2F4;
              padding: 40px 0 20px;
            }

            >div {
              &:first-child {
                padding-top: 30px;
              }
            }

            ul {
              list-style: none;
              margin: 0;
              padding: 0;

              li {
                margin: 0 0 10px 0;

                &:last-child {
                  margin-bottom: 0;
                }
              }
            }

            .organizer {
              display: grid;
              grid-template-columns: repeat(3, 1fr);
              column-gap: 40px;
              row-gap: 20px;
              img {
                max-width: 100%;
              }
            }

            .moderator {
              >div {
                display: grid;
                grid-template-columns: 95px auto;
                grid-template-rows: auto auto;
                column-gap: 15px;
                row-gap: 15px;
                margin-bottom: 20px;

                >div {
                  font-size: 13px;
                  line-height: 146%;

                  &.mname {
                    font-size: 15px;
                    text-transform: uppercase;
                  }
                }

                img {
                  grid-row: 1 / span 2;
                  max-width: 100%;
                }

                &:last-child {
                  margin-bottom: 0;
                }

              }
            }

            &::after {
              position: absolute;
              content: '';
              top: 0;
              left: 0;
              width: 100%;
              height: 1px;
              background-color: rgba(125, 226, 244, .6);
            }
          }
        }

        &::after {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 1px;
          background-color: rgba(34, 38, 51, .3);
        }
      }
    }
  }

  #efformat {
    margin: 180px 0 190px;

    .block_title {
      margin-bottom: 100px;
    }

    .slider {
      flex-wrap: nowrap;

      [class^=col] {
        min-width: 414px;
      }

      .title {
        font-size: 19px;
        line-height: 148%;
        text-transform: uppercase;
        color: #004FCB;
        margin: 30px 10px 10px;
      }

      .text {
        color: $black;
        margin: 0 10px 65px;
      }

      .link {
        position: absolute;
        left: 30px;
        bottom: 0;
      }
    }
  }

  #kind_healthy {
    background-color: $blue;
    padding: 160px 0;
    color: #fff;
    font-size: 19px;
    line-height: 135%;

    .block_title {
      color: #fff;

      span {
        color: $lightblue;
      }
    }

    ul {
      padding: 0;
      list-style: none;
      margin-top: 80px;

      li {
        display: grid;
        grid-template-columns: 115px auto;
        padding: 40px 0;
        border-bottom: 1px solid $lightblue;

        span {
          color: $lightblue;
        }

        &:first-child {
          border-top: 1px solid $lightblue;
        }
      }
    }
  }

  #informal {
    margin: 180px 0 0;

    .block_title {
      margin-bottom: 80px;
    }

    .slider {
      position: relative;

      >div {
        display: flex;
        flex-wrap: nowrap;
        position: relative;
        // left: calc(-100% - 40px);

        .img {
          margin: 0 20px;
          position: relative;
          left: -20px;
          background-size: cover;
          background-repeat: no-repeat;
          background-position: center top;
          min-height: 650px;
          min-width: 100%;
          background-image: url(../img/informal_1.jpg);
        }
      }
    }
  }

  #question_answer {
    margin: 150px 0 0;

    .block_title {
      margin-top: 30px;
      font-size: 27px;
      line-height: 130%;
    }

    .text {
      color: $black;
      margin: 20px 0 40px;
    }

    .btn.btn-primary {
      width: 330px;
      max-width: 100%;
      font-size: 15px;
      line-height: 150%;
    }
  }

  #see_also {
    margin-top: 140px;
    .small_title {
      font-size: 27px;
      font-weight: 400;
      text-transform: none;
    }
  }
}