#archive-foto{
	.container{
		@media(min-width: 1280px){
			padding-left: 0;padding-right: 0;
		}

	}
	h1{
		max-width: 500px;
		@media(max-width: 1279px){
			max-width:330px;
		}
	}
	.top_title{
		display: flex;
		-webkit-justify-content: space-between;
		        justify-content: space-between;

		margin-bottom: 100px;
		a{
			display: inline-block;

		}
		@media(max-width: 1279px){
			margin-bottom: 60px;
			a svg{
				width: 24px;
				height: 24px;
			}
		}
		.btn-close:hover svg path{
			stroke:$darkblue;
		}
	}
	.custom-grid + .custom-grid{margin-top: 40px;}
	.custom-grid{
		// padding-bottom: 120px;
		// @media(max-width: 1279px){padding-bottom: 60px;}
		img{
			max-width: 100%;
			height: auto;
		}
		.row +.row{margin-top: 40px;}
		.flex-col{
			display: flex;
			-webkit-flex-direction: column;
			    -ms-flex-direction: column;
			        flex-direction: column;
			-webkit-justify-content: space-between;
			        justify-content: space-between;
		}

	}
	.archive-foto__content{
		padding-bottom: 160px;
		@media(max-width: 1279px){padding-bottom: 140px;}
		@media(max-width: 767px){padding-bottom: 100px;}
	}

	.custom-grid--mobile{
		.col-md-6:nth-child(n+3){margin-top: 40px;}
		@media(max-width: 767px){
			.col-md-6:nth-child(n+2){margin-top: 20px;}
			}
	}
}
#archive-links{
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	padding-top: 41px;
	padding-bottom: 44px;
	border-top: 1px solid rgba(144, 148, 161, .2);
	border-bottom: 1px solid rgba(144, 148, 161, .2);
	.archive-links__box{
		max-width: 300px;
		@include bodytext;
		position: relative;
		@media(max-width: 1279px){
			font-size: 15px;
		}
	}
	.archive-links__box:before{
		content: '';
        position: absolute;
        width: 24px;
        height: 24px;
        left: 0;
        top: 0;
        transform: rotate(45deg);
        @include arrow_left('9094A1');
      }
      .archive-links__box:hover {
        color: $blue;
        &::before {
          @include arrow_left('004FCB');
        }
      }
	.archive-links__box:first-child{padding-left: 40px;}
	.archive-links__box:last-child{
		padding-right: 38px;
		text-align: right;
	}
		.archive-links__box:last-child:before{
		left: auto;
		right: 0;
		transform: rotate(135deg);
	}
	@media(max-width: 767px){

		.archive-links__box:last-child{text-align: left;}
	}
}
.fancy-foto{
	animation-name: slowShow;
	animation-duration:350ms ;
	animation-fill-mode: forwards ;
	animation-timing-function: ease-in-out;
}
@keyframes slowShow {
	0%{
		opacity: 0;
	}
	100%{
		opacity: 1;
	}
}