#upcoming-event {

  .btn-wrapper {
    padding-top: 60px;

    .button-separate {
      padding-right: 42px;
    }

    a {
      min-width: 250px;
    }
  }

  #top_content {

    .breadcrumbs a {
      font-size: 15px;
    }

    .back-to-tnf-online {
      margin-top: 40px;

      a {
        padding-left: 22px;
        position: relative;
        font-size: 15px;
        line-height: 146%;
        color: #9094a1;

        &::before {
          content: '';
          width: 12px;
          height: 12px;
          position: absolute;
          left: 0;
          background-size: cover;
          top: 1px;
          background-image: url("data:image/svg+xml,%3Csvg width='14' height='12' viewBox='0 0 14 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.72555 11L1.22749 6.38024C0.92417 6.21174 0.92417 5.78826 1.22749 5.61976L7.72555 1M4.88489 6.00006H13' stroke='%239094A1' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E%0A");
        }
      }
    }

    .top_title {
      font-size: 27px;
      margin-bottom: 60px;
      max-width: 890px;
    }

    .top_date {
      font-size: 19px;
      line-height: 148%;
      text-transform: uppercase;
      color: #004FCB;
    }

    .top_image {
      margin-top: 100px;
      margin-bottom: 120px;
      width: 100%;
      object-fit: contain;
      min-height: unset;
      //background-image: url(../img/upcoming-event_top.jpg);
    }

    .img-fluid {
      margin-top: 60px;
      margin-bottom: 120px;
      width: 100%;
      //min-height: 650px;
    }
  }

  .block_title {
    font-size: 27px;
    line-height: 130%;
  }

  #about-event {

    .download-presentation {
      font-size: 17px;
      line-height: 146%;
      display: flex;
      justify-content: flex-end;

      >a {
        position: relative;
        padding-left: 34px;

        &::before {
          content: '';
          width: 24px;
          height: 24px;
          position: absolute;
          left: 0;
          background-size: cover;
          top: 0;
          background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.62983 16.3315H5.46668C4.50693 16.3315 3.64179 15.9222 3.01361 15.26C2.38832 14.5977 2 13.6827 2 12.6743C2 11.2633 2.75837 10.0393 3.86657 9.43011C3.898 9.41287 3.91595 9.37806 3.91243 9.34053C3.89928 9.1945 3.89671 9.04509 3.90056 8.89702C3.93744 7.43399 4.73332 6.08622 6.00218 5.49262C7.00457 5.02376 8.01209 5.03931 8.86569 5.37194C8.91379 5.39053 8.96478 5.36383 8.97953 5.31211C9.61412 3.10606 11.5589 1.50003 13.8616 1.50003C16.6802 1.50003 18.9646 3.90823 18.9646 6.87926C18.9646 7.02056 18.9582 7.16153 18.9476 7.3008C18.9444 7.34373 18.9691 7.38193 19.0079 7.39511C20.744 7.99446 22 9.71948 22 11.7501C22 12.5566 21.8028 13.3152 21.4517 13.9744C20.7122 15.3803 19.2872 16.3315 17.6537 16.3315H14.1191' stroke='%23004FCB' stroke-width='1.6' stroke-miterlimit='10' stroke-linecap='round'/%3E%3Cpath d='M12 18.3413L12 13.1855' stroke='%23004FCB' stroke-width='1.2' stroke-miterlimit='10' stroke-linecap='round'/%3E%3Cpath d='M8.88629 18.2271L11.761 22.3555C11.8659 22.5482 12.1295 22.5482 12.2343 22.3555L15.1094 18.2271' stroke='%23004FCB' stroke-width='1.2' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E%0A");
        }

        span {
          font-size: 15px;
          line-height: 146%;
          color: #004FCB;
        }
      }
    }

    .description {
      margin-top: 120px;
      margin-bottom: 0;
      font-size: 19px;
      line-height: 135%;
      letter-spacing: -0.01em;
      color: #222633;

      p {
        font-size: 19px;
        line-height: 135%;
        letter-spacing: -0.01em;
        color: #222633;
        margin-bottom: 20px;

        &:last-child {
          margin-bottom: 0;
        }

        >span {
          color: #004FCB;
        }
      }
    }

    .download-presentation.mobileShow {
      display: none!important;
    }
  }

  #organizers {
    padding: 180px 0;

    [class^=col] {
      &:last-child {
        display: flex;
        margin-top: 80px;

        > div {
          margin-left: 40px;

          &:first-child {
            margin-left: 0;
          }
        }
      }
    }

   .scroll_organizators-img img,
   .scroll_organizators-marg img{
     min-width: 200px;
   }



  }

  #key-topics {
    background: #004FCB;
    padding: 160px 0;

    .block_title {
      font-size: 27px;
      line-height: 130%;
      color: #FFFFFF;
      padding-bottom: 120px;

      >span {
        color: #7DE2F4;
      }
    }

    .table-row {
      padding: 40px 0;

      &:first-child {
        padding-top: 0;
      }

      .title {
        font-size: 19px;
        line-height: 135%;
        letter-spacing: -0.01em;
        color: #7DE2F4;
      }

      .description {
        font-size: 17px;
        line-height: 146%;
        color: #FFFFFF;
      }
    }

    & hr {
      border-top: 1px solid #7DE2F4;
      margin: 0;
    }
  }

  #invited-experts {
    padding: 180px 0;

    .speakers {
      padding-top: 120px;
      position: relative;
      z-index: 5;
      flex-wrap: nowrap;

      .name {
        font-size: 19px;
        line-height: 148%;
        text-transform: uppercase;
        color: #222633;
        padding-top: 20px;
        padding-bottom: 10px;
      }
      .text {
        font-size: 15px;
        line-height: 146%;
        color: #9094A1;
      }
    }
  }

  #program {
    background: #004FCB;
    padding: 160px 0;

    .block_title {
      font-size: 27px;
      line-height: 130%;
      color: #FFFFFF;
      padding-bottom: 120px;

      >span {
        color: #7DE2F4;
      }
    }

    .table-row {
      padding: 40px 0;

      &:first-child {
        padding-top: 0;
      }

      .title {

        span {
          font-size: 19px;
          line-height: 135%;
          letter-spacing: -0.01em;
          color: #FFFFFF;
          padding-left: 39px;
          position: relative;

          &::before {
            content: '';
            width: 24px;
            height: 24px;
            position: absolute;
            left: 0;
            background-size: cover;
            top: -5px;
            background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M19.3684 22.0005C20.8218 22.0005 21.9999 20.7533 21.9999 19.2151V7.6084C21.9999 6.06974 20.8218 4.82252 19.3684 4.82252H4.63145C3.17809 4.82252 2 6.06974 2 7.6084V19.2151C2 20.7533 3.17809 22.0005 4.63145 22.0005H14.5438' stroke='%237DE2F4' stroke-width='1.6' stroke-miterlimit='10' stroke-linecap='round'/%3E%3Cpath d='M7.74609 2.00062V8.46826' stroke='%237DE2F4' stroke-width='1.6' stroke-miterlimit='10' stroke-linecap='round'/%3E%3Cpath d='M16.2578 2.00062V8.46826' stroke='%237DE2F4' stroke-width='1.6' stroke-miterlimit='10' stroke-linecap='round'/%3E%3Cpath d='M2 11.7793H21.1573' stroke='%237DE2F4' stroke-width='1.6' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E%0A");

          }
        }
      }

      .description {
        font-size: 19px;
        line-height: 135%;
        color: #FFFFFF;
        letter-spacing: -0.01em;

        span {
          color: #7DE2F4;
        }

        ul {
          list-style: none;
          padding-top: 13px;
          padding-left: 23px;

          li {
            position: relative;
            margin-top: 27px;
            font-size: 17px;

            &::before {
              content: '';
              width: 8px;
              height: 8px;
              position: absolute;
              left: -22px;
              background-size: cover;
              top: 50%;
              background: #7de2f4;
              transform: translate(0, -50%);
            }
          }
        }
      }
    }

    & hr {
      border-top: 1px solid #7DE2F4;
      margin: 0;
    }
  }

  #question {
    background: #F6F6F6;
    padding-top: 180px;
    padding-bottom: 160px;

    .block_title {
      margin-bottom: 85px;
    }

    form {

      >div {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;

        span.error-message{
          font-size: 15px;
          line-height: 146%;
          color: #E10000;
          padding-top: 10px;
          display: block;
        }
      }

      .has-error {
        input {
          outline: 1px solid rgba(203, 0, 0, 0.2);
        }
      }

      label {
        font-size: 19px;
        line-height: 135%;
        letter-spacing: -0.01em;
        color: #222633;
        margin-bottom: 0;
      }

      input {
        width: 550px;
        height: 50px;
        border: 1px solid rgba(144, 148, 161, 0.2);
        padding-left: 20px;
        display: block;
        font-size: 17px;
        line-height: 146%;
        color: #222633;

        &:focus,
        &:focus-visible{
          outline: 1px solid #004FCB;
        }
      }

      textarea {
        width: 550px;
        height: 100px;
        border: 1px solid rgba(144, 148, 161, 0.2);
        padding: 14px 20px;
        resize: none;
        font-size: 17px;
        line-height: 146%;
        color: #222633;
      }

      .submit{
        padding-top: 34px;
        padding-bottom: 40px;
        justify-content: flex-end;
      }

      .user-agreement {
        justify-content: flex-end;

        > div {
          width: 550px;
          font-size: 13px;
          line-height: 146%;
          color: #222633;

          span {
            color: #004FCB;
          }
        }
      }
    }
  }

  #company-organizer{
    padding-top: 180px;
    padding-bottom: 180px;
    color: #222633;

    .block_title {
      margin-bottom: 80px;
    }

    .articles>div {
      display: grid;
      grid-template-columns: 50% 50%;
      grid-template-rows: auto auto 1fr auto;
      row-gap: 63px;
      -moz-column-gap: 40px;
      column-gap: 40px;

      img {
        grid-row: 1/span 4;
        max-width: 100%;
      }

      .title {
        font-size: 23px;
        line-height: 136%;

        span {
          color: #004FCB;
        }
      }

      .intro {
        font-size: 19px;
        line-height: 130%;

        p {
          margin-bottom: 20px;
        }
      }
    }
  }
}