#security-measures {

  .block_title {
    font-size: 27px;
    line-height: 130%;
  }

  #top_content {

    .breadcrumbs a {
      font-size: 15px;
    }

    .top_title {
      font-size: 27px;
      margin-bottom: 60px;
    }

    .img-fluid {
      margin-top: 60px;
      margin-bottom: 120px;
      width: 100%;
      min-height: 650px;
    }
  }

  #security-measures-on-forum {
    margin-bottom: 180px;

    .description {

      p {
        font-size: 19px;
        line-height: 135%;
        letter-spacing: -0.01em;
        color: #222633;
        margin-bottom: 20px;

        &:last-child {
          margin-bottom: 0;
        }

        >span {
          color: #004FCB;
        }
      }
    }
  }

  #key-commitments {
    background: #F6F6F6;
    padding-top: 120px;
    padding-bottom: 185px;
    font-size: 17px;
    line-height: 146%;
    color: #222633;

    .block_title {
      margin-bottom: 30px;
    }

    .justify-content-between {

      [class^=col] {
        margin-top: 120px;

        .title {
          font-size: 23px;
          line-height: 136%;
          color: #004FCB;
          margin-bottom: 20px;
        }
      }
    }
  }

  #faq {
    padding-top: 180px;
    padding-bottom: 180px;
    font-size: 17px;
    line-height: 146%;
    color: #222633;

    .block_title {
      margin-bottom: 20px;
    }

    .btn-wrapper {
      padding-top: 40px;

      a {
        min-width: 230px;
      }
    }

    .accordion-toggle {
      padding: 40px 20px;
      background-color: $grey;

      .info-block {
        margin-top: 60px;

        p {
          margin-bottom: 20px;
          color: $black;
        }

        .btn {
          width: 100%;
          max-width:330px;
        }
      }
    }
    .accordion {
      min-height: 105px;
      margin-top: -20px;
    }
  }
}