#tnf-online {

  .breadcrumbs a {
    font-size: 15px;
  }

  .top_title {
    font-size: 33px;
  }

  .top_image {
    margin-bottom: 0!important;
    background-image: url(../img/tnf-online_top.jpg);
  }

  .block_title {
    font-size: 27px;
    line-height: 130%;
  }

  .button-separate {
    padding-right: 42px;
  }

  #fulcrum {
    font-size: 19px;
    line-height: 135%;
    color: #222633;
    padding: 120px 0;
    background-color: #f6f6f6;

    .btn-wrapper{
      padding-top: 60px;
    }
  }

  #broadcast_of_events {
    padding: 180px 0;
    color: #222633;

    .row.justify-content-between{
      align-items: flex-end;
      margin-bottom: 120px;
    }

    .pfilter {
      margin-bottom: 0;

      &>a::after {
        padding: 0 20px;
      }
    }

    .articles>div {
      margin-bottom: 80px;
      display: grid;
      grid-template-columns: 2fr 1fr;
      grid-template-rows: auto auto 1fr auto;
      row-gap: 40px;
      -moz-column-gap: 40px;
      column-gap: 40px;

      img {
        grid-row: 1/span 4;
        width: 100%;
      }
    }

    .date-container {
      display: flex;
      justify-content: space-between;
      align-items: baseline;
    }
    .date {
      white-space: nowrap;
      font-size: 17px;
      text-transform: uppercase;

      span {
        font-size: 33px;
        letter-spacing: -0.01em;
        padding-right: 5px;
      }
    }

    .location {
      color: #9094A1;
      font-size: 17px;
      text-transform: uppercase;
      text-align: right;
      white-space: nowrap;

      span {
        position: relative;
        padding-left: 25px;

        &::before {
          content: '';
          width: 20px;
          height: 20px;
          position: absolute;
          left: 0;
          background-size: cover;
          top: -4px;
          background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.99907 9.9928C8.8902 9.9928 7.98828 9.08368 7.98828 7.96595C7.98828 6.84822 8.8902 5.9391 9.99907 5.9391C11.1079 5.9391 12.0099 6.84822 12.0099 7.96595C12.0099 9.08368 11.1079 9.9928 9.99907 9.9928Z' stroke='%239094A1' stroke-width='1.6' stroke-miterlimit='10' stroke-linecap='round'/%3E%3Cpath d='M12.8666 15.6286C14.4725 13.6343 16.25 10.798 16.25 8.01205C16.25 4.51284 13.4458 1.66671 10 1.66671C6.55422 1.66671 3.75 4.51284 3.75 8.01205C3.75 12.984 9.41252 18.1177 9.65352 18.3334' stroke='%239094A1' stroke-width='1.6' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E%0A");
        }
      }
    }

    .title {
      font-size: 23px;
      line-height: 136%;

      span {
        color: #004FCB;
      }
    }

    .intro {
      font-size: 17px;
      line-height: 146%;
    }

    .pagination {
      margin-top: 45px;
      display: flex;
      align-items: center;
      justify-content: center;

      a {
        display: block;
        font-size: 19px;
        line-height: 136%;
        color: #9094A1;
        margin: 0 12px;

        &.active {
          color: $blue;
          font-size: 23px;
          line-height: 136%;
        }

        &:hover {
          color: $blue;
        }

        &.arrow {
          margin: 0;
          width: 22px;
          height: 36px;
          background-position: center;

          &.prev {
            margin-right: 10px;
            @include arrow_left('9094A1');

            &:hover {
              @include arrow_left('004FCB');
            }
          }

          &.next {
            margin-left: 10px;
            @include arrow_right('9094A1');

            &:hover {
              @include arrow_right('004FCB');
            }
          }
        }
      }
    }
  }
}
