// Variables Bootstrap 4

$grid-gutter-width: 40px !default;

$grid-gutter-widths: (
  lg: 40px,
  sm: 40px,
);
// Default Bootstrap gutter width variable.
$grid-gutter-width: map-get($grid-gutter-widths, lg);
$grid-gutter-width: map-get($grid-gutter-widths, sm);

$grid-breakpoints: (
        xs: 0,
        sm: 576px,
        md: 768px,
        lg: 992px,
        xl: 1280px
);

$container-max-widths: (
        sm: 540px,
        md: 720px,
        lg: 960px,
        xl: 1200px
);


$red:     #cc2eaa;
$green:   #2ecc71;

$blue: #004FCB;
$darkblue: #032663;
$grey: #F6F6F6;
$lightblue: #7DE2F4;
$black: #222633;
$aquagrey: #9094A1;

$darklightblue: #61CFE5;

.bluetext {
  color: $blue;
}

//fonts

@mixin hero_title {
  font-family: $tk-aktiv-grotesk;
  font-style: normal;
  font-weight: normal;
  font-size: 47px;
  line-height: 110%;
  color: #979797;
}

@mixin title_h3 {
  font-family: $tk-aktiv-grotesk;
  font-style: normal;
  font-weight: bold;
  font-size: 23px;
  line-height: 135%;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  color: #979797;
}

@mixin title_h4 {
  font-family: $tk-aktiv-grotesk;
  font-style: normal;
  font-weight: normal;
  font-size: 19px;
  line-height: 148%;
  text-transform: uppercase;  
  color: #979797;
}

@mixin large_text {
  font-family: $tk-aktiv-grotesk;
  font-style: normal;
  font-weight: normal;
  font-size: 23px;
  line-height: 136%;
  color: #979797;
}

@mixin bodytext_19 {
  font-family: $tk-aktiv-grotesk;
  font-style: normal;
  font-weight: normal;
  font-size: 19px;
  line-height: 135%;
  letter-spacing: -0.01em;
}

@mixin bodytext {
  font-family: $tk-aktiv-grotesk;
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 146%;
  color: #979797;  
}

@mixin button_1 {
  font-style: normal;
  font-weight: 500;
  font-size: 17px;
  line-height: 150%;
  letter-spacing: -0.01em;
  color: #FFFFFF;
  padding: 14px 34px;
  background: #004FCB;
  border-radius: 2px;
  min-width: 173px;
  &:hover {
    background-color: #032663;
  }
  &.bis {
    background-color: #ECECEC;
    color: $aquagrey;
    border: 1px solid #ECECEC;
  }
}
@mixin button_1_reverse {
  font-style: normal;
  font-weight: 500;
  font-size: 17px;
  line-height: 150%;
  letter-spacing: -0.01em;
  color: #004FCB;
  padding: 14px 34px;
  background: transparent;
  border: 1.6px solid #004FCB;
  border-radius: 2px;
  min-width: 173px;
  &:hover {
    background-color: #032663;
    color: #FFFFFF;
  }
  &.bis {
    background-color: #ECECEC;
    color: $aquagrey;
  }
}
@mixin button_1_grey {
  font-style: normal;
  font-weight: 600;
  font-size: 17px;
  line-height: 150%;
  letter-spacing: -0.01em;
  color: #FFFFFF;
  padding: 14px 34px;
  background: #9094A1;
  border-radius: 2px;
  min-width: 173px;
  &:hover {
    background-color: #032663;
  }
  &.bis {
    background-color: #ECECEC;
    color: $aquagrey;
  }
}

@mixin button_2 {
  font-style: normal;
  font-weight: 500;
  font-size: 17px;
  line-height: 150%;
  letter-spacing: -0.01em;
  color: $black;
  background: $lightblue;
  padding: 14px 34px;
  border: none;
  border-radius: 2px;
  &:hover {
    background: $darklightblue;
    box-sizing: border-box;
  }
}

@mixin button_3 {
  font-style: normal;
  font-weight: 500;
  font-size: 17px;
  line-height: 150%;
  letter-spacing: -0.01em;
  color: #FFFFFF;
  padding: 14px 34px;
  background: #004FCB;
  border-radius: 2px;
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17.509 19.219l1.778-13.538c.143-.572-.396-1.111-.968-.968L4.781 6.491M14.493 9.507l-9.564 9.564' stroke='%23fff' stroke-width='1.6' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");  
  background-repeat: no-repeat;
  background-position: 50% 50%;
  &:hover {
    background-color: #032663;
  }  
}
@mixin button_small {
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 150%;
  color: #FFFFFF;
  padding: 12px 30px;
  border: none;
  background: #004FCB;
  border-radius: 2px;
  @media(max-width: 767px){
    font-size: 13px;
    padding-left: 25px;
    padding-right: 25px;
  }

  &:hover {
    background-color: #032663;
  }
}
@mixin  button_plus{

  background: #004FCB;
  border-radius: 2px;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M12 21.9999V1.99988' stroke='white' stroke-width='1.6' stroke-miterlimit='10' stroke-linecap='round'/%3e%3cpath d='M11.7099 11.9998H2M22 11.9998H13.7671H22Z' stroke='white' stroke-width='1.6' stroke-miterlimit='10' stroke-linecap='round'/%3e%3c/svg%3e ");
  background-repeat: no-repeat;
  background-position: 50% 50%;

  &:hover {
    background-color: #032663;
  }
}
@mixin arrow_link {
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 148%;
  text-transform: uppercase;
  color: $black;
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17.509 19.219l1.778-13.538c.143-.572-.396-1.111-.968-.968L4.781 6.491M14.493 9.507l-9.564 9.564' stroke='%23222633' stroke-width='1.6' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");  
  background-repeat: no-repeat;
  background-position: 100% 50%;
  padding-right: 28px; 
  &:hover {
    text-decoration: none;
    color: $blue;
    background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17.509 19.219l1.778-13.538c.143-.572-.396-1.111-.968-.968L4.781 6.491M14.493 9.507l-9.564 9.564' stroke='%23004FCB' stroke-width='1.6' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
  }
}

