@mixin section-title{
  font-size: 33px;
  line-height: 1.15;
  font-family: $tk-aktiv-grotesk;
  color: $black;
  @media(max-width: 1279px){
    font-size: 27px;
    line-height: 1.3;
  }
    @media(max-width: 767px){
