  .video__cursor {
    position: absolute;
    width: 70px;
    height: 70px;
    top: 50%;
    left: 50%;
    pointer-events: none;
    border: 2px solid #fff;
    border-radius: 50%;
    transform: translate(-50%, -50%);
    z-index: 6; }
    .video__cursor.active {
      display: none; }
    .video__cursor::before, .video__cursor::after {
      content: '';
      position: absolute;
      border: 2px solid #fff;
      left: -30px;
      right: -30px;
      top: -30px;
      bottom: -30px;
      border-radius: 50%;
      -webkit-animation: pulse 1.8s linear infinite;
      animation: pulse 1.8s linear infinite; }
