      position: absolute;
      width: 25px;
      height: 25px;
      top: 50%;
      left: 50%;
      transform: translate(-40%, -50%); }
    .video__cursor::after {
      -webkit-animation-delay: .7s;
      animation-delay: .7s;
      opacity: 0; }

