  100% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    opacity: 0; } }

@keyframes pulse {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    -webkit-transform: scale(1.2);
