#archive, #archive-foto, #archive-categories{
	.filters-drop label{cursor:pointer;}
	h1{
		font-size: 47px;
		line-height: 1.18;
		color: $black;
		font-style: normal;
    font-weight: normal;
    font-family: $tk-aktiv-grotesk;
    @media(max-width: 1279px){
    	font-size: 27px;

    }
    @media(max-width: 767px){
    	font-size: 21px;

    }
	}
	.top_title{
		margin-top: 31px;
		margin-bottom: 80px;
		 @media(max-width: 1279px){
    	margin-top: 60px;
		 margin-bottom: 60px;

    }
	}
}
 #archive-foto, #archive-categories{
 	h1{
		font-size: 33px;
		line-height: 1.15;
		@media(max-width: 1279px){
    	font-size: 27px;
    	}
    	@media(max-width: 767px){
    	font-size: 21px;
    	}
	}
 }
#archive{

	.archive-card{
		display: block;

		&__text{
			padding-top: 40px;
			-webkit-justify-content: space-between;
			        justify-content: space-between;
		    -webkit-align-items: center;
		            align-items: center;

		    p{
		    	@include bodytext;
		    	margin: 0;
		    	margin-bottom: 20px;

		    }
		    h4{
		    	font-weight: 400;
		    	font-size: 27px;
		    	line-height: 1.3;
		    	margin: 0;
		    	font-family: $tk-aktiv-grotesk;
		    	@media(max-width: 1279px){
		    		font-size: 17px;

		    	}
		    }


		}
		& +.archive-card{
			margin-top: 120px;
		}
		 &-col svg path{stroke: $black;}

		&__img{
			overflow: hidden;
			height: 450px;
		}
		 @media(max-width: 1279px){
		 	&__text{
		    	-webkit-align-items: flex-end;
		    	        align-items: flex-end; }
			     h4{font-size: 17px;}
			     p{
			     	font-size: 15px;
			    	margin-bottom: 10px;
			     }


			 &-col svg{
			     	width: 24px;
			     	height: 24px;
			     	margin-bottom: 12px;
			     	path{stroke: $black;}
			     }
		     & +.archive-card{
				margin-top: 60px;
			}
			&__img{
				height: 389px;				
			}
	    }//-1279
	     @media(max-width: 767px){
	     	&__img {
	     		height: 300px;
				 img {
				 	max-width: unset;
				 	width: auto;
				 	height: 100%;
				 }
	     	}
	     	& +.archive-card{
				margin-top: 80px;
			}
			p{font-size: 13px;}
	     }
	}//-archive-card
	.archive-card:hover .archive-card-col svg path{stroke: $blue;}
}//-#archive

#timeline {
  overflow: hidden;
}
#timeline .timeline-button {
  border-bottom: 2px solid rgba(34, 38, 51, .3);
}
#timeline .timeline-button__inner {
  padding: 0;
  margin: 0;
  display: flex;
  white-space: nowrap;

  transition: 0.7s;
  transform: translate(-940px);
}
#timeline .timeline-button__inner button {
  font-size: 33px;
  line-height: 1.15;
  background-color: transparent;
  padding: 0;
  border: 0;
  height: 125px;
  display: flex;
  min-width: 100px;
  width: 100px;
  justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: -3px;
  cursor: pointer;
  position: relative;
  color: $aquagrey;
  transition: .3s;
  span{color: $aquagrey;}
   @media(max-width: 1279px){
  	font-size: 17px;
  	height: 54px;
  }
   @media(max-width: 767px){
  	font-size: 21px;
  }
 @media(max-width: 359px){
  	font-size: 17px;
  }
}

@media (max-width: 1279px) {
  #timeline .timeline-button__inner button {
    width: 80px;
  }
}
@media (max-width: 767px) {
  #timeline .timeline-button__inner button {
    width: 65px;
  }
}

#timeline .timeline-button__inner button:hover {
  // color: red;
}
#timeline .timeline-button__inner button:after {
  content: "\2022";
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  bottom: 0px;
  font-size: 33px;
  line-height: 0;
  color: $aquagrey;
}
#timeline .timeline-button__inner button.timeline-button--current {
  color: $blue;
  font-size: 47px;
  line-height: 1.1;
  span{color: $blue;}
  @media(max-width: 1279px){
  	font-size: 27px;
  }
   @media(max-width: 767px){
  	font-size: 33px;
  }
    @media(max-width: 359px){
  	font-size: 21px;
  }


}
#timeline .timeline-button__inner  button.timeline-button--current:after {
  font-size: 47px;color: $blue;
  bottom: -1px;
}
#timeline .timeline-content {
  padding-top: 120px;
  padding-bottom: 220px;
  @media(max-width: 1279px){
  	padding-top: 60px;
  	padding-bottom: 140px;
  }
}
#timeline .timeline-content article {
  display: none;

}
#timeline .timeline-content article.article--current {
  display: block;

  animation: opasity 1000ms linear forwards;

}

