#past-event {
  @include media-breakpoint-down(lg) {
    #invited-experts {

      .speakers {

        [class^=col] {
          min-width: 290px;
          padding-right: 0;
        }

        [class^=col]:last-child {
          margin-right: 40px;
        }
      }
    }
  }

  @include media-breakpoint-down(md) {

    .block_title {
      font-size: 21px;
    }

    #top_content {

      .top_date {
        font-size: 17px;
      }

      // .img-fluid {
      //   margin-bottom: 100px;
      // }
      .container-wideo{
        height: 350px;
        margin-bottom: 100px;
        .video__cursor{
          top: 50%;
          left: 50%;
          transform: translate(-50%,-50%);
        }
      }
    }

    #about-event {

      .description {
        margin-top: 40px;
        margin-bottom: 140px;

        p {
          font-size: 17px;
        }

        .btn-wrapper {
          display: flex;
          flex-wrap: wrap;
          row-gap: 20px;
          padding-top: 20px;
        }
      }

      .download-presentation.mobileHide {
        display: none !important;
      }

      .download-presentation.mobileShow {
        display: flex !important;
        justify-content: flex-start;
        margin-top: 40px;
      }
    }

    #organizers{

      [class^=col] {
        &:last-child {
          margin-top: 40px;
          margin-bottom: 140px;
        }
      }
      .scroll_organizators{
        width: calc(50% + 52vw);
        overflow-x: auto;
        overflow-y: hidden;
      }
      .scroll_organizators::-webkit-scrollbar{width: 0;}
    }

    #key-topics {
      padding: 100px 0;

      .block_title {
        padding-bottom: 20px;
        font-size: 21px;
      }

      .table-row .title {
        margin-bottom: 20px;
      }
    }

    #invited-experts {
      padding: 140px 0;
      .speakers {
        width: calc(50% + 53vw);
        padding-top: 60px;
        overflow-x: auto;
        overflow-y: hidden;
      }
      .speakers::-webkit-scrollbar{width: 0%;}
    }
  }

  @include media-breakpoint-down(xs) {
    .block_title {
      font-size: 21px;
    }

    #top_content {

      .back-to-tnf-online {
        display: none;
      }

      .top_title {
        font-size: 17px;
        line-height: 130%;
        margin-bottom: 30px;
      }

      .top_date {
        font-size: 13px;
      }

      // .img-fluid {
      //   min-height: 300px;
      //   object-fit: cover;
      //   margin-bottom: 80px;
      // }

      .container-wideo{
        height: 300px;
        margin-bottom: 80px;
      }
    }

    #about-event {

      .description {
        margin-top: 40px;
        margin-bottom: 80px;

        p {
          font-size: 15px;
        }
      }

      .download-presentation{

        &.mobileHide {
          display: none!important;
        }

        &.mobileShow {
          display: flex!important;
          justify-content: flex-start;
          margin-top: 40px;
        }

        >a{
          font-size: 15px;

          >span{
            font-size: 13px;
          }
        }
      }
    }

    #organizers{

      [class^=col] {
        &:last-child {
          margin-top: 40px;
          margin-bottom: 60px;
        }
      }
    }

    #key-topics {
      padding: 80px 0;

      .block_title {
        font-size: 21px;
        padding-bottom: 20px;
      }

      .table-row {

        .title {
          font-size: 17px;
          margin-bottom: 20px;
        }

        .description {
          font-size: 15px;
        }
      }
    }

    #invited-experts {
      padding: 80px 0;

      .speakers {
        padding-top: 60px;

        .name {
          font-size: 15px;
        }

        [class^=col]:last-child {
          margin-right: 20px;
        }
      }
    }
  }
}