#participation {
  .top_image {
    background-image: url(../img/participation_top.jpg);
  }
  #bpartners
  {
   padding: 160px 0;
   background: #F6F6F6;
 
   .block_title {
     margin: 0 0 40px;
   }
 
   .text {
     @include bodytext_19;
     color: $black;
   }
 }
 
 #key_event {
   margin: 180px 0;
 
   .block_title {
     margin-bottom: 140px;
   }
 
   .slider {
     flex-wrap: nowrap;
     [class^=col] {
       min-width: 413px;
       .title {
         font-size: 23px;
         line-height: 136%;
         color: $black;
         margin-bottom: 50px;
         min-height: 62px;
       }
     }
   }
 }
 
 #meet_result {
   background: #F6F6F6;
   padding: 160px 0 155px;
   .block_title {
     margin-bottom: 105px;
   }
   .block_content {
     display: grid;
     grid-template-columns: 310px auto;
     border-bottom: 1px rgba(34, 38, 51, .3) solid;
     &:first-child{
        border-top: 1px rgba(34, 38, 51, .3) solid;
     }
     div {
       padding: 40px 0;
       &:nth-child(odd) {
         font-size: 19px;
         line-height: 135%;
         color: $blue;
       }
       &:nth-child(even) {
         font-size: 17px;
         line-height: 146%;
         color: $black;
       }      
     }
   }
 }
 
 #eff_format {
   margin: 180px 0;
   .block_title {
     margin-bottom: 120px;
   }
   .mozaic {
     display: grid;
     grid-template-columns: repeat(3, auto);
     grid-template-rows: 205px 205px;
     column-gap: 40px;
     row-gap: 40px;
     > div {
       position: relative;
       img {
         // width: 100%;
         z-index: 1;
         max-width: 100%;
       }
       &:nth-child(2) {
         grid-row: 1 / span 2;
         grid-column: 2 / 2;
       }
       .title {
         position: absolute;
         bottom: 20px;
         left: 20px;
         font-size: 19px;
         line-height: 148%;
         text-transform: uppercase;
         color: #fff;
         overflow: hidden;
         max-height: 28px;
         transition: max-height .3s;
         z-index: 3;
         span {
           display: block;
           font-size: 17px;
           line-height: 146%;
           text-transform: none;
           margin: 10px 0 20px;
         }
         .btn {
           text-transform: none;
         }
       }
       &:hover {
         .title {
           max-height: 160px;
           transition: max-height .8s;
           color: $lightblue;
           span {
             color: #fff;
             padding-right: 15px;
           }
         }
         &::after {
           position: absolute;
           content: '';
           top: 0;
           left: 0;
           width: 100%;
           height: 100%;
           background-color: rgba(3, 38, 99, .7);
           z-index: 2;
         }
       }
     }
   }
 }
 
 #big_text {
   background-color: $blue;
   padding: 253px 0;
   font-size: 94px;
   line-height: 125%;
   color: #fff;
   text-align: center;
   white-space: nowrap;
   position: relative;
   div {
    width: 100%;
    position: relative;
    // left: calc(50% - 950px);
  }
   span {
     color: $lightblue;
   }
 }
 
 #tnf_special {
   margin: 180px 0 100px;
   .block_title {
     margin-bottom: 120px;
   }
   .row {
     &:last-child {
       [class^=col] {
         margin-bottom: 80px;
         .title {
           font-size: 19px;
           line-height: 135%;
           letter-spacing: -0.01em;
           color: $blue;
           margin: 20px 0;
         }
         .text {
           font-size: 17px;
           line-height: 146%;
           color: $black;
         }
       }
     }
   }
 }
 
