#registration {
  @include media-breakpoint-down(lg) {

    .top_title {
      margin-bottom: 50px;
    }

    #eff_format {
      margin: 0 0 110px;

      .block_title {
        margin-bottom: 60px;
      }

      .mozaic {
        display: grid;
        grid-template-columns: repeat(2, auto);
        grid-template-rows: repeat(2, 1fr);

        >div {
          img {
            max-width: none;
            min-width: 100%;
            // height: 300px;
            // object-fit: cover;
          }

          &:nth-child(2) {
            grid-column: 2;
            grid-row: 1;
          }

          &:nth-child(3) {
            grid-column: 2;
            grid-row: 2;
          }

          .title {
            font-size: 17px;
            color: $lightblue;
            max-height: 300px;

            span {
              font-size: 13px;
              line-height: 146%;
              color: #fff;
              margin: 10px 0 40px;
            }

            .btn {
              font-size: 15px;
              line-height: 150%;
              min-width: calc(100% - 20px);
            }
          }

          &::after {
            position: absolute;
            content: '';
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(3, 38, 99, .7);
            z-index: 2;
          }
        }
      }
    }    
    #question_answer {
      margin: 0 0 140px;

      .block_title {
        margin-bottom: 30px;
        font-size: 21px;
        line-height: 135%;
      }

      .text {
        margin: 40px 0;
      }

      a.btn.btn-primary {
        padding: 14px;
      }
    }
  }
  @include media-breakpoint-down(sm) {
    #eff_format {
      .mozaic {
        display: flex;
        flex-direction: column;
        align-items: center;
        > div {
          max-width:320px;
        }
      }
    }
  }
  @include media-breakpoint-down(xs) {
    #eff_format {
      margin: 100px 0;
      .block_title {
        margin-bottom: 60px;
      }
    }    
    #question_answer {
      margin: 0 0 100px;

      .block_title {
        margin-bottom: 0;
        line-height: 130%;
      }

      .text {
        font-size: 15px;
        line-height: 146%;
        margin: 20px 0 30px;
      }
      a.btn.btn-primary {
        width: 100%;
      }
    }
  }
}