#contacts,#technodays,#community{
	.mb-20{
		margin-bottom: 20px!important;
	}
	.mb-120{
		margin-bottom: 120px;
		@media(max-width: 1279px){
			margin-bottom: 60px;
		}
	}
	.p-160{
		padding-top: 160px;
		padding-bottom: 160px;
		@media(max-width: 1199px) {
			padding-top: 100px;
			padding-bottom: 100px;
		}

		@media(max-width: 767px) {
			padding-top: 80px;
			padding-bottom: 80px;
		}
	}
	.p-180{
		padding-top: 180px;
		padding-bottom: 180px;
		@media(max-width: 1199px) {
			padding-top: 140px;
			padding-bottom: 140px;
		}
		@media(max-width: 767px){
			padding-top: 100px;
			padding-bottom: 100px;
		}
	}
	.block_title--medium{
		font-size: 27px;
		line-height: 1.3;
		@media(max-width: 1279px) {
			font-size: 23px;

		}
		@media(max-width: 991px) {
			font-size: 21px;
			line-height: 1.35;

		}
		@media(max-width: 359px) {
			font-size: 17px;
			line-height: 1.35;

		}
	}

	.block_title--small{
		font-size: 21px;
		line-height: 1.3;
	}
	.btn-main{
		@include button_1;
		width: 100%;
		max-width: 330px;
		margin-top: 60px;
	}
	.dlink {
	  padding-left: 30px;
	  display: block;
	  background-repeat: no-repeat;
	  background-position: left top;
	  background-image: url("data:image/svg+xml,%3Csvg width='19' height='19' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.519 12.787H3.992a2.937 2.937 0 01-2.078-.855 2.908 2.908 0 01.722-4.652.072.072 0 00.04-.071 2.954 2.954 0 01-.011-.354c.031-1.168.705-2.243 1.78-2.717a3.211 3.211 0 012.426-.096.073.073 0 00.097-.048A4.318 4.318 0 0111.104.952c2.387 0 4.323 1.922 4.323 4.293 0 .112-.006.225-.015.336a.073.073 0 00.051.075 3.662 3.662 0 012.535 3.475c0 .644-.167 1.25-.464 1.775a3.68 3.68 0 01-3.218 1.881h-2.994M9.526 14.695v-4.114M6.89 14.604l2.436 3.294a.232.232 0 00.401 0l2.436-3.294' stroke='%23222633' stroke-width='1.2' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
	  color: $black;

	  span {
	    color: $blue;
	    font-size: 15px;
	    line-height: 135%;
	    display: block;
	  }

	  &:hover {
	    color: $blue;
			background-image: url("data:image/svg+xml,%3Csvg width='19' height='19' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.519 12.787H3.992a2.937 2.937 0 01-2.078-.855 2.908 2.908 0 01.722-4.652.072.072 0 00.04-.071 2.954 2.954 0 01-.011-.354c.031-1.168.705-2.243 1.78-2.717a3.211 3.211 0 012.426-.096.073.073 0 00.097-.048A4.318 4.318 0 0111.104.952c2.387 0 4.323 1.922 4.323 4.293 0 .112-.006.225-.015.336a.073.073 0 00.051.075 3.662 3.662 0 012.535 3.475c0 .644-.167 1.25-.464 1.775a3.68 3.68 0 01-3.218 1.881h-2.994M9.526 14.695v-4.114M6.89 14.604l2.436 3.294a.232.232 0 00.401 0l2.436-3.294' stroke='%23004fcb' stroke-width='1.2' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
	  }

	}
	.flex-header{
		align-items: flex-start;
		margin-bottom: 120px;

		@media(max-width: 1199px) {
			margin-bottom: 40px;
		}
		@media(max-width: 767px) {
			margin-bottom: 80px;
		}

	}

	//-contact-item
	.contacts-list__item{
		&>a {
			display: flex;
			align-items: center;
			span{
				white-space: nowrap;
			}
			span+span {
				padding-left: 12px;
			}

		}
	}
	//- team-block
	.contacts-team .block_title br{display: block;}
	.team-block {
		margin-left: -20px;
		margin-right: -20px;
		display: flex;
		// justify-content: space-between;
		flex-wrap: wrap;
		row-gap: 40px;

	}
	//-team-card
	.team-block__card{
		width: 373px;
		margin-left: 20px;
		margin-right: 20px;
		padding-bottom: 20px;
		padding-top: 20px;
		&-text{
			padding-top: 20px;
			h3{

				font-style: normal;
				font-weight: normal;
				font-size: 19px;
				line-height: 148%;
				text-transform: uppercase;
				color: #222633;
			}
			p{
				font-size: 15px;
				line-height: 1.46;
				max-width:270px;
				min-height: 42px;
				display: -webkit-box;
				-webkit-box-orient: vertical;
				-webkit-line-clamp: 2;
				overflow: hidden;
			}
		}
		.contacts-list{
			padding-top: 23px;

		}
		.contacts-list__item+.contacts-list__item{margin-top: 16px;}
	}
	.swiper-wrapper{
		width: 100%;
		overflow-x: hidden ;
		overflow-y: hidden;
		scrollbar-width: none;
		&::-webkit-scrollbar {
				  width: 0;
				}
	}
	.border-list{
		&>p{
			@include bodytext;
			max-width: 570px;
			color: $black;
			margin-bottom: 60px!important;
			@media(max-width: 1279px){font-size: 17px; line-height: 1.46;}
		}
		ul{
			@include bodytext_19;
			margin: 0;
			padding: 0;
			list-style: none;
			color: $black;
			line-height: 1.46;
			li{
				padding-top: 40px;
				padding-bottom: 40px;
				border-bottom: 1px solid $aquagrey;

				// padding-left: 20px;
				span{
					max-width: 570px;
				}
				@media(max-width: 1280px){
					padding-left: 0;
					padding-top: 25px;
					padding-bottom: 25px;
					}
			}
		}
		p + ul li:first-child {
					border-top: 1px solid $aquagrey;
				}
	}
	.border-list--icons{
		li{
			display: flex;
			-webkit-align-items: center;
			        align-items: center;
			span:first-child{
				-webkit-flex-shrink: 0;
				        flex-shrink: 0;
			}
			span:last-child{
				max-width: 500px;
			}
			span + span{
				margin-left: 60px;
			}
		}
	}
	.blue-block{
		background-color: $blue;
		color: #fff;
		.block_title{
			color: #fff;
			span{
				color:$lightblue ;
				color: #fff;
			}
		}
		.border-list span{
			color: #fff;
		}
	}
	.medium-text{
		 @include large_text;
		 color: $black;
		 @media(max-width: 1279px){

		 		font-size: 15px;
			}
		}
}
#contacts{
	img{
		max-width:100%;
	}
	input, input:focus, button, button:focus{outline: none;}
	.container{
		@media(min-width:1280px){
			padding-left: 0;
			padding-right: 0;
		}
	}
	p{
		margin: 0;
	}
	.top_title{
		@media(min-width:1280px){

			margin-bottom: 100px;
		}
		@media(max-width:642px) {

			padding-left: 15px;
		}
	}
	.social p{margin-bottom: 20px;}
	.contacts-block{

		.contacts-list{

			color: $black;
			@include bodytext_19;
			flex-direction: column;
			@media(min-width:1280px) {
				min-width:380px;
				max-width: 380px;
			}
			&__item{
				width: 100%;
				margin-bottom: 40px;
				span+span {
					padding-left: 17px;
				}

			}
		}

		.social{

			margin-top: 20px;
			align-items: center;

			a{
				line-height: 1;
				&+a {
				margin-top: 0;
				margin-left: 16px;
				}
				&:hover svg path{
					fill: $darkblue;
				}
			}

			&-items{
				svg{
					width: 32px;
					height: 32px;
				}
				svg path{
					fill: $blue;
					transition:.3s;
				}
			}

		}

		.tg-info{

			a{
				@include button_small;
			}
		}
		.map{
			width: 100%;
			height: 450px;
		}
	}

	#contacts-team{
		.block_title--medium{
			margin-bottom: 100px;
			@media(max-width: 991px){
				margin-bottom: 80px;
				max-width: 320px;

			}
			@media(max-width: 767px) {
				margin-bottom: 60px;
				max-width: 250px;
			}
		}
	}
	#part_lines{
		background-color: $blue;
		background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='1920' height='450' viewBox='0 0 1920 450' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='1920' height='450' fill='%23004FCB'/%3e%3cmask id='mask0_2002:31911' style='mask-type:alpha' maskUnits='userSpaceOnUse' x='0' y='0' width='1920' height='450'%3e%3cpath d='M0 0H1920V450H0V0Z' fill='%23004FCB'/%3e%3c/mask%3e%3cg mask='url(%23mask0_2002:31911)'%3e%3cmask id='mask1_2002:31911' style='mask-type:alpha' maskUnits='userSpaceOnUse' x='-422' y='-3651' width='4058' height='4986'%3e%3cpath d='M1501.77 1333.67L3634.78 -2609.08L1711.96 -3649.32L-421.053 293.43L1501.77 1333.67Z' fill='%230052CB' stroke='%237DE2F4'/%3e%3c/mask%3e%3cg mask='url(%23mask1_2002:31911)'%3e%3cpath opacity='0.5' d='M-143.161 465.089L-871.607 -1940.43' stroke='%237DE2F4' stroke-linecap='round' stroke-linejoin='round'/%3e%3cpath opacity='0.5' d='M3745.62 -2433.62L-108.299 634.287L1815.57 -3650.5' stroke='%237DE2F4' stroke-linecap='round' stroke-linejoin='round'/%3e%3cpath opacity='0.5' d='M4482.95 301.99L-97.4589 719.936L3822.9 -940.484' stroke='%237DE2F4' stroke-linecap='round' stroke-linejoin='round'/%3e%3cpath opacity='0.5' d='M4621.6 2711.01L-96.9376 657.048L4660.12 1389.29' stroke='%237DE2F4' stroke-linecap='round' stroke-linejoin='round'/%3e%3c/g%3e%3c/g%3e%3c/svg%3e");
		background-repeat: no-repeat;
		background-position: left center;
		padding-top: 187px;
		padding-bottom: 187px;
		.block_title{
			color: #fff;
		}
		.btn{
			@include button_2;
		}
		.btn-wrapper{
			padding-left: 65px;
		}
	}

	// - contacts-service
	.contacts-service{
		padding-top: 0;
		@media(min-width:1280px){
			padding-top: 180px;
		}

		.contacts-list-wrapper{
			padding-top: 65px;
			p{
				font-size: 19px;
				color: $black;
				margin-bottom: 20px;
			}

			.contacts-list__item+.contacts-list__item {
				margin-top: 16px;
			}
		}
	}

	// -mass-media
	.mass-media{
		background-color: $grey;
		&-text{
			@include bodytext_19;
			color: $black;
			p +p {
				margin-top: 18px;
			}
			.btn{
				margin-top: 50px;
				@include button_1;
			}
		}
		.dlink {
				margin-top: 0;
				margin-bottom: 0;
			}
	}

	// tabs
	.faq{
		.accordion-toggle{
			padding: 40px 20px;
			background-color: $grey;
			.info-block{
				margin-top: 60px;
				p{
					margin-bottom: 20px;
					color: $black;
				}
				.btn{
					width: 100%;
					max-width:330px;
				}
			}
		}
		.nav-tabs{
			flex-direction: column;
			border: 0px;
			.nav-link,
			.nav-link.active{
				@include bodytext_19;
				border: 0px;
				background-color: transparent;
				padding-left: 0;
				padding-right: 0;
				padding-top: 10px;
				padding-bottom: 10px;
				color: $black;


			}
			.nav-link.active{
				color:$blue;
			}
		}
		.accordion {
			min-height: 105px;
			margin-top: -20px;
		}
	}
	.faq .block_title{
		margin-bottom: 120px;
		@media(max-width:1199px){
			font-size: 21px;
			margin-bottom: 60px;
		}
	}

}
// -filters dropdown
#contacts, #archive-categories{
	.filters-block {
		position: relative;

		.filters-input {
			height: 52px;
			display: none;
			@media(max-width: 1279px){
				display: block;
			}
			input {
				width: 100%;
				height: 52px;
				padding-right: 55px;
				padding-left: 15px;
				background-color: transparent;
				border: 0;
				border-radius: 2px;
				outline: none;
				color:$aquagrey;
				&::placeholder{
					font-size: 15px;
					color:$aquagrey;
				}
			}
		}

		.filters-btn {
			@include button_plus;
			height: 52px;
			width: 52px;
			position: absolute;
			top: 0;
			right: 0;
			z-index: 5;
			color: #fff;
			background-color: $blue;
			border: 0;

		}

		.filters-drop {
			@media(min-width: 1280px){
				display: block!important;
			}
			.nav-link {width: 100%;}
			div{
				flex-direction: column;
				display: flex;
			}
			@media(max-width: 1279px){
				padding: 20px;
				display: none;
				&-input {	display: block;}
				.nav-link{font-size: 17px; width: 33%;}
			}
			@media(max-width: 767px) {
				padding-left: 10px;
				padding-right: 10px;
				div{justify-content: space-between;}
			.nav-link {
					width: 48%;

				}
				@media(max-width: 359px) {
					.nav-link {
						font-size: 15px;
					}
				}
			}
		}
	}
}
#contacts .border-list p+ul li:first-child {
	border-top: 1px solid rgba(144, 148, 161, .6);
}
#contacts .border-list ul li {
	border-bottom: 1px solid rgba(144, 148, 161, .6);
	span{
		color:inherit;
		@media(max-width: 767px){
			color:$aquagrey;
		}
		
	}
}