        }
      }

      .search_burger {
        .search {
          // background-image: url("data:image/svg+xml,%3Csvg width='19' height='22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.854 16.638C2.654 15.123 1.2 12.492 1.2 9.5 1.2 4.806 4.78 1 9.2 1c4.418 0 8 3.806 8 8.5s-3.582 8.5-8 8.5M14.2 17l4 4' stroke='%23fff' stroke-width='1.2' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
