  }

  .link {
    @include arrow_link;
    line-height: 31.05px;
  }

  .lines {
    min-height: 940px;
    background-color: $blue;
    display: flex;
    align-items: center;
    position: relative;
    overflow: hidden;
    .date {
      margin-bottom: 40px;
      font-size: 37px;
      line-height: 95%;
      text-transform: uppercase;
      color: $lightblue;

      >span {
        font-size: 19px;
        line-height: 115%;
      }
    }

    .title {
      margin-bottom: 20px;
      font-weight: bold;
      font-size: 47px;
      line-height: 110%;
      letter-spacing: -0.01em;
      color: #FFFFFF;
    }

    .text {
      margin-bottom: 80px;
      font-size: 33px;
      line-height: 116%;
      color: #FFFFFF;
    }

    .registrations-count {
      margin-top: 40px;
      display: flex;
      align-items: center;
      color: $lightblue;
      .big {
        font-size: 36px;
        line-height: 36px;
        font-weight: 200;
        font-family: "aktiv-grotesk-thin",sans-serif;
        margin-right: 8.5px;
        padding-right: 8.5px;
        border-right: 1px solid #fff;
      }
      .small {
        font-size: 14px;
        line-height: 14px;
      }
    }

    &_bg {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      &-item{
        position: absolute;
        width: 150%;
        height: 2px;
        right: 0;
        bottom: 18%;
        transform-origin: right 0;
        background-color: #94c2ff;
        transition: .5s;
      }
      &-item:nth-child(1){
        transform: rotate(314deg);
        bottom: 11%;
      }
      &-item:nth-child(2){
        transform: rotate(356deg);
        bottom: 16%;
      }
      &-item:nth-child(3){
        transform: rotate(386deg);
        bottom: 19%;
      }
      &-item:nth-child(4){
        transform: rotate(403deg);
        bottom: 22%;
      }
      &-item:nth-child(5){
        transform: rotate(422deg);
        bottom: 26%;
      }
      &-item:nth-child(6){
        transform: rotate(435deg);
        bottom: 35%;
      }
      &-item.active{
        opacity: 0;
        bottom: 16%;
        transform: rotate(360deg);
      }
      &-item.active:nth-child(2){
        opacity: 0;
        background-color: #fff;
        transform: rotate(360deg);
      }
      &-circle{
        position: absolute;
        width:10px;
        height: 10px;
        top: 50%;
        border-radius: 50%;
        transform: translateY(-50%);
      }
      .circle_color-w{
        opacity: 0;
        right: -20%;
        background-color: #fff;
      }
      .circle_color-b{
        opacity: 0;
        right: -20%;
        background-color: #a2caff;
      }
    }
  }
  
  #about {
    margin-bottom: 175px;
  
    .container {
      margin-top: 180px;
  
      &:first-child {
        margin-top: 0;
      }
    }
  
    .video {
      margin-top: 140px;
    }
  
    .numbers {
      margin-top: 120px;
  
      display: grid;
      grid-template-columns: repeat(3, 33%);
      grid-template-rows: repeat(2, 1fr);
      //grid-column-gap: 40px;
  
        .lblock {
          height: 450px;
          grid-row: 1 / span 2;
          &:first-child {
              grid-column: 1/ 1;
              margin-right: 20px;
              svg {
                position: absolute;
                bottom: 10px;
                right: 0;
                transform: rotateZ(7.5deg);
                max-width: 100%;
              }
          }  
          &:last-child {
              grid-column: 3 /3;
              margin-left: 20px;
              svg {
                position: absolute;
                bottom: 30px;
                right: calc(50% - 95px);
            }
          }
  
        }
  
        .sblock {
          height: 205px;
          grid-column: 2/ 2;
          margin-left: 20px;
          margin-right: 20px;
  
          &:nth-child(3) {
            margin-bottom: 0;
            margin-top: 20px;
          }
        }
  
        [class$=block] {
          background-color: $blue;
          padding: 40px 0 0 40px;
          position: relative;
          overflow: hidden;
          &:hover {
            background-color: $darkblue;
          }
        }
  
        .number {
          font-size: 96px;
          line-height: 100%;
          color: #FFFFFF;
        }
  
        .text {
          @include bodytext_19;
          color: #fff;
          margin-top: 20px;
        }
    }
  
    .partners {
      // display: flex;
      // justify-content: space-between;
      margin-bottom: 75px;
      overflow: hidden;
      .slick-track {
        margin-left: 0;
        display: flex;
        align-items: center;
      }

      img {
        max-width: 100%;
        margin: 0 auto;
      }
    }
  }  
}

.container-wideo{
  // width: 720px;
  width: 100%;
  margin: 0 auto;
}
.video {
  width: 320px;
  height: 35vw;
  position: relative;
  margin: 0 auto;
  margin-top: 140px;
  transition: .3s;
  &_over{
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-image: url(../img/home_video.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    z-index: 5;
    transition: .3s;
    &.active{
      opacity: 0;
      pointer-events: none;
    }
  }
  &__cursor{
    position: absolute;
    width: 70px;
    height: 70px;
    top: 50%;
    left: 50%;
    pointer-events: none;
    border: 2px solid #fff;
    border-radius: 50%;
    transform: translate(-50%, -50%);
    z-index: 6;
    &.active{
      display: none;
    }
    &::before,
    &::after {
      content: '';
      position: absolute;
      border: 2px solid #fff;
      left: -30px;
      right: -30px;
      top: -30px;
      bottom: -30px;
      border-radius: 50%;
      -webkit-animation: pulse 1.8s linear infinite;
              animation: pulse 1.8s linear infinite;
    }
    svg {
      position: absolute;
      width: 25px;
      height: 25px;
      top: 50%;
      left: 50%;
      transform: translate(-40%, -50%);
    }
    &::after {
      -webkit-animation-delay: .7s;
              animation-delay: .7s;
    opacity: 0;
    }
  }
}
.video-bl{
  width: 100%;
  height: 100%;
  margin: 0 auto;
}

@-webkit-keyframes pulse {
  0% {
   -webkit-transform: scale(0.5);
           transform: scale(0.5);
   opacity: 0;
  }
  50% {
   opacity: 1;
  }
  100% {
   -webkit-transform: scale(1.2);
           transform: scale(1.2);
   opacity: 0;
  }
 }
 @keyframes pulse {
  0% {
   -webkit-transform: scale(0.5);
           transform: scale(0.5);
   opacity: 0;
  }
  50% {
   opacity: 1;
  }
  100% {
   -webkit-transform: scale(1.2);
           transform: scale(1.2);
   opacity: 0;
  }
 }

#renewal {
  padding-top: 145px;
  background-color: $blue;

  .title {
    @include title_h3;
    color: #fff;
  }

  .links {
    a {
      @include arrow_link_white;
      margin-left: 40px;

      &:first-child {
        margin-left: 0;
      }
    }
  }

  .articles {
    flex-wrap: nowrap;
    margin-top: 80px;
    padding-bottom: 200px;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 20px;
      height: 2px;
      background-color: $lightblue;
      width: calc(100% - 40px);
    }

    >[class^=col] {
      padding-top: 70px;
      position: relative;

      a {
        display: block;
        padding: 20px 110px 20px 20px;
        @include bodytext_19;
        @include white_arrow_bg;
        background-position: calc(100% - 25px) calc(100% - 20px);

        .date {
          color: $lightblue;
          text-transform: uppercase;
          margin-bottom: 20px;
        }

        .text {
          color: #fff;
          height: 50px;
          overflow: hidden;
        }

        &:hover {
          text-decoration: none;
          background-color: $darkblue;
        }
      }

      &::before {
        position: absolute;
        content: '';
        top: -6px;
        left: 15px;
        width: 14px;
        height: 14px;
        background-repeat: no-repeat;
        background-image: url("data:image/svg+xml,%3Csvg width='14' height='14' viewBox='0 0 14 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='7' cy='7' r='7' fill='white'/%3E%3C/svg%3E%0A");
      }
    }
  }
}
.drag_bl{
  overflow-x: auto;
  overflow-y: hidden;
  cursor: pointer;
  user-select: none;
  -ms-overflow-style: none;
    scrollbar-width: none;
}
.drag_bl::before{
  top: 7px !important;
}
.drag_bl .renewal_line::before{
  top: 1px !important;
}
.drag_bl.active .renewal_line{
