.preloader{
    position: fixed;
    display: flex;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    pointer-events: none;
    z-index: 9999;
    transition: .3s;
    &__item{
        width: 5.555vw;
        height: 100%;
        background-color: var(--blue);
    }
    &.active{
        opacity: 0;
    }
}