#about {
  .top_image {
    background-image: url(../img/about_top.jpg);
  }

  .block_title {
    font-size: 27px;
  }

  #fulcrum {
    font-size: 19px;
    line-height: 135%;
    color: $black;
    padding: 180px 0 156px;
    background-color: #F6F6F6;

    .row {
      &:first-child {
        align-items: flex-end;
        margin-bottom: 120px;
      }
    }

    .title {
      color: $blue;
      margin: 20px 0;
    }
  }

  #increase_number {
    margin: 180px 0;

    .diagram {
      margin-top: 120px;

      .diagram_grid {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        border-bottom: 4px solid #CEDDF5;
        position: relative;

        &::after {
          content: '';
          position: absolute;
          height: 5px;
          width: 350px;
          background-color: $blue;
          left: 0;
          bottom: -4px;
        }

        >div {
          display: flex;
          align-items: flex-end;

          >div {
            border: 1px solid #CEDDF5;
            box-sizing: border-box;
            min-height: 220px;
            flex-grow: 1;
            position: relative;

            &::before {
              content: '';
              position: absolute;
              width: 15px;
              height: 15px;
              left: -8px;
              bottom: 18px;
              background-position: center;
              background-image: url("data:image/svg+xml,%3Csvg width='15' height='15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='7.32' cy='7.165' r='7' fill='%237DE2F4'/%3E%3C/svg%3E");
            }

            .year {
              font-size: 19px;
              line-height: 148%;
              position: absolute;
              left: 20px;
              top: 90px;
            }

            .qty {
              font-size: 72px;
              line-height: 100%;
              color: $blue;
              position: absolute;
              left: 20px;
              top: 140px;
            }
          }

          &:nth-child(2) {
            >div {
              min-height: 350px;

              &::before {
                bottom: calc(220px - 7.5px)
              }

              .year {
                left: auto;
                right: calc(100% + 8px);
                top: 100px;
              }

              .qty {
                top: 94px;
              }
            }
          }

          &:nth-child(3) {
            >div {
              min-height: 420px;

              &::before {
                bottom: calc(350px - 7.5px)
              }

              .year {
                left: auto;
                right: calc(100% + 8px);
                top: 40px;
              }

              .qty {
                top: 38px;
              }
            }
          }

          &:nth-child(4) {
            >div {
              min-height: 550px;

              &::before {
                bottom: calc(420px - 7.5px)
              }

              .year {
                left: auto;
                right: calc(100% + 8px);
                top: 100px;
              }

              .qty {
                top: 94px;
              }
            }
          }
        }
      }
    }

  }

  #our_mission {
    >div {
      &:first-child {
        padding: 280px 0 360px;
        background-color: $blue;
        color: #fff;
        position: relative;

        &::after {
          content: '';
          position: absolute;
          width: 1920px;
          height: 900px;
          bottom: 0;
          left: 0;
          opacity: 0.5;
          background-repeat: no-repeat;
          background-image: url("data:image/svg+xml,%3Csvg width='1920' height='900' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg stroke='%237DE2F4' stroke-linecap='round' stroke-linejoin='round'%3E%3Cpath d='M-45.349 707.772L-620.01-1589.46M3664.66-2124.14L21.456 890.335 1954.07-3250.52M4316.98 487.117L41.663 984.355 3754.88-695.58'/%3E%3Cpath d='M4409.51 2811.66L40.238 916.712 4500.55 1528.19'/%3E%3C/g%3E%3C/svg%3E");
        }
      }

      &:nth-child(2) {
        background-color: #F6F6F6;
        margin-top: 90px;
        padding: 30px 0 180px;
        position: relative;

        &::before {
          content: '';
          width: 100%;
          height: 100px;
          position: absolute;
          right: 0;
          top: -90px;
          transform-origin: right top;
          background-color: #FFF;
          transform: skewY(6.65deg);
        }

        &::after {
          content: '';
          width: 100%;
          height: calc(100% + 215px);
          position: absolute;
          right: 0;
          top: 0;
          transform-origin: right top;
          background-color: #F6F6F6;
          transform: skewY(6.65deg);
          z-index: 2;
        }

        [class^=col] {
          z-index: 3;
        }
      }
    }

    .big_title {
      font-size: 72px;
      line-height: 70%;
      margin-bottom: 60px;
      text-transform: uppercase;
    }

    .block_title  {
      font-size: 27px;
    }

    .big_text {
      font-size: 33px;
      line-height: 115%;
    }

    ul {
      list-style: none;
      padding: 0;

      li {
        >div {
          &:first-child {
            font-size: 23px;
            line-height: 136%;
            color: $black;
            margin: 40px 0 20px;
          }
        }

        &:first-child {
          >div {
            &:first-child {
              margin-top: 0;
            }
          }
        }
      }
    }

    .fluid-slider {
      background-color: #fff;
      z-index: 4;
      position: relative;
      display: flex;

      >div {
        position: relative;
        top: -40px;
        margin-left: 40px;

        &:first-child {
          margin-left: 0;
        }
      }
    }
  }

  #why_choose_tnf {
    margin: 160px 0 0;

    .block_title {
      margin-bottom: 120px;
    }

    .why_grid {
      display: grid;
      grid-template-columns: repeat(4, calc(25% - 30px));
      grid-template-rows: repeat(2, 1fr);
      column-gap: 40px;
      row-gap: 40px;
      margin-bottom: 156px;

      >div {
        position: relative;

        &[class^=number-] {

          >div {
            background-color: $blue;
            padding: 20px;
            font-size: 15px;
            color: $lightblue;
            height: 100%;


            span {
              display: block;
              font-size: 19px;
              line-height: 135%;
              color: #FFF;
              margin-top: 69px;
            }
          }

        }

        &[class^=image-] {
          img {
            max-width: 100%;
          }
        }

        >div {
          position: relative;
        }

        &.number- {
          &1 {
            >div {
              top: 94px;
            }
          }

          &2,
          &5 {
            >div {
              top: 156px;
            }
          }
        }

        &.image- {
          &2 {
            >div {
              top: 94px;
            }
          }
        }
      }
    }
  }

  #tnf_grown {
    margin: 180px 0;

    .block_title {
      margin-bottom: 120px;
    }

    .slider {
      flex-wrap: nowrap;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        width: 100vw;
        height: 2px;
        background-color: rgba(34, 38, 51, .3);
        left: 25px;
        top: 0;
      }

      [class^=col] {
        &::after {
          content: '';
          position: absolute;
          top: -7px;
          left: 20px;
          width: 15px;
          height: 15px;
          background-repeat: no-repeat;
          background-position: center;
          background-image: url("data:image/svg+xml,%3Csvg width='15' height='15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14.5 7.756c0 3.739-2.926 6.74-6.5 6.74-3.575 0-6.502-3.001-6.502-6.74 0-3.74 2.927-6.742 6.501-6.742 3.575 0 6.502 3.002 6.502 6.742z' fill='%23004FCB' stroke='%23004FCB'/%3E%3C/svg%3E");
        }
      }

      .year {
        font-size: 27px;
        line-height: 130%;
        color: $blue;
        margin: 40px 0 20px;
      }

      .text {
        color: $black;
        margin-bottom: 40px;
        min-height: 48px;
      }
    }
  }

  #our_profi {
    background-color: #F6F6F6;
    padding: 160px 0;

    .gallery {
      margin-top: 80px;

      [class^=col] {
        margin-top: 40px;
      }
    }

    .name {
      font-size: 19px;
      line-height: 148%;
      color: $black;
      margin: 40px 0 5px;
    }

    .text {
      font-size: 15px;
      height: 72px;
    }

  }

  #see_also {
    margin: 180px 0;

    .small_title {
      font-size: 27px;
      line-height: 130%;
      text-transform: none;
      font-weight: 400;
    }
  }

}
#main_site {
    margin: 180px 0 0;
    .speakers{
      cursor: none;
      position: relative;
      z-index: 5;
      &__left,
      &__right{
        position: absolute;
        width: 50%;
        height: 100%;
        top: 0;
        z-index: 2;
      }
      &__left{
        left: 0;
      }
      &__right{
        right: 0;
      }
      &__wrapp{
        position: relative;
        flex-wrap: nowrap;
        z-index: 1;
      }
      &__width{
        position: relative;
        z-index: 1;
      }
      div{
        pointer-events: none;
      }
      .arrow {
        margin-right: 0;
        position: absolute;
        width: 80px;
        height: 80px;
        cursor: pointer;
        background-repeat: no-repeat;
        // opacity: .4;
        opacity: 0;
        transition: .3s;
        background-image: url("data:image/svg+xml,%3Csvg width='80' height='80' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect y='.566' width='79.333' height='79.333' rx='39.667' fill='%23fff'/%3E%3Cpath d='M37.665 56.8l17.942-15.307c.837-.559.837-1.962 0-2.52L37.665 23.666M45.507 40.233H23.1' stroke='%237DE2F4' stroke-width='1.9' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
      }
      &:hover .arrow_pr-next{
        opacity: 1;
        z-index: 3;
      }
      .arrow_pr-next{
        transition: opacity .3s;
        &.active{
          transform: rotate(180deg);
        }
      }
    }
    .supported_by {
      margin-top: 230px;
      margin-bottom: 230px;
      align-items: center;
    }
    .block_title {
    &.big {
      margin-bottom: 120px;
      font-size: 33px;
    }

    font-size: 27px;
    line-height: 130%;
  }
}