#article {
  @include media-breakpoint-down(lg) {}

  @include media-breakpoint-down(md) {
    font-size: 17px;
    line-height: 146%;

    .link_back {
      display: none;
    }

    .title {
      font-size: 21px;
      line-height: 135%;
    }

    #main_content {
      margin: 100px 0 140px;

      .row {
        &:last-child {
          margin-top: 140px;

          a {
            font-size: 15px;
            line-height: 146%;
            margin: 20px 0;

            &.prev {
              padding: 0 0 0 35px;
            }

            &.next {
              padding: 0 35px 0 0;
            }
          }
        }
      }

      .speakers {
        max-width: 65%;
        font-size: 17px;
        line-height: 146%;
        text-transform: none;

        span {
          font-size: 15px;
          line-height: 146%;
          margin-top: 10px;
        }
      }
    }
  }

  @include media-breakpoint-down(sm) {}

  @include media-breakpoint-down(xs) {
    font-size: 15px;
    line-height: 146%;

    .title {
      font-size: 19px;
      line-height: 135%;
    }

    #main_content {
      margin: 60px 0 100px;

      .row {
        &:nth-child(2) {
          margin-top: 80px;
        }
        &:last-child {
          margin-top: 80px;
          a {
            &.prev {
              display: none;
            }
          }
        }
      }

      .speakers {
        font-size: 15px;
        line-height: 146%;
        max-width: none;

        span {
          font-size: 13px;
          line-height: 146%;
        }
      }

      .text {
        margin-top: 80px;
        .social {
          width: 100%;
          display: flex;
          justify-content: space-between;
        }
      }
    }
  }
}