#participation {
  @include media-breakpoint-down(lg) {

    #bpartners {
      padding: 100px 0;

      .text {
        font-size: 17px;
        line-height: 146%;
      }
    }

    #key_event {
      margin: 140px 0;

      .slider {
        margin-left: -15px;

        [class^=col] {
          min-width: 239px;
          padding: 0 15px;

          .title {
            font-size: 17px;
            line-height: 146%;
            margin-bottom: 16px;
            min-height: 48px;
          }
        }
      }
    }

    #meet_result {
      padding: 100px 0;

      .block_title {
        margin-bottom: 60px;
      }

      .block_content {
        div {
          &:nth-child(odd) {
            font-size: 17px;
            line-height: 146%;
          }

          &:nth-child(even) {
            font-size: 15px;
            line-height: 146%;
          }
        }
      }
    }

    #eff_format {
      margin: 140px 0;

      .block_title {
        margin-bottom: 60px;
      }

      .mozaic {
        display: flex;
        flex-wrap: nowrap;

        >div {
          img {
            max-width: auto;
            min-width: 320px;
            height: 300px;
          }

          .title {
            font-size: 17px;
            color: $lightblue;
            max-height: 300px;

            span {
              font-size: 13px;
              line-height: 146%;
              color: #fff;
              margin: 10px 0 40px;
            }

            .btn {
              font-size: 15px;
              line-height: 150%;
              min-width: calc(100% - 20px);
            }
          }

          &::after {
            position: absolute;
            content: '';
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(3, 38, 99, .7);
            z-index: 2;
          }
        }
      }
    }

    #big_text {
      font-size: 48px;
      line-height: 155%;
      padding: 300px 0;

      // div {
      //   width: 980px;
      //   position: relative;
      //   left: calc(50% - 490px);
      // }
    }

    #tnf_special {
      margin: 120px 0 80px;

      .block_title {
        margin-bottom: 60px;
      }

      .row {
        &:last-child {
          [class^=col] {
            margin-bottom: 40px;

            .title {
              font-size: 17px;
              line-height: 146%;
            }

            .text {
              font-size: 15px;
              line-height: 146%;
            }
          }
        }
      }
    }

    #part_lines {
      font-size: 33px;
      line-height: 115%;
      padding: 280px 0;
    }

    #part_comm {
      margin: 140px 0;

      .large_text {
        margin-top: 40px;
        font-size: 17px;
      }

      .slider {
        margin-top: 60px;

        >div {
          .img {
            min-height: 390px;
            //background-size: contain;
            background-position: center;
          }
        }
      }
    }
  }

  @include media-breakpoint-down(md) {
    #key_event {
      .block_title {
        br {
          display: inline;
        }
      }
    }

    #key_event{
      .slider__scroll{
        width: calc(50% + 50vw);
        overflow-x: auto;
        overflow-y: hidden;
      }
      .slider__scroll::-webkit-scrollbar{width: 0;}
    }

    #part_comm{
      .slider{
        #slidewrapper{
          width: calc(50% + 50vw);
          overflow-x: auto;
          overflow-y: hidden;
        }
        #slidewrapper::-webkit-scrollbar{width: 0;}
      }
    }
  }

  @include media-breakpoint-down(xs) {
    #bpartners {
      padding: 80px 0;

      .block_title {
        margin: 0 0 40px;
      }

      .text {
        font-size: 15px;
        line-height: 146%;
      }
    }

    #key_event {
      margin: 100px 0;

      .block_title {
        margin-bottom: 80px;
      }

      .slider {
        margin-left: -5px;
        [class^=col] {
          min-width: 246px;
          padding: 0 5px;

          .title {
            font-size: 15px;
            line-height: 146%;
            margin-bottom: 20px;
            max-width: 80%;
            min-height: 42px;
          }
        }
      }

    }

    #meet_result {
      padding: 80px 0;

      .block_title {
        margin-bottom: 80px;
      }

      .block_content {
        grid-template-rows: auto, auto;
        grid-template-columns: 1fr;
        div {
          &:nth-child(2) {
            border-top: none;
          }
          &:nth-child(odd) {
            border-bottom: none;
            padding-bottom: 20px;
          }
          &:nth-child(even) {
            padding-top: 0;
          }      
        }        
      }      
    }

    #eff_format {
      margin: 100px 0;
      .block_title {
        margin-bottom: 60px;
      }

      .mozaic {
        flex-direction: column;
        align-items: center;
        > div {
          max-width:320px;
        }
      }
    }

    #big_text {
      font-size: 33px;
      // line-height: 115%;
      padding: 100px 0;

      // div {
      //   width: 740px;
      //   position: relative;
      //   left: calc(50% - 370px);
      // }
    }

    #tnf_special {
      margin: 100px 0 40px;
      .block_title {
        margin-bottom: 90px;
      }
      .row {
        &:last-child {
          [class^=col] {
            margin-bottom: 60px;
            .title {
              margin: 30px 0 10px;
            }
            svg {
              width: 40px;
            }
          }
        }
      }
    }

    #part_lines {
      padding: 266px 0;
      font-size: 21px;
      line-height: 130%;
      text-align: center;      
    }

    #part_comm {
      margin: 100px 0;
      .large_text {
        margin-top: 60px;
        font-size: 15px;
      }
      .slider {
        margin-top: 80px;
        > div {
          // left: calc(50vw - 430px);
          .img {
            min-width: 280px;
            min-height: 300px;
            background-size: cover;
            margin: 0 5px;
            overflow: hidden;
            flex-grow: 0;
          }
        }
      }
    }    
  }
}