body {
  @include bodytext;
  overflow: hidden;
  &.active{
    overflow: auto;
    overflow-x: hidden;
  }
}

a {
  color: $black;

  &:hover {
    color: $blue;
    text-decoration: none;
  }
}

.btn.btn-primary {
  @include button_1;
}
.btn.btn-primary-reverse {
  @include button_1_reverse;
}
.btn.btn-primary-grey {
  @include button_1_grey;
}

.btn.btn-secondary {
  @include button_2;
}

.small_title {
  font-weight: bold;
  font-size: 23px;
  line-height: 135%;
  color: $black;
  text-transform: uppercase;
}

.large_text {
  @include large_text;
  color: $black;
  letter-spacing: -.3px;
}

header {
  border-bottom: 1px solid $blue;
  // position: relative;
  width: 100%;
  height: auto;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #fff;
  transition: .3s;
  z-index: 900;
  //overflow-x: hidden;
  &.active{
    transform: translateY(-100%);
  }
