    >.row {
      width: 100%;

      .nav {
        .nav-item {
          padding: 68px 14px 30px;
        }
        &:last-of-type {
          justify-content: flex-end;
          flex-grow: 1;
        }
      }
    }

    .search_burger {
      position: absolute;
      left: 100%;
      display: flex;
      margin-top: 76px;

      .search {
        width: 24px;
        height: 24px;
        margin-right: 75px;
        background-repeat: no-repeat;
        background-position: center;
        background-image: url("data:image/svg+xml,%3Csvg width='19' height='22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.627 16.638C2.427 15.123.973 12.492.973 9.5c0-4.694 3.58-8.5 7.999-8.5 4.418 0 8 3.806 8 8.5s-3.582 8.5-8 8.5M13.973 17l4 4' stroke='%23004FCB' stroke-width='1.6' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
        cursor: pointer;
      }

      .burger {
        color: #fff;
        text-transform: uppercase;
        cursor: pointer;
        position: relative;
        padding-left: 45px;
        z-index: 10;

        >span {
          display: block;
          position: absolute;
          width: 32px;
          height: 2px;
          background-color: #fff;
          border-radius: 1px;
          left: 0;
          top: calc(50% - 1px);
          opacity: 0;
          transition: transform .5s, opacity .2s;

          &:first-child {
            top: calc(50% - 12px);
            opacity: 1;
            transform: rotateZ(45deg);
            transform-origin: left top;
          }

          &:nth-child(3) {
            top: calc(50% + 10px);
            opacity: 1;
            transform: rotateZ(-45deg);
            transform-origin: left bottom;
          }
        }

        &.collapsed {
          >span {
            opacity: 1;
            transition: transform .5s, opacity .2s linear .3s;
            background-color: $black;

            &:first-child {
              top: calc(50% - 9px);
              opacity: 1;
              transform: rotateZ(0);
              transform-origin: left top;
            }

            &:nth-child(3) {
              top: calc(50% + 7px);
              opacity: 1;
              transform: rotateZ(0);
              transform-origin: left bottom;
            }
          }

          >div {
            >span {
              color: $black;

              &:first-child {
                opacity: 1;
              }

              &:last-child {
                opacity: 0;
              }
            }
          }
        }

        >div {
          position: relative;

          >span {
            position: absolute;
            left: 0;
            top: 0;
            transition: opacity .15s linear;

            &:first-child {
              opacity: 0;
            }

            &:last-child {
              opacity: 1;
            }
          }
        }

        &:hover {
          >span {
            background-color: $lightblue;
          }  
          > div {
            > span {
              color: $lightblue;
            }
          }
          &.collapsed {
            >span {
              background-color: $blue;
            } 
            > div {
              > span {
                color: $blue;
              }
            }                       
          }        
        }
      }
    }
  }

