  #mainMenu {
    top: 0;
    left: 0;
    width: 100%;
    position: absolute;
    z-index: 9;
    background-color: $darkblue;

    &::after {
      position: absolute;
      content: '';
      width: 100%;
      top: 155px;
      height: 2px;
      background-color: #fff;
      opacity: .3;
    }

    &::before {
      position: absolute;
      content: '';
      width: 50%;
      height: 100%;
      top: 0;
      left: 50%;
      background-color: $black;
      opacity: .5;
    }

    [class^=col] {
      min-height: 100vh;

      &:first-child {
        background-color: $darkblue;

        .title {
          font-size: 27px;
          line-height: 130%;
          color: #fff;
          margin-top: 70px;
        }

        .mainMenu {
          margin-top: 55px;
          display: grid;
          grid-template-columns: 1fr 1fr 1fr;

          .mMenuBack {
            display: none;
          }

          >div {
            margin-top: 60px;
            font-size: 19px;
            line-height: 148%;
            text-transform: uppercase;
            color: #fff;

            ul {
              list-style: none;
              margin: 20px 0 0 0;
              padding: 0;

              li {
                margin-bottom: 15px;

                a {
                  color: $aquagrey;
                  white-space: nowrap;
                  font-size: 19px;
                  text-transform: none;

                  &:hover {
                    color: $lightblue;
                  }
                }

                &:last-child {
                  margin-bottom: 0;
                }
              }
            }
          }
        }
      }

      &:last-child {
        color: $aquagrey;
        font-size: 15px;

        a {
          color: $lightblue;

          &:hover {
            color: $lightblue;
          }
        }

        >div {
          margin: 220px 0 0 35px;

          .tel {
            margin-bottom: 35px;

            .clink {
              background-image: url("data:image/svg+xml,%3Csvg width='20' height='23' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11.134 20.17c4.274 3.008 7.807.394 7.866-2.738.024-1.296-3.749-3.472-4.571-2.82-1.032.816-1.802 2.403-3.424 1.368-1.623-1.034-5.568-4.293-5.407-6.359.09-1.156 1.881-1.035 1.745-3.087 0 0-.446-4.361-2.076-4.888C2.348.703 1.175 4.477 1.01 7.528c-.247 4.558 4.566 9.037 7.763 10.947' stroke='%237DE2F4' stroke-width='1.2' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
