.counter {
  display: flex;

  .arrow {
    width: 24px;
    height: 24px;
    display: block;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url("data:image/svg+xml,%3Csvg width='24' height='25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M13.06 21.27L3.56 13.167c-.443-.296-.443-1.038 0-1.334l9.499-8.104M8.908 12.5H20.77' stroke='%23222633' stroke-width='1.5' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
    cursor: pointer;

    &.next {
      background-image: url("data:image/svg+xml,%3Csvg width='24' height='25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10.94 21.27l9.499-8.103c.443-.296.443-1.038 0-1.334L10.94 3.729M15.092 12.5H3.229' stroke='%23222633' stroke-width='1.5' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
    }
  }

  .count {
    font-size: 17px;
    line-height: 24px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: $black;
    margin: 0 40px;

    span {
      font-size: 22px;
      line-height: 24px;
      color: $blue;
    }
  }
}

.pfilter {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  margin-bottom: 80px;

  a {
    color: $aquagrey;
    text-transform: uppercase;
    line-height: 31.05px;
    letter-spacing: -.45px;
    white-space: nowrap;

    &::after {
      content: '/';
      padding: 0 10px;
    }

    &:last-child::after {
      content: '';
    }

    &:hover,
    &.active {
      color: $blue;

      &::after {
        color: $aquagrey;
      }
    }
  }
}

footer {
  background-color: $blue;
  color: #fff;
  overflow: hidden;

  .dark {
    background-color: #0A45C1;

    .row {
      padding: 65px 0 55px;

      [class^=col] {
        &:first-child {
          font-weight: bold;
          font-size: 23px;
          line-height: 135%;
          letter-spacing: -0.01em;
          text-transform: uppercase;
          color: #FFFFFF;
        }
        form {
          min-width: 100%;
          position: relative;
          input {
            &[type=text] {
              background: #0A45C1;
              border: 1.6px solid #61CFE5;
              box-sizing: border-box;
              border-radius: 2px;
              color: #fff;
              font-size: 15px;
              line-height: 146%;   
              padding: 15px;  
              min-width: 100%;
              &::placeholder {
                color: #fff;
              }
            }
            &[type=submit] {
              content: '';
              position: absolute;
              top: 0;
              right: 0;
              height: 100%;
              width: 54px;
              background-color: $lightblue;
              border: none;
              @include black_arrow_bg;
              background-position: center;
            }
          }
        }
      }
    }
  }

  .row {
    padding: 60px 0 60px;
    position: relative;

    &.organizers {
      
      .title {
        font-size: 17px;
        line-height: 148%;
        text-transform: uppercase;
        color: $lightblue;
        margin-bottom: 40px;
      }

      [class^=col] {
        a {
          display: flex;
          align-items: center;
          color: #fff;

          &:hover {
            color: $lightblue;
          }

          span {
            font-size: 17px;
            line-height: 125%;
            letter-spacing: -0.01em;
            margin-left: 15px;
          }

          img {
            min-height: 45px;
          }
        }
      }

      &::before {
        content: "";
        position: absolute;
        width: calc(100% - 40px);
        height: 2px;
        background-color: rgba(255, 255, 255, .2);
        top: 0;
        left: 20px;
      }      

      &::after {
        content: "";
        position: absolute;
        width: calc(100% - 40px);
        height: 2px;
        background-color: rgba(255, 255, 255, .2);
        bottom: 0;
        left: 20px;
      }
    }

    &.copy {
      padding-top: 55px;
      justify-content: space-between;

      [class^=col] {
        >div {
          padding-left: 40px;
          padding-top: 40px;
          background-repeat: no-repeat;
          background-position: left bottom;
          background-image: url("data:image/svg+xml,%3Csvg width='23' height='59' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M20.185 1.347C20.122.65 20.725.04 21.515 0c.801-.04 1.472.519 1.472 1.226L23 23.865c0 .15-.113.284-.278.326-.231.06-.466-.082-.485-.294l-2.052-22.55zM11.747 10.067c-.245-.668.178-1.407.933-1.63.765-.225 1.56.169 1.743.864l5.25 20.079c.04.15-.038.31-.19.389-.21.108-.47.022-.543-.179l-7.193-19.523zM6.607 20.205c-.395-.615-.148-1.43.541-1.792.7-.368 1.568-.118 1.893.545l7.482 15.289a.35.35 0 01-.116.433c-.183.138-.448.098-.565-.084L6.607 20.205zM1.845 30a1.261 1.261 0 01-.012-1.89c.575-.527 1.499-.478 2.005.107l9.72 11.214a.358.358 0 01.006.465.404.404 0 01-.576.045L1.845 30zM.911 41.391c-.758-.288-1.12-1.1-.787-1.77.34-.685 1.257-.928 1.975-.523l10.534 5.935c.17.096.248.285.189.455-.072.205-.318.307-.54.223L.912 41.39zM6.668 52.357c-.815.129-1.57-.388-1.63-1.115-.063-.75.64-1.384 1.491-1.344l8.293.39c.208.01.378.145.41.327.037.212-.126.413-.366.45l-8.198 1.292zM17.847 58.394c-.347.616-1.193.795-1.807.381-.64-.43-.723-1.295-.174-1.8l3.793-3.493a.44.44 0 01.518-.048c.192.114.26.347.157.532l-2.487 4.428z' fill='%23fff'/%3E%3C/svg%3E");
        }

        >a {
          margin: 40px 40px 0 0;
          display: inline-block;
          color: #fff;

          &:hover {
            color: $lightblue;
          }

          &:last-child {
            margin-right: 0;
          }
        }
      }
    }

  }

  [class^=col] {
    .bottomMenu {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;

      >div {
        >div {
          color: $lightblue;
          text-transform: uppercase;
        }
      }

      ul {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
          margin-top: 11px;

          a {
            color: #fff;
            white-space: nowrap;

            text &:hover {
              color: $lightblue;
            }

            &:hover {
              color: $lightblue;
            }
          }

          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }

    .bottomContact {
      display: grid;
      grid-template-columns: 50% 50%;
      grid-template-rows: 1fr 1fr;
      grid-row-gap: 55px;
      grid-column-gap: 55px;
      .tel {
        margin-right: 40px;

        .clink {
          background-image: url("data:image/svg+xml,%3Csvg width='20' height='23' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11.134 20.17c4.274 3.008 7.807.394 7.866-2.738.024-1.296-3.749-3.472-4.571-2.82-1.032.816-1.802 2.403-3.424 1.368-1.623-1.034-5.568-4.293-5.407-6.359.09-1.156 1.881-1.035 1.745-3.087 0 0-.446-4.361-2.076-4.888C2.348.703 1.175 4.477 1.01 7.528c-.247 4.558 4.566 9.037 7.763 10.947' stroke='%237DE2F4' stroke-width='1.2' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
          &:hover {
            color: #fff;
            background-image: url("data:image/svg+xml,%3Csvg width='20' height='23' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11.134 20.17c4.274 3.008 7.807.394 7.866-2.738.024-1.296-3.749-3.472-4.571-2.82-1.032.816-1.802 2.403-3.424 1.368-1.623-1.034-5.568-4.293-5.407-6.359.09-1.156 1.881-1.035 1.745-3.087 0 0-.446-4.361-2.076-4.888C2.348.703 1.175 4.477 1.01 7.528c-.247 4.558 4.566 9.037 7.763 10.947' stroke='%23FFF' stroke-width='1.2' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
          }
        }
      }

      .mail {
        .clink {
          background-image: url("data:image/svg+xml,%3Csvg width='23' height='18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2 2.499l7.842 7.19c1.23 1.086 3.196 1.08 4.415-.017L22 2.549M21 16.499l-6-8M2 16.499l6-8' stroke='%237DE2F4' stroke-width='1.2' stroke-miterlimit='10' stroke-linecap='round'/%3E%3Cpath d='M5.555 16.499h-3.74c-.45 0-.815-.35-.815-.782V2.28c0-.432.365-.782.815-.782h19.37c.45 0 .815.35.815.782v13.436c0 .432-.365.782-.815.782H9.677' stroke='%237DE2F4' stroke-width='1.2' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
          &:hover {
            color: #fff;
            background-image: url("data:image/svg+xml,%3Csvg width='23' height='18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2 2.499l7.842 7.19c1.23 1.086 3.196 1.08 4.415-.017L22 2.549M21 16.499l-6-8M2 16.499l6-8' stroke='%23FFF' stroke-width='1.2' stroke-miterlimit='10' stroke-linecap='round'/%3E%3Cpath d='M5.555 16.499h-3.74c-.45 0-.815-.35-.815-.782V2.28c0-.432.365-.782.815-.782h19.37c.45 0 .815.35.815.782v13.436c0 .432-.365.782-.815.782H9.677' stroke='%23FFF' stroke-width='1.2' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
          }        
        }
      }

      .clink {
        color: $lightblue;
        display: block;
        text-transform: uppercase;
        margin-bottom: 16px;
        padding-left: 35px;
        background-repeat: no-repeat;
        background-position: left center;
      }

      .social {
        >div {
          &:first-child {
            margin-bottom: 8px;
            display: flex;
            justify-content: space-between;
          }
        }

        a {
          &:hover {
            path {
              fill: #fff;
            }
          }

        }
      }

      .appstore {
        >a {
          &:first-child {
            margin-right: 13px;
          }
        }

        >div {
          margin-top: 10px;
        }
      }      
    }
  }




}

section {
  overflow: hidden;
}

.breadcrumbs {
  margin-top: 30px;
  a {
    font-size: 13px;
    line-height: 146%;
    color: $aquagrey;
    &:last-child {
      color: $black;
      &::after {
        display: none;
      }
    }
    &::after {
      content: '/';
      padding-left: 5px;
    }
  }
}
.top_title {
  font-size: 47px;
  line-height: 110%;
  color: $black;
  margin-top: 60px;
  margin-bottom: 120px;
  span {
    color: $blue;
  }
}
.block_title {
  font-size: 33px;
  line-height: 115%;
  color: $black;
  span {
    color: $blue;
  }
}
.link {
  @include arrow_link;
}
.top_image {
  min-height: 650px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top;
}
.prices {
  margin-top: 100px;
  margin-bottom: 210px;
  flex-wrap: nowrap;

  >[class^=col] {
    min-width: 330px;
    >div {
      border-top: 2px solid $aquagrey;
      border-bottom: 2px solid $aquagrey;
      min-height: 100%;
      padding: 40px;
      color: $black;
      position: relative;

      .title {
        font-weight: bold;
        font-size: 23px;
        line-height: 135%;
        letter-spacing: -0.01em;
        text-transform: uppercase;
        margin-bottom: 40px;
        position: relative;

        >span {
          position: absolute;
          right: 0;
        }
      }

      .text {
        @include bodytext_19;
        letter-spacing: -.3px;
        margin-bottom: 80px;

        >span {
          display: block;
          margin: 40px 0;
          font-weight: normal;
          font-size: 17px;
          line-height: 135%;
          letter-spacing: -0.01em;
        }

        ul {
          list-style: none;
          margin: 0;
          padding: 0;

          li {
            font-weight: normal;
            font-size: 17px;
            line-height: 135%;
            letter-spacing: -0.01em;
            margin-bottom: 20px;
            @include blue_li;
          }
        }
      }

      span {
        color: $blue;
      }

      &:hover {
        border-top: 2px solid $darkblue;
        border-bottom: 2px solid $darkblue;
        background-color: $darkblue;
        color: #fff;

        .title,
        span {
          color: $lightblue;
        }

        .text {
          ul {
            li {
              @include blue_li_hover;
            }
          }
        }

        .btn {
          color: $darkblue;
          background-color: $lightblue;
        }
      }

      .btn {
        width: calc(100% - 80px);
        text-align: center;
        padding: 14px 0;
        position: absolute;
        bottom: 40px;
      }
    }
  }
}
.blue_li {
  @include blue_li;
}
.custom-checkbox {
  margin-top: 15px;
  padding-left: 30px;
  .custom-control-input {
    &:focus {
      border: none;
      box-shadow: none;
      ~.custom-control-label {
        &::before {
          box-shadow: none;
        }            
      }              
    }
    &:checked {
      ~.custom-control-label {
        &::after {
          background-image: url("data:image/svg+xml,%3Csvg width='13' height='12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.213 7.314l2.836 3.631c.092.086.226.066.297-.044l6.865-9.9' stroke='%23fff' stroke-width='1.9' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");            
        }            
      }       
    }
  }
  .custom-control-label {
    line-height: 28px;
    &::after {
      width: 20px;
      height: 20px;    
      left: -30px;       
    }
    &::before {
      border-radius: 1px;
      width: 20px;
      height: 20px;
      border: 1.5px solid #004FCB;
      left: -30px;     
    }
  }
  &:focus {
    .custom-control-label {
      &::before {
        box-shadow: none;
      }            
    }          
  }
}
.accordion {
  .card {
    border: none;
    position: relative;

    .card-header {
      padding: 0;
      background: none;
      border: none;

      .btn {
        padding: 0;
        font-size: 19px;
        line-height: 135%;
        color: $blue;
        border: none;
        padding: 40px 0 20px;
        transition: padding-bottom .2s;
        display: flex;
        align-items: center;
        height: 105px;
        padding-right: 100px;        

        &:hover {
          text-decoration: none;
        }

        &.collapsed {
          padding-bottom: 40px;
          transition: padding-bottom .2s;
          color: $black;

          &::after {
            background-color: #9094A1;
          }

          &::before, .cross {
            transform: rotate(0);
            transition: transform .2s linear;
          }
        }

        &::before, .cross {
          content: '';
          position: absolute;
          top: 40px;
          right: 0;
          width: 24px;
          height: 24px;
          @include black_cross;
          transform: rotate(45deg);
          transition: transform .2s linear;
        }

        &::after {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 1px;
          background-color: $blue;
        }
      }

    }

    .card-body {
      padding: 0 0 20px 0;
      color: $black;
    }

    &:first-child {
      .card-header {
        border-top: none;
        padding-top: 0;
      }
    }
  }
}

#see_also {
  height: auto;
  overflow: hidden;
  margin-bottom: 180px;
  .slider__scroll{
    transition: .3s;
  }
  .slider {
    margin-top: 80px;
    flex-wrap: nowrap;
    a {
      display: block;
      position: relative;
      &:hover {
        span {
          color: $lightblue;
          background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17.509 19.219l1.778-13.538c.143-.572-.396-1.111-.968-.968L4.781 6.491M14.493 9.507l-9.564 9.564' stroke='%237DE2F4' stroke-width='1.6' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");  
        }
        &::before {
          content: '';
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
          background-color: rgba(0, 0, 0, .3);
        }
      }
    }
    span {
      position: absolute;
      bottom: 45px;
      left: 40px;
      color: #fff;
      background-image: none;
      background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17.509 19.219l1.778-13.538c.143-.572-.396-1.111-.968-.968L4.781 6.491M14.493 9.507l-9.564 9.564' stroke='%23FFF' stroke-width='1.6' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");  
    }
  }
}
.dlink {
  padding-left: 30px;
  margin: 40px 0 50px;
  display: block;
  background-repeat: no-repeat;
  background-position: left center;
  background-image: url("data:image/svg+xml,%3Csvg width='19' height='19' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.519 12.787H3.992a2.937 2.937 0 01-2.078-.855 2.908 2.908 0 01.722-4.652.072.072 0 00.04-.071 2.954 2.954 0 01-.011-.354c.031-1.168.705-2.243 1.78-2.717a3.211 3.211 0 012.426-.096.073.073 0 00.097-.048A4.318 4.318 0 0111.104.952c2.387 0 4.323 1.922 4.323 4.293 0 .112-.006.225-.015.336a.073.073 0 00.051.075 3.662 3.662 0 012.535 3.475c0 .644-.167 1.25-.464 1.775a3.68 3.68 0 01-3.218 1.881h-2.994M9.526 14.695v-4.114M6.89 14.604l2.436 3.294a.232.232 0 00.401 0l2.436-3.294' stroke='%23222633' stroke-width='1.2' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
  color: $black;

  span {
    color: $blue;
    font-size: 11px;
    line-height: 135%;
    display: block;
  }

  &:hover {
    color: $blue;
  }

}

#archiv {
  overflow: hidden;
  margin-bottom: 180px;
  .pfilter {
    margin-bottom: 85px;
    a {
      &::after {
        padding: 0 30px 0 30px;
      }
    }
  }
  .slider {
    position: relative;
    > div {
      display: flex;
      flex-wrap: nowrap;
      position: relative;
      // left: calc(-100% - 40px);
      .img {
        margin: 0 20px;
        position: relative;
        left: -20px;
        background: url(../img/arch.jpg) no-repeat top center;
        background-size: cover;
        min-height: 650px;
        min-width: 100%;
      }
    }
    #slidewrapper{
      transition: .6s;
    }
    .arrow {
      position: absolute;
      // top: calc(50% - 40px);
      width: 80px;
      height: 81px;
      background-repeat: no-repeat;
      cursor: pointer;
      background-image: url("data:image/svg+xml,%3Csvg width='80' height='81' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='-.5' y='.5' width='79' height='79' rx='39.5' transform='matrix(-1 0 0 1 79 .5)' stroke='%23fff'/%3E%3Cpath d='M43.363 57.067L25.422 41.761c-.838-.559-.838-1.962 0-2.52l17.941-15.307M35.522 40.501h22.406' stroke='%23fff' stroke-width='1.9' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
      opacity: 1;
      pointer-events: none;
    }
    .archiv_btn{
      transition: opacity .3s;
      &.active{
        transform: rotateZ(0deg);
      }
    }
    .slider_viewport{
      cursor: none;
    }
    .slider_viewport:hover .archiv_btn{
        opacity: 1;
    }
  }
}

#row_slider{
  position: relative;
  cursor: none;
  z-index: 2;
  .archiv__left,
  .archiv__right{
    position: absolute;
    width: 50%;
    height: 100%;
    top: 0;
    z-index: 3;
  }
  .archiv__left{
    left: 0;
  }
  .archiv__right{
    right: 0;
  }
  .row_wrapp{
    width: 100%;
    position: relative;
    flex-shrink: 0;
    z-index: 1;
  }
  .archiv_btn{
    position: absolute;
    width: 80px;
    height: 81px;
    background-repeat: no-repeat;
    background-image: url("data:image/svg+xml,%3Csvg width='80' height='81' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='-.5' y='.5' width='79' height='79' rx='39.5' transform='matrix(-1 0 0 1 79 .5)' stroke='%23fff'/%3E%3Cpath d='M43.363 57.067L25.422 41.761c-.838-.559-.838-1.962 0-2.52l17.941-15.307M35.522 40.501h22.406' stroke='%23fff' stroke-width='1.9' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
    pointer-events: none;
    opacity: 0;
    z-index: 2;
    transform: rotateZ(180deg);
    &.active{
      transform: rotateZ(0deg);
    }
  }
  &:hover .archiv_btn{
    opacity: 1;
  }
}

.dsgallery {
  flex-wrap: nowrap;
  img {
    height: 450px;
  }
}

.padding-menu {
  padding-top: 138px;
}