#news {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;


  .top_title {
    margin: 60px 0 110px;
  }

  #articles {
    color: $black;
    padding-top: 70px;
    overflow: visible;

    .p-qty {
      position: absolute;
      top: -70px;
      right: 40px;

      ul {
        list-style: none;
        padding: 0;
        margin: 0;
        display: flex;

        li {
          margin-left: 20px;
          font-size: 19px;
          line-height: 135%;

          a {
            color: #9094A1;
          }

          &.active,
          &:hover {
            a {
              color: $blue;
            }
          }
        }
      }
    }

    .filter-title {
      display: none;
      font-size: 15px;
      line-height: 146%;
      background: #F6F6F6;
      border-radius: 2px;
      padding: 15px 51px 15px 15px;
      margin-bottom: 0;
      position: relative;
      color: #9094A1;

      &::before {
        content: '';
        position: absolute;
        width: 51px;
        height: 100%;
        top: 0;
        right: 0;
        background-color: $blue;
      }

      &::after {
        content: '';
        position: absolute;
        width: 51px;
        height: 100%;
        top: 0;
        right: 0;
        background-position: center;
        background-size: 26px;
        @include white_cross;
        transition: transform .4s;
      }

      &.open {
        &::after {
          transform: rotate(45deg);
          transition: transform .5s;
        }
      }
    }

    .filter {
      background-color: #F6F6F6;
      position: relative;
      top: 0;
      transition: top .4s;

      ul {
        padding: 40px 20px 0;
        list-style: none;
        margin: 0;

        li {
          font-size: 19px;
          line-height: 135%;
          margin-bottom: 20px;

          &.active {
            a {
              color: $blue;
            }
          }

          &:last-child {
            margin-bottom: 0;
          }
        }
      }

      >div {
        padding: 80px 20px 40px;

        span {
          color: $blue;
        }

        form {
          margin-top: 20px;
          position: relative;
        }

        input {
          border: 1px solid #E9E8E8;
          box-sizing: border-box;
          border-radius: 2px;
          height: 60px;
          width: 100%;
          padding-left: 20px;
          background-color: #f6f6f6;

          &[type=submit] {
            position: absolute;
            width: 65px;
            right: 0;
            border: 1px solid $blue;
            background-color: $blue;
            @include arrow_link_white;
            background-position: center;
            background-size: 28px;
          }
        }
      }
    }

    .articles {
      >a {
        display: block;
        padding: 60px 150px 60px 20px;
        position: relative;
        display: grid;
        grid-template-columns: 270px auto;
        align-items: center;
        @include blue_arrow_bg;
        background-position: right 10px center;
        background-size: 50px;
        border-bottom: 1px solid rgba(144, 148, 161, .4);

        &:first-child {
          border-top: 1px solid rgba(144, 148, 161, .4);
        }

        &:hover {
          background-color: $blue;
          color: #fff;
          @include white_arrow_bg;
          .float_img {
            display: block;
          }
        }

        .date {
          font-size: 41px;
          line-height: 85%;

          span {
            font-size: 19px;
            line-height: 148%;
            text-transform: uppercase;
          }
        }

        .title {
          font-size: 23px;
          line-height: 136%;
        }

        .float_img {
          display: none;
          position: absolute;
          right: -70px;
          bottom: calc( 100% - 40px);
        }
      }

      .pagination {
        margin-top: 85px;
        display: flex;
        align-items: center;

        a {
          display: block;
          font-size: 19px;
          line-height: 136%;
          color: #9094A1;
          margin: 0 12px;

          &.active {
            color: $blue;
            font-size: 23px;
            line-height: 136%;
          }

          &:hover {
            color: $blue;
          }

          &.arrow {
            margin: 0;
            width: 22px;
            height: 36px;
            background-position: center;

            &.prev {
              margin-right: 10px;
              @include arrow_left('9094A1');

              &:hover {
                @include arrow_left('004FCB');
              }
            }

            &.next {
              margin-left: 10px;
              @include arrow_right('9094A1');

              &:hover {
                @include arrow_right('004FCB');
              }
            }
          }
        }
      }
    }
  }
}
#success_project {
    margin: 180px 0;

    .block_title {
      margin-bottom: 120px;
      font-size: 27px;
      line-height: 130%;
    }

    .articles {
      > div {
        margin-bottom: 80px;
        display: grid;
        grid-template-columns: auto 1fr;
        grid-template-rows: auto auto 1fr auto;
        row-gap: 40px;
        column-gap: 40px;

        img {
          grid-row: 1 / span 4;
          max-width: 100%;
        }

        > div {
          &:last-child {
            align-self: flex-end;
          }
        }

        &:last-child {
          margin-bottom: 0;
        }
      }

      .date {
        
        color: $aquagrey;
        @media(min-width:992px){
          text-transform: uppercase;
        }
      }

      .title {
        font-size: 23px;
        line-height: 136%;
        color: $blue;
      }

      .intro {
        color: $black;
      }
    }
  }