#security-measures {

  @include media-breakpoint-down(md) {
    .block_title {
      font-size: 21px;
    }

    #top_content {

      .img-fluid {
        margin-top: 0;
        margin-bottom: 100px;
      }
    }

    #security-measures-on-forum {
      margin-bottom: 140px;

      .description {
        margin-top: 40px;

        p {
          font-size: 17px;
        }
      }
    }

    #key-commitments {
      padding: 100px 0;
      font-size: 15px;

      .block_title {
        padding-bottom: 20px;
        font-size: 21px;
      }

      .justify-content-between {

        [class^=col] {
          margin-top: 40px;

          .title {
            font-size: 19px;
            line-height: 135%;
            letter-spacing: -.01em;
            margin-bottom: 10px;
          }
        }
      }
    }

    #faq {
      padding: 140px 0;

      .btn-wrapper {
        display: none;
      }

      .tab-content{
        margin-top: 30px;
      }

      .btn-main {
        min-width: 50%;
        margin-top: 60px;
        font-size: 15px;
      }

      .accordion {
        min-height: unset;
        margin-top: 0;
      }
    }
  }

  @include media-breakpoint-down(xs) {

    #top_content {

      .top_title {
        font-size: 21px;
        line-height: 115%;
        margin-bottom: 30px;
        max-width: 270px;

        br {
          display: none;
        }
      }

      .img-fluid {
        margin-top: 30px;
        min-height: 300px;
        object-fit: cover;
        margin-bottom: 80px;
      }
    }

    #security-measures-on-forum {
      margin-bottom: 80px;

      .description {
        margin-top: 40px;

        p {
          font-size: 15px;
        }
      }
    }

    #key-commitments {
      padding: 80px 0;

      .block_title {
        font-size: 21px;
        margin-bottom: 20px;
        padding-bottom: 0;

        br {
          display: none;
        }
      }

      .justify-content-between {

        [class^=col] {
          margin-top: 40px;

          .title {
            font-size: 17px;
            line-height: 136%;
          }
        }
      }
    }

    #faq {
      padding: 80px 0;
      font-size: 15px;

      .btn-wrapper{
        display: none;
      }

      .accordion {
        margin-bottom: 0;
      }

      .d-none {
        display: block!important;

        .btn-main {
          width: 100%;
          font-size: 13px;
          font-weight: 400;
        }
      }
    }
  }
}