
  @include media-breakpoint-down(lg) {
    #success_project {
      .articles {
        >div {
          grid-template-columns: 3fr 1fr;
        }

        .date {
          font-size: 15px;
          line-height: 146%;
        }

        .title {
          font-size: 17px;
          line-height: 146%;
        }

        .intro {
          font-size: 15px;
          line-height: 146%;
        }

        .link {
          font-size: 15px;
          line-height: 148%;
        }
      }
    }
  }
#news {
  @include media-breakpoint-down(md) {

    .top_title {
      margin: 60px 0;
    }

    .block_title {
      br {
        display: inline;
      }
    }

    #articles {

      padding-top: 0;

      .filter-title {
        display: block;
      }

      .filter {
        margin-bottom: 40px;
        overflow: hidden;
        max-height: 0;
        transition: max-height .4s;
        position: static;

        font-size: 17px;
        line-height: 146%;

        &.open {
          max-height: 800px;
          transition: max-height .5s;
        }

        ul {
          padding-top: 20px;

          li {
            font-size: 17px;
            line-height: 146%;
          }
        }

        >div {
          padding-top: 60px;

          input {
            height: 52px;

            &[type=submit] {
              width: 52px;
            }
          }
        }
      }

      .p-qty {
        display: none;
      }

      .articles {
        >a {
          grid-template-columns: 1fr;
          grid-template-rows: auto, 1fr;
          padding: 40px 150px 40px 20px;
          background-size: 24px;
          background-position: right 10px top 50px;

          &:hover {
            .float_img {
              display: none;
            }
          }

          .date {
            font-size: 27px;
            line-height: 135%;
            margin-bottom: 25px;

            span {
              font-size: 15px;
              line-height: 148%;
            }
          }

          .title {
            font-size: 19px;
            line-height: 135%;
            letter-spacing: -0.01em;
          }
        }

        .pagination {
          margin-top: 60px;
          justify-content: center;

          a {
            font-size: 17px;
            line-height: 146%;

            &.active {
              font-size: 21px;
              line-height: 135%;
            }
          }
        }
      }
    }


  }

  @include media-breakpoint-down(sm) {
    #articles {
      .articles {
        >a {
          padding-right: 20px;
          background-position: right 10px top 40px;

          .date {
            font-size: 26px;
            line-height: 85%;

            span {
              font-size: 15px;
              line-height: 88%;
              text-transform: uppercase;
            }
          }

          .title {
            font-size: 19px;
            line-height: 135%;
          }
        }


        .pagination {
          a {
            font-size: 19px;
            line-height: 136%;

            &.active {
              font-size: 23px;
              line-height: 136%;
            }
          }
        }
      }
    }
  }

}
@include media-breakpoint-down(md) {
  #success_project {
      margin: 140px 0;

      .block_title {
        margin-bottom: 60px;
        font-size: 21px;
        line-height: 135%;
      }

      .articles {
        display: block;
        >div {
          grid-template-columns: 1fr;
          grid-template-rows: 1fr repeat(4, auto);
          row-gap: 20px;
          margin-bottom: 60px;
          

          img {
            grid-row: 1;
          }

          &:last-child {
            margin-bottom: 0;
          }
        }

        .title {
          // text-transform: uppercase;
        }

        .date {
          margin: 20px 0 0;
        }

        .intro {
          margin-bottom: 20px;
        }
      }

    }
}
@include media-breakpoint-down(sm) {
#success_project {

    .articles {
      display: flex;
      overflow: hidden;
      overflow-x: auto;
      scrollbar-width: none;

      &::-webkit-scrollbar {
        display: none;
      }

      >div {
        
        margin-bottom: 0px;
        flex-shrink: 0;
        width: 320px;

        &+div {
          margin-left: 30px;
        }
      }
    }
  }
}
@include media-breakpoint-down(xs) {
    #success_project {
      margin: 100px 0;

      .block_title {
        margin-bottom: 80px;
      }

      .articles {
        
        >div {
          // margin-bottom: 80px;
         
          width: 320px;
         

          img {
            height: 300px;
            object-fit: cover;
          }
          &:last-child {
            margin-bottom: 0;
          }
        }

        .date {
          margin: 5px 0 0;
        }

        .intro {
          margin-bottom: 20px;
        }

        .link {
          font-size: 13px;
          line-height: 148%;
        }
      }

    }
  }