#exhibition {
  @media(max-width: 1699px){
    .invitation{
      min-height: 600px;
      &-img{height: 600px;}
      &-text__inner{height: 600px;}
    }
  }  
  @include media-breakpoint-down(lg) {
    #promote_product {
      padding: 100px 0;

      .title {
        font-size: 21px;
        line-height: 135%;
        margin-bottom: 40px;
      }

      .text {
        font-size: 17px;
        line-height: 146%;
      }
    }

    #market_leaders {
      margin: 140px 0;
      font-size: 21px;
      line-height: 135%;




      [class^=col] {
        &:last-child {
          display: flex;
          margin-top: 40px;

          >div {
            margin-left: 0;
            img {
              margin-left: 5px;
            }

            &:first-child {
              img {
                margin-left: 0;
              }
            }
          }
        }
      }
    }

    #exhibition_data {
      min-height: 450px;
      padding-bottom: 130px;

      [class^=col] {
        z-index: 2;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: center;
        min-height: 450px;

        >div {
          font-size: 82px;
          line-height: 70%;
          max-width: 320px;

          span {
            font-size: 41px;
            line-height: 70%;
          }

          >div {
            font-size: 17px;
            line-height: 146%;
            margin-top: 20px;
          }

          &:last-child {
            position: absolute;
            bottom: -130px
          }
        }
      }

      position: relative;

      &::before {
        content: '';
        position: absolute;
        width: calc(50% + 90px);
        height: calc(100% - 130px);
        right: calc(50% - 90px);
        top: 0;
        background-image: url(../img/exhibition_data_bg.jpg);
        background-repeat: no-repeat;
        background-position: right center;
        background-size: cover;
      }

      &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: calc(100% - 130px);
        left: calc(50% + 40px);
        top: 0;
        background-color: #F6F6F6;
        transform: skewX(-12.5deg);
        transform-origin: left top;
        z-index: 1;
      }
    }

    #what_participation {
      margin: 140px 0;

      .block_title {
        font-size: 21px;
        line-height: 135%;
        margin-bottom: 20px;
      }

      ul {

        li {
          display: grid;
          grid-template-columns: 300px auto;
          border-bottom: 1px solid rgba(144, 148, 161, .6);
          padding: 40px 0;

          div {
            &:first-child {
              font-size: 17px;
              line-height: 146%;
            }

            &:last-child {
              font-size: 15px;
            }
          }
        }
      }
    }

    #exhibition_tnf {
      padding: 215px 0;

      &::before {
        top: calc(50% - 450px);
        background-size: cover;
      }

      .title {
        font-size: 64px;
        line-height: 115%;
        margin-bottom: 60px;
      }

      .text {
        font-size: 17px;
        line-height: 146%;
      }
    }

    #optimal_variant {
      margin: 140px 0;

      .block_title {
        font-size: 21px;
        line-height: 135%;
      }

      [class^=container] {
        &:nth-child(2) {
          .row {
            [class^=col] {
              padding-bottom: 140px;
              padding-left: 15px;
              padding-right: 15px;
            }

            &::before {
              bottom: 6.5px;
            }
          }
        }
      }

      .row {
        .title {
          font-size: 17px;
          line-height: 146%;
        }

        .text {
          font-size: 17px;
          line-height: 146%;
          // min-height: 72px;
        }

        .square {
          font-size: 15px;
        }

        .price {
          font-size: 17px;
          line-height: 146%;
        }

        .btn {
          width: calc(100% - 40px);
          color: $darkblue;
          position: absolute;
          bottom: 40px;
          display: none;
          font-size: 17px;
          line-height: 150%;
        }
      }
    }

    #stands_booked {
      margin: 0 0 140px;

      .block_title {
        font-size: 21px;
        line-height: 135%;
      }
    }

    #register_participation {
      padding: 100px 0;

      .block_title {
        font-size: 21px;
        line-height: 135%;
      }

      .row {

        &:last-child {
          margin-top: 60px;
          flex-wrap: nowrap;
          margin-left: -15px;

          [class^=col] {
            padding-right: 15px;
            padding-left: 15px;

            >div {
              padding-top: 40px;
              padding-bottom: 130px;
            }
          }
        }
      }

      .title {
        font-size: 17px;
        line-height: 146%;
        margin-top: 40px;
      }

      .text {
        font-size: 15px;
        line-height: 146%;
      }

      .btn {
        font-size: 15px;
      }
    }

    #archiv {
      margin: 140px 0;

      .row {
        &:last-child {
          margin-top: 80px;
        }
      }

      .block_title {
        font-size: 21px;
        line-height: 135%;
        white-space: nowrap;
      }

      .link {
        font-size: 15px;
      }

      .pfilter {
        margin-top: 40px;
      }
    }

		.invitation{
			min-height: 480px;
			&-img{height: 480px;}
			&-text__inner{height: 480px;padding-bottom: 20px;}
		}
		.invitation-img--desktop{
			display: none;
		}
		.invitation-img--mobile{
			display: block;
			width: 48%;

		}
		.invitation-benefits{
			padding-top: 45px;
			padding-bottom: 25px;
			.invitation-text__inner,
			.invitation-img{height: 300px;}
			.invitation-block{min-height: 300px;}
			.invitation-block + .invitation-block{margin-top: 50px;}
		}    
  }

  @include media-breakpoint-down(md) {
    #optimal_variant {
      .slider__scroll {
        width: calc(50% + 50vw);
        overflow-x: auto;
        overflow-y: hidden;
      }
      .slider__scroll::-webkit-scrollbar{width: 0;}
      .block_title {

        br {
          display: inline;
        }
      }

      [class^=container] {
        &:nth-child(2) {
          .row {
            margin-left: 0;
            [class^=col] {
              padding-bottom: 0;

              &::after {
                display: none;
              }
            }

            &::before {
              display: none;
            }
          }
        }
      }
    }

    #stands_booked {
      .block_title {
        margin-bottom: 60px;

        br {
          display: inline;
        }
      }

      .row {
        &:first-child {
          margin-bottom: 60px;

          .btn {
            font-size: 15px;
            min-width: calc(50% - 17px);

            &.bis {
              border: 1px solid #ececec;
            }

            &:first-child {
              margin-right: 30px;
            }
          }
        }
      }
    }

    #register_participation {

      .block_title {
        br {
          display: inline;
        }
      }

      .row {
        &:last-child {
          [class^=col] {
            > div {
              border-left: 2px solid $darkblue;
              background-color: $darkblue;

              .number,
              .title,
              .text {
                color: #fff;

                span {
                  color: $lightblue;
                }
              }

              .btn {
                display: block;
              }              
            }
          }
        }
      }
    }

    #archiv {
      .slider{
        >div {
          .img{
            min-height: 440px;
          }
        }
      }
      .block_title {
        br {
          display: inline;
        }
      }

      .row {
        &:last-child {
          margin-top: 40px;
        }
      }

      .pfilter {
        justify-content: flex-start;
      }
    }

		.invitation{
			min-height: 450px;
			&-img{height: 450px;}
			&-text__inner{height: 450px;}

      .title-decore {
        font-size: 82px;
        line-height: 70%;
        span {
          font-size: 41px;
          line-height: 70%;
        }        
      }

      .medium-text {
        font-size: 17px;
        line-height: 146%;
      }      
		}
		.invitation-text__inner .title-decore{
			font-size: 17px;
			max-width: 275px;
			&:before{
				bottom: -25px;
			}
			}
		.invitation-benefits{
			padding-top: 45px;
			padding-bottom: 25px;
			.invitation-text__inner,
			.invitation-img{height: 185px;}
			.invitation-block{min-height: 185px;}
		}    

  }

  @include media-breakpoint-down(sm) {
    #market_leaders {

      [class^=col] {
        &:last-child {
          display: grid;
          grid-template-columns: repeat(2, calc(50% - 7px));
          column-gap: 14px;
          row-gap: 14px;

          img {
            min-width: 100%;
          }
        }
      }
    }

    #exhibition_data {
      [class^=col] {


        >div {
          &:last-child {
            padding-left: 20px;
          }
        }
      }
    }

    #what_participation {
      margin: 100px 0;

      .block_title {
        margin-bottom: 40px;
        br {
          display: inline;
        }
      }

      ul {

        li {
          grid-template-columns: 1fr;
          grid-template-rows: repeat(2, auto);
          padding: 10px 0;
          div {
            margin: 10px 0;
          }
        }
      }
    }
    #exhibition_tnf {
      padding: 180px 0;

      .title {
        font-size: 47px;
        line-height: 120%;
        margin-bottom: 40px;
        span {
          white-space: nowrap;
        }
      }
    }  
    #optimal_variant {
      margin: 100px 0;
      .block_title {

        br {
          display: inline;
        }
      }

      [class^=container] {
        &:nth-child(2) {
          .row {
            margin-top: 80px;;
            [class^=col] {
              padding-right: 5px;
              padding-left: 5px;
              min-width: 330px;
              > div {
                padding: 40px 10px 110px;
              }
            }
          }
        }
      }
      .row {
        .text {
          font-size: 15px;
          min-height: auto;
          height: 42px;
        }
        .square {
          font-size: 17px;
          margin-top: 20px;
        }
        .btn {
          width: calc(100% - 20px);
        }
        .dlink {
          margin-top: 60px;
        }
      }
    }   
    #stands_booked {
      margin: 0 0 100px;

      .row {
        &:last-child {
          [class^=col] {
            // left: -390px;
            left: 0;
            .stands_booked-map{
              width: calc(50% + 50vw);
              overflow-x: auto;
              overflow-y: hidden;
            }
            .stands_booked-map::-webkit-scrollbar{width: 0;}
          }
          svg {
            max-width: none;
            min-width: 1188px;
          }
        }
      }
    }   
    #register_participation {
      padding: 80px 0;

      .block_title {
        margin-bottom: 20px;
      }

      .row {

        &:last-child {
          margin-top: 60px;
          flex-wrap: nowrap;
          margin-left: -5px;

          [class^=col] {
            padding-right: 5px;
            padding-left: 5px;
            min-width: 290px;

            >div {
              padding: 20px 20px 110px;
            }
          }
        }
      }

      .title {
        font-size: 19px;
        line-height: 135%;
        margin-top: 80px;
      }
      .text {
        max-height: 42px;
      }
      .btn {
        font-size: 13px;
        bottom: 30px;
        min-width: calc(100% - 50px);
      }
    }    
    #archiv {
      margin: 100px 0;
      .slider{
        >div {
          .img{
            min-height: 300px;
          }
        }
      }
      .row {
        &:last-child {
        }
      }

      .pfilter {
        a {
          &::after {
            padding: 0 10px;
          }
        }
      }

      .link {
        font-size: 13px;
      }    
    }    
    .invitation{margin-top: 100px;}
		.invitation-text__inner{
			margin-top: 393px;
			height: auto;
			padding-bottom: 60px;
			max-width: 320px;
			margin-left: auto;
			margin-right: auto;
		}
		.invitation-img--mobile{
			height: 350px;
			width: 100%;

		}
		.invitation-text__inner .title-decore{font-size: 84px; line-height: 70%; margin-bottom: 20px;}
    .invitation-benefits{
      padding-top: 0;
      padding-bottom: 40px;
      .invitation-img{height: 350px;}
       .invitation-text__inner{height: auto;}
       .invitation-block +.invitation-block{
        margin-top: 0px;
      }
    }
    .invitation-benefits .invitation-text{
      max-width: 320px;
      margin-left: auto;
      margin-right: auto;
    }                
  }

  @include media-breakpoint-down(xs) {
    #promote_product {
      .text {
        font-size: 15px;
      }
    }

    #market_leaders {
      margin: 100px 0;

      [class^=col] {
        &:last-child {
          display: grid;
          grid-template-columns: repeat(2, calc(50% - 7px));
          column-gap: 14px;
          row-gap: 14px;
        }
      }
    }

    #exhibition_data {
      padding-bottom: 80px;
      min-height: 350px;

      [class^=col] {
        min-height: 350px;

        >div {
          font-size: 60px;

          span {
            font-size: 30px;
          }

          >div {
            font-size: 15px;
          }

          &:last-child {
            bottom: -80px;
          }
        }
      }
    }
    #stands_booked {
      .block_title {
        margin-bottom: 80px;
      }
      [class^=col] {
        &:last-child {
          display: flex;
          flex-direction: column;
          .btn {
            font-size: 17px;
            text-transform: uppercase;
            &:first-child {
              margin: 0 0 20px;
            }
          }
        }
      }
      // .row {
      //   &:last-child {
      //     [class^=col] {
      //       left: calc(50vw - 660px);
      //     }
      //   }
      // }      
    }

    #archiv {
      .pfilter {
        a {
          white-space: nowrap;
          &::after {
            padding: 0 5px;
          }
        }
      }
    }        
  }
}

.invitation-img--desktop + .invitation-block .invitation-text{
	@media(min-width: 1280px){
		padding-bottom: 70px;
	}
}