#program {
  @include media-breakpoint-down(xl) {}

  @include media-breakpoint-down(lg) {
    #timetable {
      .title {
        display: none;

        &.filter {
          display: block;
          font-size: 15px;
          line-height: 146%;
          background: #F6F6F6;
          border-radius: 2px;
          padding: 15px 51px 15px 15px;
          margin-bottom: 0;
          position: relative;

          &::before {
            content: '';
            position: absolute;
            width: 51px;
            height: 100%;
            top: 0;
            right: 0;
            background-color: $blue;
          }

          &::after {
            content: '';
            position: absolute;
            width: 51px;
            height: 100%;
            top: 0;
            right: 0;
            background-position: center;
            background-size: 26px;
            @include white_cross;
            transition: transform .4s;
          }

          &.open {
            &::after {
              transform: rotate(45deg);
              transition: transform .5s;
            }
          }
        }

      }

      .filters {
        display: grid;
        grid-template-columns: 50% 50%;
        row-gap: 50px;
        margin-bottom: 60px;
        overflow: hidden;
        max-height: 0;
        transition: max-height .4s;

        &.open {
          max-height: 800px;
          transition: max-height .5s;
        }

        >form {
          display: flex;
          justify-content: space-between;
          grid-column: 1 / span 2;
          padding: 60px 0 0 0;

          >div {
            .pidpys {
              margin-top: 0;
            }

          }
        }

        >div {
          margin-bottom: 50px;

          &:last-child {
            grid-column: 1 / 1;
            grid-row: 2 / 2;
          }

          .dlink {
            margin: 0;
          }

          .btn {
            width: auto;
            margin-bottom: 0;
          }
        }
      }
    }

    #efformat {
      margin: 140px 0;

      .block_title {
        margin-bottom: 60px;

        br {
          display: inline;
        }
      }
      .slider {
        margin-left: -15px;

        [class^=col] {
          min-width: 239px;
          padding: 0 15px;
        }

        .title {
          font-size: 17px;
          line-height: 146%;
          margin: 20px 0 10px;
          text-transform: none;
        }

        .text {
          margin: 0 0 45px;
          font-size: 15px;
          line-height: 146%;
        }

        .link {
          left: 15px;
        }
      }
    }

    #kind_healthy {
      padding: 100px 0;
      font-size: 17px;
      line-height: 146%;

      .block_title {
        margin-bottom: 60px;

        br {
          display: inline;
        }
      }

      ul {
        margin-top: 60px;
      }

    }

    #informal {
      margin: 140px 0 0;

      .block_title {
        margin-bottom: 60px;

        br {
          display: inline;
        }
      }
      .slider {
        >div {
          // left: calc(-100% - 30px);

          .img {
            min-height: 389px;
            margin: 0 15px;
            left: -15px;
          }
        }
      }
    }

    #question_answer {
      margin: 110px 0 0;

      .block_title {
        font-size: 21px;
        line-height: 135%;
      }
    }
  }

  @include media-breakpoint-down(md) {
    #timetable {
      .container-lg {
        padding: 0;

        [class^=col] {
          &:first-child {
            margin: 0 40px;
          }
        }

        .filters {


          >div {
            .btn {
              font-size: 15px;
              line-height: 150%;
            }
          }
        }

        #accordionTimetable {
          .card {
            .card-header {
              .btn {
                padding-left: 40px;

                &::before {
                  right: 40px;
                }

                &.collapsed {
                  .program-collapse-grid {
                    .calendarm {
                      background-image: url("data:image/svg+xml,%3Csvg width='22' height='22' viewBox='0 0 22 22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M18.1618 20.9971C19.5613 20.9971 20.6957 19.796 20.6957 18.3146V7.13704C20.6957 5.65525 19.5613 4.45415 18.1618 4.45415H3.97136C2.5719 4.45415 1.4375 5.65525 1.4375 7.13704V18.3146C1.4375 19.796 2.5719 20.9971 3.97136 20.9971H13.5161' stroke='%23222633' stroke-width='1.9' stroke-miterlimit='10' stroke-linecap='round'/%3E%3Cpath d='M6.96875 1.73734V7.96509' stroke='%23222633' stroke-width='1.9' stroke-miterlimit='10' stroke-linecap='round'/%3E%3Cpath d='M15.168 1.73734V7.96509' stroke='%23222633' stroke-width='1.9' stroke-miterlimit='10' stroke-linecap='round'/%3E%3Cpath d='M1.4375 11.1541H19.8843' stroke='%23222633' stroke-width='1.9' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E%0A");
                    }
                  }
                }

                .program-collapse-grid {
                  grid-template-columns: 35px 187px auto;

                  .calendarm {
                    background-repeat: no-repeat;
                    background-position: left top;
                    background-image: url("data:image/svg+xml,%3Csvg width='22' height='22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M18.162 20.998c1.4 0 2.534-1.201 2.534-2.683V7.138c0-1.482-1.135-2.683-2.534-2.683H3.972c-1.4 0-2.534 1.2-2.534 2.683v11.177c0 1.482 1.134 2.683 2.533 2.683h9.545M6.969 1.738v6.227M15.168 1.738v6.227M1.438 11.154h18.446' stroke='%23fff' stroke-width='1.9' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
                  }

                  .calendar {
                    background: none;
                  }

                  .small-text-blue {
                    text-align: left;
                    font-size: 17px;
                    line-height: 146%;
                  }

                  .text {
                    font-size: 17px;
                    line-height: 146%;
                  }

                  >div {
                    &:nth-child(5) {
                      grid-column: 1 / span 2;
                      text-align: left;
                    }
                  }

                }
              }
            }

            .card-body {
              .program-collapse-content {
                margin-left: 262px;

                .content-title {
                  font-size: 17px;
                  line-height: 146%;
                  padding: 40px 0 20px;
                }
              }
            }

            &::after {
              width: calc(100% - 80px);
              left: 40px;
            }
          }
        }
      }
    }

    #efformat{
      .slider__scroll{
        width: calc(50% + 50vw);
        overflow-x: auto;
        overflow-y: hidden;
      }
      .slider__scroll::-webkit-scrollbar{width: 0;}
    }

    #faqAccordion {
      .card {
        .card-header {
          .btn {
            // font-size: 21px;
          }
        }
      }
    }


    #informal{
      #slidewrapper{
        width: calc(50% + 50vw);
        overflow-x: auto;
        overflow-y: hidden;
      }
    }


  }

  @include media-breakpoint-down(sm) {
    #timetable {
      margin-top: 80px;

      .container-lg {
        padding: 0;

        [class^=col-] {
          &:first-child {
            margin: 0 20px;
          }

          &:last-child {
            margin: 0 20px;
          }
        }

        .filters {
          font-size: 15px;
          line-height: 146%;
          margin-bottom: 35px;

          >form {
            >div {
              .pidpys {
                font-size: 15px;
                line-height: 148%;
              }
            }
          }
        }

        #accordionTimetable {
          margin-bottom: 0;

          .card {
            .card-header {
              .btn {
                padding: 20px 10px 30px;

                &.collapsed {
                  padding: 20px 10px;

                  .program-collapse-grid {

                    .calendarm {
                      background: none;
                    }

                    .calendar {
                      background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M19.368 22C20.822 22 22 20.753 22 19.215V7.608c0-1.538-1.178-2.785-2.632-2.785H4.631C3.178 4.823 2 6.07 2 7.608v11.607C2 20.753 3.178 22 4.631 22h9.913M7.746 2v6.468M16.26 2v6.468M2 11.78h19.157' stroke='%23004FCB' stroke-width='1.6' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
                    }
                  }
                }

                .program-collapse-grid {
                  grid-template-columns: 35px 130px auto;
                  width: 100%;
                  white-space: normal;

                  .calendarm {
                    background: none;
                  }

                  .calendar {
                    grid-column: 1;
                    grid-row: 2;
                    background-repeat: no-repeat;
                    background-position: left top;
                    height: 24px;
                    background-image: url("data:image/svg+xml,%3Csvg width='22' height='22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M18.162 20.998c1.4 0 2.534-1.201 2.534-2.683V7.138c0-1.482-1.135-2.683-2.534-2.683H3.972c-1.4 0-2.534 1.2-2.534 2.683v11.177c0 1.482 1.134 2.683 2.533 2.683h9.545M6.969 1.738v6.227M15.168 1.738v6.227M1.438 11.154h18.446' stroke='%23fff' stroke-width='1.9' stroke-miterlimit='10' stroke-linecap='round'/%3E%3C/svg%3E");
                  }

                  .small-text-blue {
                    font-size: 15px;
                    line-height: 146%;
                  }

                  .text {
                    font-size: 19px;
                    line-height: 135%;
                  }

                  .small-text {
                    display: flex;
                    justify-content: space-between;
                    grid-row: 5;
                    grid-column: 1 / span 3;

                    span {
                      font-size: 13px;
                      line-height: 146%;
                      padding-right: 0;
                    }
                  }



                  >div {

                    &:nth-child(2) {
                      text-align: right;
                      margin-right: 0;
                    }

                    &:nth-child(5) {
                      margin-right: 0;
                      grid-column: 2;
                      line-height: 24px;
                    }

                    &:nth-child(3) {
                      grid-row: 3;
                      grid-column: 1 / span 3;
                      margin-top: 30px;
                    }

                    &:nth-child(6) {
                      grid-row: 4;
                      grid-column: 1 / span 3;
                    }
                  }
                }

                &::before {
                  top: 40px;
                  right: 20px;
                }
              }
            }

            .card-body {
              padding: 0 10px;

              .program-collapse-content {
                margin-left: 0;
                font-size: 13px;

                >div {
                  &:first-child {
                    padding-top: 30px;
                  }
                }

                .organizer {
                  grid-template-columns: repeat(2, 1fr);
                }

                .content-title {
                  font-size: 15px;
                  line-height: 146%;
                }

                .moderator {
                  padding-bottom: 40px;

                  >div {
                    grid-template-columns: 100%;
                    grid-template-rows: repeat(3, auto);

                    img {
                      grid-row: 1;
                    }

                    .mname {
                      font-size: 13px;
                      line-height: 146%;
                    }
                  }

                }
              }
            }

            &::after {
              width: 100%;
              left: 0;
            }
          }
        }
      }
    }
  }

  @include media-breakpoint-down(xs) {
    #efformat {
      margin: 100px 0 120px;

      .block_title {
        margin-bottom: 60px;

        br {
          display: inline;
        }
      }

      .slider {
        margin-left: -5px;

        [class^=col] {
          min-width: 246px;
          padding: 0 5px;

          .title {
            font-size: 17px;
            line-height: 148%;
            margin-bottom: 10px;
            text-transform: uppercase;
            max-width: 80%;
            min-height: 50px;
          }

          .text {
            font-size: 13px;
            line-height: 146%;
            margin-bottom: 40px;
          }

          .link {
            left: 5px;
          }
        }
      }

    }

    #kind_healthy {
      padding: 80px 0;
      font-size: 15px;

      ul {
        li {
          padding: 25px 0;
        }
      }
    }

    #informal {
      margin: 100px 0 0;

      .slider {
        >div {
          // left: calc(-100% - 10px);

          .img {
            min-height: 300px;
            margin: 0 5px;
            left: -5px;
          }
        }
      }
    }

    #timetable {
      .container-lg {
        padding: 0;

        .filters {
          font-size: 15px;
          line-height: 146%;
          display: block;

          &.open {
            max-height: 1200px;
          }

          >form {
            padding-top: 40px;
            flex-direction: column;

            >div {
              .pidpys {
                font-size: 15px;
                line-height: 148%;
                padding-bottom: 5px;
                margin-top: 40px;
              }

              .custom-checkbox {
                margin-top: 10px;
              }

              &:first-child {
                .pidpys {
                  margin-top: 0;
                }
              }
            }
          }

          >div {
            .dlink {
              margin-top: 40px;
            }

            .btn {
              width: 100%;
            }
          }
        }
      }
    }
  }
}