#organizers {
  @include media-breakpoint-down(lg) {
    #fresh_solutions {
      margin: 140px 0;
    }

    #search_decision {
      padding: 227px 0;

      &::after {}

      .big_title {
        font-size: 33px;
        line-height: 120%;
      }
    }

    #partners {
      margin: 140px 0 0;
    
      .filter {
        ul {
          li {
            font-size: 17px;
          }
        }
      }
    }    
  }

  @include media-breakpoint-down(md) {
    #fresh_solutions {
      margin: 140px 0;

      .block_title {
        margin-bottom: 40px;
        line-height: 115%;
      }

      .content_title {
        font-size: 17px;
        line-height: 135%;
      }
    }

    #partners {
   
      .filter-title {
        display: block;
      }

      .partners {
        > div {
          margin-bottom: 140px;
        }
      }

      .filter {
        margin-bottom: 40px;
        overflow: hidden;
        max-height: 0;
        transition: max-height .4s;        
        &.open {
          max-height: 800px;
          transition: max-height .5s;
        }
        ul {
          padding-top: 20px;
        }
      }

      .title {
        font-size: 21px;
      }

      .cards {
        column-gap: 30px;
        row-gap: 30px;
      }
    }
        
  }

  @include media-breakpoint-down(sm) {
    #search_decision {
      padding: 227px 0 262px;

      &::after {}

      .big_title {
        font-size: 21px;
        text-align: center;
      }
    }    
    #partners {
      margin: 100px 0 0;
   
      .cards {
        column-gap: 10px;
        row-gap: 10px;
        > div {
          padding: 20px;
        }
      }
    }     
  }

  @include media-breakpoint-down(xs) {
    #fresh_solutions {
      margin: 128px 0 100px;

      .block_title {
        margin-bottom: 40px;
        line-height: 115%;
      }

      .content_title {
        font-size: 17px;
        line-height: 135%;
        margin-bottom: 60px;
      }

      ul {
        li {
          div {
            &:last-child {
              font-size: 15px;
              position: relative;
              top: -3px;
            }
          }
        }
      }
    }

    #partners {
      margin-bottom: 20px;
      .partners {
        > div {
          margin-bottom: 80px;
        }
      }
      .cards {
        grid-template-columns: 100%;
        column-gap: 0;
        row-gap: 40px;

        > div {
          padding: 40px;
          width: 320px;
          max-width: 100%;
          margin: 0 auto;
        }
      }    
      .text {
        font-size: 15px;
      }  
    }
   
  }
}