#contacts{

	@media(max-width:1279px) {
		.faq .tab-content {
			margin-top: 60px;
		}
		.contacts-service .block_title
		{margin-bottom: 40px;
		max-width: 300px;
		}
		.social p{margin-bottom: 10px;}
		.team-block {
			margin-left: -15px;
			margin-right: -15px;
			&__card {
				width: 280px;
				margin-left: 15px;
				margin-right: 15px;
				flex-shrink: 0;
				padding-top: 0;
			}
		}
		.faq{
			.accordion {margin-top:0;}
			.info-block{display: none;}
			.accordion-toggle {
				padding: 0;
			}

			.nav-tabs {
				flex-direction: row;}
		}

	}
	@media(max-width:1199px) {
		.container--special{
			position: relative;
		}
		.contacts-list {font-size: 17px;}
		.contacts-list__item{margin-bottom: 20px;}

	}
	@media(max-width:991px) {

		.contacts-block{
			.contacts-list{
				flex-wrap: wrap;
				flex-direction: row;
				margin-bottom: 60px;

				&__item{
					width: 50%;
				}
				&__item:nth-child(2n) {
					display: flex;
					justify-content: flex-end;
				}
			}
			.social{
				margin-top: 0;
			}
			.map{
				margin-top: 20px;
				height: 390px;
			}
		}
		.mass-media{
			&-text{
				padding-top: 60px;
				font-size: 17px;
				.btn{
					width: 100%;
					max-width: 330px;
					text-align: center;
				}
			}

		}
		.team-block {
			flex-wrap: nowrap;
			margin-left: 0;
			margin-right: 0;
			justify-content: space-between;

			&__card {
				width: 270px;
				margin-left: 0;
				margin-right: 0;
				flex-shrink: 0;

				h3 {
					font-size: 15px;
					line-height: 1.48;
				}

				.contacts-list__item a {
					font-size: 15px;
				}
			}

		}

		.team-block__card+.team-block__card {
			margin-left: 30px;
		}
		#contacts-team .container--special {
			height: 490px;
		}

	}
	@media(max-width:767px) {
		.contacts-service .block_title{margin-bottom: 60px;}
		.social p{margin-bottom: 35px;}
		.top_title{
			margin-bottom: 60px;
		}
		.mass-media-text .dlink {
				margin-top: 60px;
				margin-bottom: 10px;
			}
		.contacts-block{

			.contacts-list {
				font-size: 15px;
				flex-direction: column;
				&__item {
					width: 100%;
					margin-bottom: 50px;
				}

				&__item:nth-child(2n) {
					display: flex;
					justify-content: flex-start;
				}
			}
			.map {
				margin-top: 10px;
				height: 300px;
			}
		}

		.team-block__card+.team-block__card {
			margin-left: 10px;
			}


	.contacts-service {

		.block_title--medium{
			font-size: 21px;
			margin-bottom: 60px;
		}
	}
	.mass-media {
		&-text {font-size: 15px;}
		@media(max-width: 767px){
			.block_title--medium{
					max-width: 215px;
			}
			.btn{
				max-width:100%;
			}
		}
	}
	}
}

#contacts, #technodays, #community{
	@media(min-width:768px) and (max-width:991px){
		.contacts-service {
			.contacts-list{
				display: flex;
				justify-content: space-between;
			}
			.contacts-list__item+.contacts-list__item {
				margin-top: 0!important;
			}
		}
	}
	@media(max-width: 1279px){

		.border-list ul{font-size: 17px;}
	}

	@media(max-width: 1199px){
		.border-list ul{
			font-size: 15px;
			li{
				span + span {margin-left: 42px;}
				}
			}
	}
	@media(max-width: 991px){
		.swiper-wrapper {
			overflow-x: auto;
			-webkit-overflow-scrolling: touch;
			position: absolute;
			top: 0;
			left: 20px;
			width: calc(50% + 50vw);

			&::-webkit-scrollbar {
				display: none;
			}
		}
	}
	@media(max-width: 767px){
		.border-list {
			p {
				margin-bottom: 60px;
				font-size: 19px;
			}
			ul{

				li{
					padding-top: 25px;
					padding-bottom: 25px;
				}
			}
		}
	}
}
@media(max-width: 991px){
	#contacts .faq .block_title{max-width:344px;}
}
@media(max-width: 767px) {
	#contacts .faq .block_title {
		max-width: 280px;
	}
}