#article {
  font-size: 19px;
  line-height: 135%;
  letter-spacing: -0.01em;
  color: $black;

  p {
    margin-bottom: 20px;
  }

  .top_image {
    background-image: url(../img/article_bg.jpg);
  }

  .link_back {
    margin-top: 60px;

    a {
      padding-left: 35px;
      background-position: left center;
      @include arrow_left('9094A1');
      color: $aquagrey;
      background-size: 14px;
    }
  }

  .top_title {
    margin-bottom: 80px;
  }

  .title {
    font-size: 27px;
    line-height: 130%;
    color: $blue;
    margin-bottom: 40px;
  }

  #main_content {
    margin: 120px 0 180px;

    .row {
      &:nth-child(2) {
        margin-top: 60px;
      }

      &:last-child {
        justify-content: space-between;
        align-items: center;
        margin-top: 120px;
        position: relative;
        a {
          display: block;
          font-size: 19px;
          line-height: 135%;
          letter-spacing: -0.01em;
          color: #9094A1;
          margin: 40px 0;
          position: relative;
          &::before {
            content: '';
            position: absolute;
            width: 24px;
            height: 24px;
            left: 0;
            top: calc(50% - 12px); 
            transform: rotate(45deg);
            @include arrow_left('9094A1');        
          }
          &:hover {
            color: $blue;
            &::before {
              @include arrow_left('004FCB'); 
            }
          }
          &.prev {
            padding: 0 0 0 45px;
          }
          &.next {
            padding: 0 45px 0 0;
            &::before {
              left: auto;
              right: 0;
              transform: rotate(135deg);
            }
          }
        }
        &::before, &::after {
          content: '';
          position: absolute;
          width: calc(100% - 40px);
          height: 1px;
          opacity: .2;
          left: 20px;
          top: 0;
          background-color: $aquagrey;
        }
        &::after {
          top: auto;
          bottom: 0;
        }
      }
    }

    .speakers {
      max-width: 50%;
      font-size: 19px;
      line-height: 148%;
      text-transform: uppercase;

      >div {
        margin-bottom: 20px;
      }

      span {
        display: block;
        font-size: 15px;
        line-height: 146%;
        text-transform: none;
      }
    }

    .text {
      margin-top: 60px;
      font-size: 15px;
      line-height: 146%;

      span {
        display: block;
        color: $aquagrey;
        margin: 40px 0;
      }

      .social {
        a {
          margin-right: 20px;

          &:last-child {
            margin-right: 0;
          }
        }

        svg {
          width: 24px;
          height: 24px;

          path {
            fill: $blue;
          }
        }
      }

    }
  }
}