/*** data-aos plagin ***/
[data-aos="anim_bl"]{
	opacity: 0;
	transform: translateY(20px);
    transition: 1s;
}
[data-aos="anim_bl"].aos-animate{
	opacity: 1;
	transform: translateY(0);
}

[data-aos="anim_left"]{
	opacity: 0;
	transform: translateX(10%);
    transition: 1s;
}
[data-aos="anim_left"].aos-animate{
	opacity: 1;
	transform: translateX(0);
}


[data-aos="anim_bottom"]{
	opacity: 0;
	transform: translateY(100%);
    transition: 1s;
}
[data-aos="anim_bottom"].aos-animate{
	opacity: 1;
	transform: translateY(0);
}