@media(max-width: 1699px){
	#technodays .invitation{
		min-height: 600px;
		&-img{height: 600px;}
		&-text__inner{height: 600px;}
	}
}
@media(max-width: 1279px){

	#technodays {
		.leaders-text .block_title{
			max-width: 440px;

		}
		.leaders-logo__item img{max-width: 130px;}
		.invitation-text .title-decore{margin-bottom: 60px;}
	}
	#technodays .directions{
		.block_title{
			max-width: 330px;
			margin-bottom: 20px;
		}
	}

	#technodays{
		.invitation{
			min-height: 480px;
			&-img{height: 480px;}
			&-text__inner{height: 480px;padding-bottom: 20px;}
		}
		.invitation-img--desktop{
			display: none;
		}
		.invitation-img--mobile{
			display: block;
			width: 48%;

		}
		.invitation-benefits{
			padding-top: 45px;
			padding-bottom: 25px;
			.invitation-text__inner,
			.invitation-img{height: 300px;}
			.invitation-block{min-height: 300px;}
			.invitation-block + .invitation-block{margin-top: 50px;}
		}
	}

}
@media(max-width: 991px) {

	#technodays {
		.leaders-logo__item{
		-webkit-justify-content: flex-start;
		        justify-content: flex-start;
		  padding-top: 38px;
		img{max-width: 100%;}
		}
		.invitation{
			min-height: 450px;
			&-img{height: 450px;}
			&-text__inner{height: 450px;}
		}
		.invitation-text__inner .title-decore{
			font-size: 17px;
			max-width: 275px;
			&:before{
				bottom: -25px;
			}
			}
		.invitation-benefits{
			padding-top: 45px;
			padding-bottom: 25px;
			.invitation-text__inner,
			.invitation-img{height: 185px;}
			.invitation-block{min-height: 185px;}
		}

	}

