@media(max-width: 767px) {
	.directions .block_title {margin-bottom: 35px;}
	#technodays .top_title br{display: none;}

	#technodays{
		.top_image.top-image--desktop{
			display: none;
		}
		.top_image.top-image--mobile {
			display: block;
		}
		.invitation{margin-top: 100px;}
		.top_title{margin-bottom: 60px;}
		.leaders-logo__item{width: 50%;}
		.invitation-text__inner{
			margin-top: 393px;
			height: auto;
			padding-bottom: 60px;
			max-width: 320px;
			// margin-left: auto;
			margin-right: auto;
		}
		.invitation-img--mobile{
			height: 350px;
			width: 100%;

		}
		.invitation-text__inner .title-decore{font-size: 21px; line-height: 1.3;}
	}
	#technodays .invitation-benefits{
		padding-top: 0;
		padding-bottom: 40px;
		.invitation-img{height: 350px;}
		 .invitation-text__inner{height: auto;}
		 .invitation-block +.invitation-block{
			margin-top: 0px;
		}
	}
	#technodays .invitation-benefits .invitation-text{
		max-width: 320px;
		margin-left: auto;
		margin-right: auto;
	}
}
.invitation-img--desktop + .invitation-block .invitation-text{
	@media(min-width: 1280px){
		padding-bottom: 70px;
	}
}