#exhibition {
  .top_image {
    background-image: url(../img/exhibition_top.jpg);
  }

  #promote_product {
    background-color: #F6F6F6;
    padding: 120px 0;

    .title {
      font-size: 27px;
      line-height: 130%;
      color: $black;
      margin-bottom: 60px;

      span {
        color: $blue;
      }
    }

    .text {
      font-size: 19px;
      line-height: 135%;
      color: $black;
    }
  }

  #market_leaders {
    margin: 180px 0;
    font-size: 27px;
    line-height: 130%;
    color: $black;

    span {
      color: $blue;
    }

    img {
      margin-left: 40px;

      &:first-child {
        margin-left: 0;
      }
    }

    [class^=col] {
      &:last-child {
        display: flex;
        > div {
          margin-left: 40px;

          &:first-child {
            margin-left: 0;
          }          
        }
      }
    }

    .row {
      align-items: center;
    }
  }

  .content-mask__text .container>div{
    // height: 250px;
  }  

	.invitation-block{
		position: relative;
	}
	.invitation-img--mobile{
			display: none;
			background-position: center 30%;
			-webkit-background-size: cover;
			     -o-background-size: cover;
			        background-size: cover;
			background-repeat: no-repeat;
		}
	.leaders{

		&-text{
			background-color: $grey;
			padding-top: 180px;
			padding-bottom: 180px;
			@media(max-width: 1279px){
				padding-top: 100px;
				padding-bottom: 128px;
			}
			@media(max-width: 767px){
				padding-bottom: 100px;
			}

			.block_title{
				max-width: 770px;

			}
		}
		&-logo{
			.block_title{
				height: 100%;
				display: flex;
				-webkit-align-items: center;
				        align-items: center;
				span{display: block;}
			}
		}
		&-logo-block{

			-webkit-flex-wrap: wrap;
			    -ms-flex-wrap: wrap;
			        flex-wrap: wrap;

		}
		&-logo__item{
			width: 25%;
			-webkit-justify-content: center;
			        justify-content: center;
		}
	}

	.invitation{
		background-color: $grey;

		min-height: 900px;
		position: relative;
		&-img{
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			height: 900px;
			img{
				max-width: unset;
				width: auto;
				max-height: 100%;
				}

		}
		&-text__inner{
			height: 900px;
			display: flex;
			-webkit-flex-direction: column;
			    -ms-flex-direction: column;
			        flex-direction: column;
			-webkit-justify-content: center;
			        justify-content: center;
		}


	.title-decore{
    margin-bottom: 0;
    font-size: 144px;
    line-height: 100%;
    color: $blue;
    &::before {
      display: none;
    }
    span {
      font-size: 64px;
      line-height: 100%;
    }
  }

  .medium-text {
    font-size: 19px;
    line-height: 135%;
    color: $black;    
  }

  .content-mask__img {
    &::after {
      transform: rotate(18deg);
      transform-origin: top left;
      top: 0;
      bottom: -120px;
      width: 760px;
      right: auto;
      left: 100%;
    }
  }

 }
    .invitation-text__inner{@media(min-width: 1280px){padding-left: 20px;}}
 	.invitation-benefits{
		.invitation-text__inner{
			height: auto;
		}
		.invitation-block + .invitation-block{margin-top: 80px;}
	}
	.invitation-benefits{
		padding-top: 32px;
		padding-bottom: 315px;
		background-color: #FFF;
		.invitation-text +.invitation-text{
			margin-top: 80px;
		}
	}  

  #exhibition_data {
    min-height: 900px;
    padding-bottom: 160px;

    [class^=col] {
      z-index: 2;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      justify-content: center;
      min-height: 900px;

      >div {
        font-size: 144px;
        line-height: 100%;
        color: $blue;
        max-width: 373px;

        span {
          font-size: 64px;
          line-height: 100%;
        }

        >div {
          font-size: 19px;
          line-height: 135%;
          color: $black;
          margin-top: 40px;
        }

        &:last-child {
          position: absolute;
          bottom: -160px
        }
      }
    }

    position: relative;

    &::before {
      content: '';
      position: absolute;
      width: calc(50% + 190px);
      height: calc(100% - 160px);
      right: calc(50% - 190px);
      top: 0;
      background-image: url(../img/exhibition_data_bg.jpg);
      background-repeat: no-repeat;
      background-position: right center;
    }

    &::after {
      content: '';
      position: absolute;
      width: 100%;
      height: calc(100% - 160px);
      left: calc(50% + 190px);
      top: 0;
      background-color: #F6F6F6;
      transform: skewX(-24.5deg);
      transform-origin: left top;
      z-index: 1;
    }
  }

  #what_participation {
    margin: 180px 0;

    .block_title {
      font-size: 27px;
      line-height: 130%;
    }

    ul {
      padding: 0;
      list-style: none;

      li {
        display: grid;
        grid-template-columns: 300px auto;
        border-bottom: 1px solid rgba(144, 148, 161, .6);
        padding: 40px 0;

        div {
          &:first-child {
            font-size: 19px;
            line-height: 135%;
            color: $blue;
          }

          &:last-child {
            color: $black;
          }
        }
      }
    }
  }

  #exhibition_tnf {
    padding: 335px 0 325px;
    color: #fff;
    background-color: $blue;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      width: 1919px;
      height: 900px;
      top: 0;
      left: 0;
      background-repeat: no-repeat;
      background-image: url("data:image/svg+xml,%3Csvg width='1920' height='900' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cmask id='a' maskUnits='userSpaceOnUse' x='-403' y='-3201' width='3932' height='4789'%3E%3Cpath d='M1493.18 1586.39l2034.28-3760.25-1895.5-1025.46L-402.321 560.928 1493.18 1586.39z' fill='%230052CB' stroke='%237DE2F4'/%3E%3C/mask%3E%3Cg mask='url(%23a)' stroke='%237DE2F4' stroke-linecap='round' stroke-linejoin='round'%3E%3Cpath d='M-84.606 730.453l-831.25-2322.943M3646.96-1826.42L-83.3 731.163 1655.27-3200.32'/%3E%3Cpath d='M4421.24 816.233l-4505.847-85.78L3714.18-396.769'/%3E%3Cpath d='M4596.78 2988.49L-84.605 730.452 4583.61 1846.96'/%3E%3C/g%3E%3C/svg%3E");
    }

    .title {
      font-size: 72px;
      line-height: 120%;
      margin-bottom: 40px;
      text-transform: uppercase;
      letter-spacing: -0.03em;
    }

    .text {
      font-size: 33px;
      line-height: 115%;
    }
  }

  #optimal_variant {
    overflow: hidden;
    margin: 180px 0;

    .block_title {
      font-size: 27px;
      line-height: 130%;
    }

    .dlink {
      margin: 0;
      background-position: left top 5px;

      span {
        font-size: 15px;
        line-height: 146%;
      }
    }

    [class^=container] {
      &:first-child {
        .row {
          justify-content: space-between;
          align-items: center;          
        }
      }  
      &:nth-child(2) {
        .row {
          margin-top: 126px;
          flex-wrap: nowrap;
          position: relative;
  
          // &::before {
          //   content: '';
          //   position: absolute;
          //   bottom: 5.5px;
          //   left: 20px;
          //   width: 200%;
          //   height: 2px;
          //   background-color: rgba(34, 38, 51, .3);
          // }
  
          [class^=col] {
            // padding-bottom: 187.5px;
            min-width: 360px;
  
            >div {
              padding: 40px 20px 135px;
              position: relative;
              border-top: 1px solid rgba(144, 148, 161, .3);
              border-bottom: 1px solid rgba(144, 148, 161, .3);

              > img {
                max-width: 100%;
              }
  
              &:hover {
                background-color: $darkblue;
  
                .btn {
                  display: block;
                }
  
                .title,
                .price {
                  color: $lightblue;
                  span {
                    color: $lightblue;
                  }
                }
  
                .text,
                .square {
                  color: #fff;
                }
              }
            }
  
            // &::after {
            //   content: '';
            //   position: absolute;
            //   bottom: 0;
            //   left: 12.5px;
            //   width: 15px;
            //   height: 15px;
            //   background-repeat: no-repeat;
            //   background-position: center;
            //   background-image: url("data:image/svg+xml,%3Csvg width='15' height='15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14.5 7.756c0 3.739-2.926 6.74-6.5 6.74-3.575 0-6.502-3.001-6.502-6.74 0-3.74 2.927-6.742 6.501-6.742 3.575 0 6.502 3.002 6.502 6.742z' fill='%23004FCB' stroke='%23004FCB'/%3E%3C/svg%3E");
            // }
          }
        }
      }          
    }

    .row {
      .title {
        font-size: 23px;
        line-height: 136%;
        color: $blue;
        margin: 40px 0 20px;
      }

      .text {
        font-size: 19px;
        line-height: 135%;
        color: $black;
        min-height: 50px;
      }

      .square {
        color: $black;
        margin: 40px 0 20px;
      }

      .price {
        font-size: 23px;
        line-height: 136%;
        color: $blue;

        span {
          color: $black;
        }
      }

      .btn {
        width: calc(100% - 40px);
        color: $darkblue;
        position: absolute;
        bottom: 40px;
        display: none;
      }
    }


  }

  #stands_booked {
    margin: 0 0 180px;

    .block_title {
      font-size: 27px;
      line-height: 130%;
    }

    .row {
      &:first-child {
        justify-content: space-between;
        align-items: center;
        margin-bottom: 80px;

        .btn {
          font-weight: 400;
          min-width: 270px;
          line-height: 148%;

          &:first-child {
            margin-right: 40px;
          }
        }
      }

      svg {
        max-width: 100%;
      }
    }

  }

  #register_participation {
    padding: 120px 0;
    background-color: #F6F6F6;

    .block_title {
      font-size: 27px;
      line-height: 130%;
    }

    .dlink {
      margin: 0;
      background-position: left top 5px;
      span {
        font-size: 15px;
        line-height: 146%;        
      }
    }

    .row {
      &:first-child {
        justify-content: space-between;
        align-items: center;
      }

      &:last-child {
        margin-top: 120px;

        [class^=col] {
          min-width: 360px;
          >div {
            padding: 20px 20px 180px;
            border-left: 2px solid #4678E1;
            

            &:hover {
              border-left: 2px solid $darkblue;
              background-color: $darkblue;

              .number,
              .title,
              .text {
                color: #fff;

                span {
                  color: $lightblue;
                }
              }

              .btn {
                display: block;
              }
            }
          }
        }
      }
    }

    .number {
      font-size: 19px;
      line-height: 148%;
      color: $blue;
    }

    .title {
      font-size: 23px;
      line-height: 136%;
      color: $black;
      margin: 80px 0 20px;

      span {
        color: $blue;
      }
    }

    .text {
      font-size: 19px;
      line-height: 135%;
      color: $black;
      letter-spacing: -0.01em;
      max-height: 50px;
    }

    .btn {
      position: absolute;
      bottom: 40px;
      min-width: calc(100% - 80px);
      font-weight: 700;
      display: none;
    }
  }

  #archiv {
    margin: 180px 0;

    .block_title {
      font-size: 27px;
      line-height: 130%;
    }

    .row {
      &:first-child {
        align-items: flex-end;
      }

      &:last-child {
        margin-top: 120px;
      }
    }

    .pfilter {
      margin-bottom: 0;

      a {
        &::after {
          padding: 0 20px;
        }
      }
    }

    .slider {
      >div {
        .img {
          background-image: url(../img/exhibition_archive_1.jpg);

          &:first-child {
            background-image: url(../img/exhibition_archive_2.jpg);
          }

          &:last-child {
            background-image: url(../img/exhibition_archive_3.jpg);
          }
        }
      }
    }
  }
}