#upcoming-event {
  @include media-breakpoint-down(lg) {
    #invited-experts {

      .speakers {

        [class^=col] {
          min-width: 290px;
          padding-right: 0;
        }
      }
    }
    #key-topics {

      .block_title {
        padding-bottom: 60px;
      }
    }
    #program {

      .block_title {
        padding-bottom: 60px;
      }
    }
  }

  @include media-breakpoint-down(md) {
    .block_title {
      font-size: 21px;
    }

    #top_content {

      .top_date {
        font-size: 17px;
      }

      .top_image {
        margin-top: 60px;
        margin-bottom: 100px;
      }

      .img-fluid {
        margin-top: 0;
        margin-bottom: 100px;
      }
    }

    #about-event {

      .description {
        margin-top: 40px;
        margin-bottom: 140px;

        p {
          font-size: 17px;
        }

        .btn-wrapper {
          display: flex;
          flex-wrap: wrap;
          row-gap: 20px;
          padding-top: 20px;
        }
      }

      .download-presentation.mobileHide {
        display: none !important;
      }

      .download-presentation.mobileShow {
        display: flex !important;
        justify-content: flex-start;
        margin-top: 40px;
      }
    }

    #organizers{
      padding: 0;

      [class^=col] {
        &:last-child {
          margin-top: 40px;
          margin-bottom: 140px;
        }
      }

      .scroll_organizators {
        width: calc(50% + 52vw);
        overflow-x: auto;
        overflow-y: hidden;
      }
      .scroll_organizators::-webkit-scrollbar{width: 0;}
    }

    #key-topics {
      padding: 100px 0;

      .block_title {
        padding-bottom: 20px;
        font-size: 21px;
      }

      .table-row .title {
        margin-bottom: 20px;
      }
    }

    #invited-experts {
      padding: 140px 0;

      .speakers {
        width: calc(50% + 52vw);
        padding-top: 60px;
        overflow-x: auto;
        overflow-y: hidden;
      }
      .speakers::-webkit-scrollbar{width: 0%;}
    }

    #program {
      padding: 100px 0;

      .block_title {
        font-size: 21px;
      }

      .table-row {

        .title {
          font-size: 17px;
          margin-bottom: 40px;
        }

        .description {
          font-size: 15px;
        }
      }
    }

    #question {
      padding-top: 100px;
      padding-bottom: 100px;

      .block_title {
        margin-bottom: 40px;
      }

      form {

        > div {

          span.error-message {
            font-size: 15px;
          }
        }

        .has-error {

          label {
            width: auto;
          }
        }

        label {
          font-size: 17px;
          width: min-content;
        }
      }
    }

    #company-organizer {
      padding-top: 140px;
      padding-bottom: 140px;

      .block_title {
        margin-bottom: 40px;
      }

      .articles>div {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr repeat(4, auto);
        row-gap: 20px;

        .title {
          font-size: 17px;
        }

        .intro {
          font-size: 15px;
        }

        .link{
          height: 15px;
          display: inline-block;
          background-size: contain;
          padding-right: 20px;
        }
      }
    }
  }

  @include media-breakpoint-down(xs) {

    #top_content {

      .back-to-tnf-online {
        display: none;
      }

      .top_title {
        font-size: 17px;
        line-height: 130%;
        margin-bottom: 30px;
        max-width: 320px;
      }

      .top_date {
        font-size: 13px;
      }

      .img-fluid {
        margin-top: 30px;
        min-height: 300px;
        object-fit: cover;
        margin-bottom: 80px;
      }

      .top_image {
        margin-top: 60px;
        margin-bottom: 80px;
      }
    }

    #about-event {

      .description {
        margin-top: 40px;
        margin-bottom: 80px;
        font-size: 15px;

        p {
          font-size: 15px;
        }

        .btn-wrapper {
          display: flex;
          flex-wrap: wrap;
          row-gap: 20px;
          padding-top: 20px;

          .btn {
            font-size: 13px;
            font-weight: 400;
            width: 100%;
          }

          .btn-primary {
            order: 0;
          }

          .btn-primary-reverse {
            order: 1;
          }
        }

        .download-presentation{

          >a{
            font-size: 15px;

            >span{
              font-size: 13px;
            }
          }
        }
      }
    }

    #key-topics {
      padding: 80px 0;

      .block_title {
        font-size: 21px;
        padding-bottom: 20px;

        br {
          display: block;
        }
      }

      .table-row {

        .title {
          font-size: 17px;
          margin-bottom: 20px;
        }

        .description {
          font-size: 15px;
        }
      }
    }

    #invited-experts {

      .speakers {
        padding-top: 60px;

        [class^=col] {
          min-width: 290px;
          padding-right: 0;
        }

        .name {
          font-size: 15px;
        }
      }
    }

    #program {
      padding: 80px 0;

      .block_title {
        font-size: 21px;
        padding-bottom: 60px;
      }

      .table-row {

        .title {
          font-size: 17px;
          margin-bottom: 40px;
        }

        .description {
          font-size: 15px;

          ul li {
            font-size: 15px;
          }
        }
      }
    }

    #question {
      padding-top: 80px;
      padding-bottom: 80px;

      .block_title {
        margin-bottom: 60px;
      }

      form {

        > div {
          flex-wrap: wrap;

          span.error-message {
            font-size: 13px;
          }
        }

        .has-error {

          div {
            width: 100%;

            input {
              width: 100%;
            }
          }
        }

        label {
          font-size: 15px;
          margin-bottom: 10px;
          width: 100%;
        }

        input,
        textarea {
          &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
            font-size: 15px;
          }
          &::-moz-placeholder { /* Firefox 19+ */
            font-size: 15px;
          }
          &:-ms-input-placeholder { /* IE 10+ */
            font-size: 15px;
          }
          &:-moz-placeholder { /* Firefox 18- */
            font-size: 15px;
          }
        }

        .submit{
          padding-top: 20px;
          padding-bottom: 20px;

          a {
            font-size: 13px;
            font-weight: 400;
            width: 100%;
          }
        }
      }
    }

    #company-organizer {

      .block_title {
        margin-bottom: 60px;
      }

      .articles>div {

        img {
          height: 300px;
          -o-object-fit: cover;
          object-fit: cover;
          grid-row: 1;
        }

        .title {
          font-size: 17px;
        }

        .intro {
          font-size: 15px;
        }

        .link{
          height: 15px;
          display: inline-block;
          background-size: contain;
          padding-right: 20px;
        }
      }
    }
  }
}