#registration {

  .top_title {
    margin-bottom: 100px;
  }

  #eff_format {
    margin: 0 0 150px;
    .block_title {
      margin-bottom: 120px;
    }
    .mozaic {
      display: grid;
      grid-template-columns: repeat(3, auto);
      grid-template-rows: 205px 205px;
      column-gap: 40px;
      row-gap: 40px;
      > div {
        position: relative;
        img {
          // width: 100%;
          z-index: 1;
          max-width: 100%;
        }
        &:nth-child(2) {
          grid-row: 1 / span 2;
          grid-column: 1 / 1;
        }
        &:nth-child(3) {
          grid-row: 1 / span 2;
          grid-column: 3;
        }        
        .title {
          position: absolute;
          bottom: 20px;
          left: 20px;
          font-size: 19px;
          line-height: 148%;
          text-transform: uppercase;
          color: #fff;
          overflow: hidden;
          max-height: 28px;
          transition: max-height .3s;
          z-index: 3;
          span {
            display: block;
            font-size: 17px;
            line-height: 146%;
            text-transform: none;
            margin: 10px 0 20px;
          }
          .btn {
            text-transform: none;
          }
        }
        &:hover {
          .title {
            max-height: 160px;
            transition: max-height .8s;
            color: $lightblue;
            span {
              color: #fff;
              padding-right: 15px;
            }
            .btn {
              text-transform: none;
            }
          }
          &::after {
            position: absolute;
            content: '';
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(3, 38, 99, .7);
            z-index: 2;
          }
        }
      }
    }
  }  

  #question_answer {
    margin: 0 0 180px;

    .block_title {
      font-size: 27px;
      line-height: 130%;
      margin-top: 30px;
    }

    .text {
      color: $black;
      margin: 20px 0 40px;
    }

    .btn.btn-primary {
      width: 330px;
      max-width: 100%;
      font-size: 15px;
      line-height: 150%;
    }
  }
}