.content-mask{

	height: 100vh;

	position: relative;
	position: sticky;
	top:0;
	z-index: 5;
}
#technodays .content-mask__img,
.content-mask__img{
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	width: 54%;
	height: 100vh;
	overflow: hidden;
	.mask-img-item{
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right:0;
		opacity: 0;
		transition: .3s;
		margin-right: 1px;
		margin-bottom: 1px;

		background-repeat: no-repeat;
		background-position: center;
		-webkit-background-size: cover;
	     -o-background-size: cover;
	        background-size: cover;
			
	}
	
}
#technodays {
	.content-mask__img{
		@media(min-width:768px) {
			.mask-img-item:nth-child(3) {
				background-position: 78% top;
			}

			.mask-img-item:nth-child(2) {
				background-position: 18% top;
			}
		}
	}
}
.mask-img-item.visible{
	opacity: 1;
}
.content-mask__img:after{
	content: '';
	position: absolute;
	right: 0;
	top: -50px;
	bottom:0;
	width: 215px;
	background-color: $grey;
	z-index: 2;
	transform: translateX(110px) rotate(-12deg);
	@media(max-width: 1279px){display: none;}
}
.content-mask__element{
	width: 46%;
	height: 100vh;
	position: relative;
	margin-left: auto;
}
.mask-part{
	position: absolute;
	left: 0;
	right: 0;
	height: 30%;

	background-color: $grey;
	&__top{
		top:0;
		background: rgb(246,246,246);
    background: linear-gradient(0deg, rgba(246, 246, 246, 0.35) 0%,  #F6f6f6 22%);
	
	}
	&__bottom{bottom:0;
	background: rgb(246,246,246);
    background: linear-gradient(180deg, rgba(246, 246, 246, 0.35) 0%,  #F6f6f6 22%);
	}
}

.content-mask__text .container>div{
	width: 38%;
	max-width: 480px;
	margin-left: auto;
}
.content-mask__text{
	margin-top: -64vh;
	padding-bottom: 30vh;
}

.content-mask__text .container>div{
	height: 450px;
	display: flex;
	-webkit-flex-direction: column;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;

}
#technodays .mask-part__top{
	@media(min-width:1920px) {
	left: -50px;
	}
} 
#technodays .blueray{
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	height: 900px;
	.blueray-content{
		max-width: 787px;
		margin-left: auto;
		margin-right: auto;
	}
	.title{
	font-size: 144px;
	line-height: .7;
	margin-bottom: 40px;

	letter-spacing: -0.03em;
	text-transform: uppercase;
	}

	@media(max-width: 1279px){

		height: 750px;
		.blueray-content{
			max-width: 566px;
		}
		.title{
			font-size: 64px;
			line-height: 1.15;

		}
	}
	@media(max-width: 991px){
		height: 614px;
	}
	@media(max-width: 767px){
		height: 640px;
		.blueray-content{
			max-width: 270px;
		}
		.title{
			font-size: 82px;
			line-height: 0.7;

		}
	}
}
