	}
	img{
		max-width: 100%;
	}
	p, ul{
		margin: 0;
		padding: 0;
		list-style-type: none;
		}
	button{outline: none;}
	.border-list ul li{
		border-bottom-color:$lightblue ;
	}
	.invitation-block{
		position: relative;
	}
	.invitation-img--mobile{
			display: none;
			background-position: center 30%;
			-webkit-background-size: cover;
			     -o-background-size: cover;
			        background-size: cover;
			background-repeat: no-repeat;
		}
