			display: flex;
			-webkit-flex-direction: column;
			    -ms-flex-direction: column;
			        flex-direction: column;
			-webkit-justify-content: center;
			        justify-content: center;
		}


	.title-decore{margin-bottom: 120px;}

 }
    .invitation-text__inner{@media(min-width: 1280px){padding-left: 20px;}}
 	.invitation-benefits{
		.invitation-text__inner{
			height: auto;
		}
		.invitation-block + .invitation-block{margin-top: 80px;}
	}
	.invitation-benefits{
		padding-top: 32px;
		padding-bottom: 315px;
		background-color: #FFF;
