#technodays #register_participation .block_title,
#technodays #success_project .block_title{max-width: 331px;}
#register_participation {
    padding: 120px 0;
    background-color: #F6F6F6;

    .block_title {
      font-size: 27px;
      line-height: 130%;
    }

    .dlink {
      margin: 0;
      background-position: left top 5px;
      span {
        font-size: 15px;
        line-height: 146%;
      }
    }

    .row {
      &:first-child {
        justify-content: space-between;
        align-items: center;
      }

      &:last-child {
        margin-top: 120px;

        [class^=col] {
          min-width: 360px;
          >div {
            padding: 20px 20px 180px;
            border-left: 2px solid #4678E1;
			background-color: #f6f6f6;
			height: 100%;
			.text {
				color: #9094A1
			}
            &:hover {
              border-left: 2px solid $darkblue;
              background-color: $darkblue;

              .number,
              .title,
              .text {
                color: #fff;

                span {
                  color: $lightblue!important;
                }
              }

              .btn {
                display: block;
              }
            }
          }
        }
      }
    }

    .number {
      font-size: 19px;
      line-height: 148%;
      color: $blue;
    }

    .title {
      font-size: 23px;
      line-height: 136%;
      color: $black;
      margin: 80px 0 20px;

      span {
        color: $blue;
      }
    }

    .text {
      font-size: 19px;
      line-height: 135%;
      color: $black;
      letter-spacing: -0.01em;
      max-height: 50px;
    }

    .btn {
      position: absolute;
      bottom: 40px;
      min-width: calc(100% - 80px);
      font-weight: 700;
      display: none;
    }
 }

@include media-breakpoint-down(lg) {
  #register_participation {
    padding: 100px 0;

    .block_title {
      font-size: 21px;
      line-height: 135%;
    }

    .row {

      &:last-child {
        margin-top: 60px;
        flex-wrap: nowrap;
        margin-left: -15px;
        @media(max-width: 991px){
        	   overflow-x: auto;
          overflow-y: hidden;
		  scrollbar-width: none;
		  &::-webkit-scrollbar {
		  width: 0;
		}
        }

        [class^=col] {
          padding-right: 15px;
          padding-left: 15px;

          >div {
            padding-top: 40px;
            padding-bottom: 130px;
          }
        }
      }
    }

    .title {
      font-size: 17px;
      line-height: 146%;
      margin-top: 40px;
    }

    .text {
      font-size: 15px;
      line-height: 146%;
    }

    .btn {
      font-size: 15px;
    }
  }
}

@include media-breakpoint-down(md) {

  #register_participation {

    .block_title {
      br {
        display: inline;
      }
    }
  }
}

@include media-breakpoint-down(sm) {
  #register_participation {
    padding: 80px 0;

    .block_title {
      margin-bottom: 20px;
    }

    .row {

      &:last-child {
        margin-top: 60px;
        flex-wrap: nowrap;
        margin-left: -5px;

        [class^=col] {
          padding-right: 5px;
          padding-left: 5px;
          min-width: 290px;

          >div {
            padding: 20px 20px 110px;
          }
        }
      }
    }

    .title {
      font-size: 19px;
      line-height: 135%;
      margin-top: 80px;
    }
    .text {
      max-height: 42px;
    }
    .btn {
      font-size: 13px;
      bottom: 30px;
      min-width: calc(100% - 50px);
    }
  }

}
@media(max-width:991px){
	#register_participation .row:last-child [class^="col"]>div {
	
		background-color: #F6F6F6;
	}
	#register_participation .row:last-child [class^="col"]>div .number,
	#register_participation .row:last-child [class^="col"]>div .title span{

		color: #004FCB;
	}
	#register_participation .row:last-child [class^="col"]>div .title {

		color: #222633;
	}
	#register_participation .row:last-child [class^="col"]>div .text {

		color: #9094A1;
	}
	
	#register_participation .row:last-child [class^="col"]>div .btn {
		display: none;
	}

	#register_participation .row:last-child [class^="col"]>div:hover .btn {
		display: block;
	}
}