section{
	p, ul, h1, h2, h3{margin: 0;}
	ul{list-style-type: none; padding: 0;}
}
#community{
	.container{
		@media(min-width: 1280px){
			padding: 0;
		}
	}
	.top_title br{display: block;}
	.participants-header{
		margin-bottom: 109px;
		@media(max-width: 1279px){margin-bottom: 60px;}
	    -webkit-align-items: center;
	            align-items: center;
	}
	.privileges{

		.medium-text{
			max-width: 788px;
			margin-bottom: 120px;
			@media(max-width: 1279px){
				margin-bottom: 60px;
				font-size: 17px;
			}
			@media(max-width: 767px){font-size: 15px;}
		}
	}

	.skew-block-text{
		max-width: 786px;
		margin-left: auto;
		p{
			font-size: 33px;
			line-height: 1.15;

		}
		.big-title{
			font-size: 52px;
			-height: 1.2;
			letter-spacing: -0.03em;
			text-transform: uppercase;
			margin-bottom: 50px;

		}
		@media(max-width: 1279px){
			max-width: 526px;
			margin-right: auto;
			.big-title{
				font-size: 49px;
				margin-bottom: 40px;

				}
				p{font-size: 17px; line-height: 1.46;}
			}
			@media(max-width: 767px){
				 max-width: 284px;
				.big-title{font-size: 30px;}

			}
	}
	.packages{
		.block_title{
			max-width: 374px;
		}
	}
	.see_also{
		margin-top: 116px;
		
		@media(max-width: 1279px){
			margin-top: 140px;
		}
		@media(max-width: 767px){
			margin-top: 100px;
			a span{
				background-image: none;
			}
		}
	}
}
.participants{
	padding-top: 100px;
	padding-bottom: 195px;
	background-color: $grey;
	position: relative;
	#participants {
		position: relative;
		top: -185px;
	}
	@media(max-width: 1279px){
		padding-top: 100px;
		padding-bottom: 100px;
	}
	@media(max-width: 767px){
		padding-top: 80px;
		padding-bottom: 80px;
	}
	@media(max-width: 767px) {
		padding-top: 80px;
		padding-bottom: 60px;
	}
	&-btn{
		margin-bottom: 20px;
		text-align: right;
	}
	&-btn--mobile{margin-top: 60px;
		@media(max-width: 767px) {
			margin-top: 27px;
		}
	}

	.accordion-wrapper{
		max-height: 900px;
		width: 1215px;
		padding-right: 30px;
		overflow-y: auto;
		 scrollbar-width: thin;
  		scrollbar-color: $blue rgba(144, 148, 161, .3);
		&::-webkit-scrollbar {
		  width: 6px;
		  height: 6px;
		}
		&::-webkit-scrollbar-track {
		  background-color: rgba(144, 148, 161, .3);
		}
		&::-webkit-scrollbar-thumb {
		  border-radius: 2px;
		  background-color: $blue;
		}
		@media(max-width: 1279px){
			max-height: 1435px;
			width: 100%;
		}
		@media(max-width: 767px){
			max-height: 625px;
			padding-right: 10px;
		}
		@media(max-width: 375px){
			max-width: 340px;
		}
	}
	.accordion .card .card-header .btn{padding-bottom: 40px;}
	.accordion{
		.card{
			.card-header .btn{
				-webkit-border-radius: 0;
			        border-radius: 0;
			    font-size: 23px;
			    line-height: 1.36;
			    @media(max-width: 1279px){
			    	font-size: 17px;
			    }
			     @media(max-width: 767px){
			    	font-size: 19px;
			    }
			   }
			background-color: transparent;
			&:first-child .card-header .btn{border-top: 1px solid $blue;}
			&:first-child .card-header .btn.collapsed{border-top: 1px solid rgb(144, 148, 161);}
		}
		.card-body{
			li{
				font-size: 17px;
				line-height: 1.35;
				letter-spacing: -0.01em;
				display: flex;
				-webkit-align-items: center;
				        align-items: center;
				p:first-child{width: 30%; padding-right: 20px;}
				p:last-child{width: 70%;}

			}
			li +li{margin-top: 20px;}
			@media(max-width: 767px){
				li{
				-webkit-flex-direction: column;
				    -ms-flex-direction: column;
				        flex-direction: column;
				    p:first-child{width: 100%; font-size: 15px;}
				p:last-child{width: 100%; font-size: 13px;}
				p{
					padding-top: 5px;
					padding-bottom: 5px;
				 }
				}
			}
		}
	}
}


// blue cards
.skew-block{
	overflow: hidden;
	&__top,&__bottom{
		position: relative;
		height: 900px;
		width: 100%;
		background-color: $blue;
		background-position: center;
		background-repeat: no-repeat;
		-webkit-background-size: cover;
		     -o-background-size: cover;
		        background-size: cover;
		@media(max-width: 1279px){
			height: 800px;
		}
		@media(max-width: 767px){
			height: 680px;
		}
	}
	&__top{
		padding-top: 246px;
		height: 750px;

	}

	&__bottom:after{
		content: '';
		position: absolute;
		top: 40px;
		right: -30%;
		left: -30%;
		z-index: 2;
		height: 80px;
		background-color: #FFF;
		transform: rotate(6.3deg);
		@media(max-width: 1199px){
			height: 50px;
			top: 50px;
		}
		@media(max-width: 767px){
			top: 70px;
			transform: rotate(9.3deg);
		}
		@media(max-width: 374px) {
			
			transform: rotate(12.3deg);
		}
	}
	@media(max-width: 1199px){
		&__top{
			height: 700px;
			}
		&__bottom{
			height: 824px;}
	}
	@media(max-width: 767px){
		&__top{
			height: 568px;
			padding-top: 160px;}

		&__bottom{
			height: 750px;
			}
		}

}

// blue cards
.blue-cards{
	.community_bl{
		flex-wrap: nowrap;
	}
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	&__item{
		position: relative;
		color: $black;
		width: 373px;

		padding: 40px 20px 184px 20px;
		border-top: 2px solid rgba(144, 148, 161,.3);
		border-bottom: 2px solid rgba(144, 148, 161,.3);
		background-color: #FFF;
		-webkit-transition: .3s;
		   -moz-transition: .3s;
		    -ms-transition: .3s;
		     -o-transition: .3s;
		        transition: .3s;
		-webkit-flex-shrink: 0;
		        flex-shrink: 0;
	}
	&__item:hover{
		color: #fff;
		background-color: $darkblue;
		.blue-card__head{
			h3, p{color: $lightblue;}

		}
		.blue-card__body li:after{
			background-color: $lightblue;
		}
		.btn{
			color: $black!important;
			background-color: $lightblue!important;
			}
	}
	.blue-card__head{
		padding-right: 35px;
		-webkit-justify-content: space-between;
		        justify-content: space-between;
	            align-items: flex-end;
	            margin-bottom: 42px;
		h3, p{
			margin: 0;
			text-transform: uppercase;
			font-weight: bold;
			font-size: 23px;
			line-height: 1.35;
			letter-spacing: -0.01em;
		}
		p{color: $blue;}
		p>span{
			font-size: 11px;
		}
	}
	.blue-card__body{
		p{
		margin-bottom: 40px;
		font-size: 19px;
		line-height: 1.35;
		letter-spacing: -0.01em;
		min-height: 50px;

		}
		li{
			font-size: 17px;
			line-height: 1.46;
			position: relative;
			padding-left: 17px;
			max-width: 267px;
			&:after{
				content: '';
				width: 7px;
				height: 7px;
				background-color: $darkblue;
				position: absolute;
				left: 0;
				top: 50%;
				transform: translateY(-50%);
			}
		}
		li +li{margin-top: 22px;}
	}
	.blue-card__footer{
		position: absolute;
		bottom: 40px;
		left: 20px;
		right: 20px;
		// z-index: 2;
		.btn{
			@include button_1;
			width: 100%;
			transition: .3s;
			white-space: nowrap;


		}
	}
}

.blue-cards{
	@media(max-width: 1279px){
		&__item{
			width: 300px;
			padding-bottom: 130px;

		}
		.blue-card__head{
			padding-right: 0;
			h3, p{font-size: 21px;}
		}
		.blue-card__body{
			p{font-size: 17px;}
			li{font-size: 15px; max-width: 294px;}
		}
	}
	@media(max-width: 991px){
		.blue-card__head{padding-right: 35px;}
		.row{
			flex-wrap: nowrap;
			width: calc(51vw + 50%);
			overflow-x: auto;
			scrollbar-width: none;
			  &::-webkit-scrollbar {
			  width: 0;
			}
			.col-md-4{
				min-width: 329px!important;
				padding-left: 0;padding-right: 0;
				}
			.col-md-4 +.col-md-4{margin-left: 30px;}

			}


		&__item{
			width: 329px;
			padding-left: 10px;
			padding-right: 10px;
			.blue-card__footer{
				left: 10px;
				right: 10px;
			}
			.blue-card__footer .btn {font-size: 15px;}
		}

	}
	@media(max-width: 767px){
		.row{
			flex-wrap: wrap;
			width: auto;
			.col-md-4{
				width: 100%!important;
				min-width: 100%!important;
				padding-left: 15px;
				padding-right: 15px;
				}
			.col-md-4 +.col-md-4{margin-left: 0; margin-top: 10px;}
			}
		&__item{
			width: 100%;
			margin-left: auto;
			margin-right: auto;
			padding-bottom: 175px;
			padding-left: 20px;
			padding-right: 20px;
			.blue-card__head{
				padding-right: 0;
			h3, p{font-size: 19px;}
			}
			.blue-card__footer .btn span{display: none;}
			.blue-card__footer .btn {font-size: 13px;}
		}

	}

}
#community .community__top-image{
	img.visible-mobile {
		display:none;
	}

	img.visible-desktop {
		display: block;
	}
	@media(max-width:1279px) {
		height: 430px;
	}
	@media(max-width:767px) {
		height: auto;
		
		img.visible-mobile {
		display: block;
		}
		img.visible-desktop {
			display: none;
		}
	}
	@media(max-width:579px) {height: 350px;}
}
#community .community__top-image img{
	max-width: 100%;
		width: 100%;
		max-height: 100%;
		left: 0;
	
		@media(max-width:1279px) {
		width: auto;
		max-height: 100%;
		max-width: unset;
		display: block;
		position: relative;
		
	}
	@media(max-width:899px) {
	   left: -30%;
	}
	@media(max-width:767px) {
		left: 0;
	}
	@media(max-width:424px) {
		left: -60px;
		width: auto;
		max-height: 100%;
		max-width: unset;
		display: block;
		position: relative;
	}
	
}
@media (max-width: 1279.98px) {
	#community #main_site .dsgallery img {
		width: auto
	}
}

#community .blueray:before{
	background-position: 0 -15px;
	@media(max-width:1199px) {
		background-position: left top 25%;
	}
	@media(max-width:991px) {
		top: -25px;
	}
	@media(max-width:767px){
	
	background-position: left;	
	}
	@media(max-width:374px) {
		top: -9px;
	}
	
}
#community .participants-btn--mobile .btn.btn-primary {
	@media(max-width:767px) {
		width: 100%;
	}
}

