#about {
  @include media-breakpoint-down(lg) {


    #fulcrum {
      padding: 100px 0;
      font-size: 17px;
      line-height: 146%;

      .block_title {
        margin-bottom: 0;
      }

      .row {
        &:first-child {
          margin-bottom: 60px;
        }

        &:last-child {
          [class^=col] {
            >div {
              &:last-child {
                font-size: 15px;
              }
            }
          }
        }
      }
    }

    #increase_number {
      margin: 140px 0;

      .diagram {
        margin-top: 0;

        .diagram_grid {
          >div {
            >div {
              .qty {
                font-size: 48px;
                line-height: 100%;
              }
            }
          }
        }
      }
    }

    #our_mission {
      >div {
        &:first-child {
          padding: 301px 0 454px;

          &::after {
            height: 100%;
            // background-size: cover;
          }
        }

        &:nth-child(2) {
          margin-top: 40px;
          padding-bottom: 100px;

          &::before {
            height: 50px;
            top: -40px;
          }
        }

      }

      .big_title {
        font-size: 64px;
        line-height: 70%;
        text-transform: uppercase;
        margin-bottom: 40px;
      }

      .big_text {
        font-size: 17px;
        line-height: 146%;
      }

      ul {
        li {
          >div {
            font-size: 15px;

            &:first-child {
              font-size: 17px;
            }
          }
        }
      }

      .fluid-slider {
        >div {
          img {
            max-width: 330px;
          }
        }
      }
    }

    #why_choose_tnf {
      margin: 100px 0 0;

      .block_title {
        margin-bottom: 80px;
      }

      .why_grid {
        >div {
          &[class^=image-] {
            img {
              height: 270px;
              object-fit: cover;
            }
          }
        }
      }
    }

    #tnf_grown {
      margin: 140px 0;

      .block_title {
        margin-bottom: 68px;
      }

      .slider {
        margin-left: -15px;
        [class^=col] {
          min-width: 370px;
          padding-right: 15px;
          padding-left: 15px;
          &::after {
            left: 15px;
          }
        }

        .year {
          font-size: 15px;
          line-height: 148%;
        }

        .text {
          font-size: 15px;
          line-height: 146%;
        }

        img {
          width: 100%;
          height: 300px;
          object-fit: cover;
        }
      }
    }

    #our_profi {
      padding:  100px 0;
      .block_title {
        margin-bottom: 0;
      }
      .gallery {
        width: calc(50% + 51vw);
        overflow-x: auto;
        margin-top: 60px;
        flex-wrap: nowrap;
        margin-left: -10px;
        [class^=col] {
          padding-left: 10px;
          padding-right: 10px;
          min-width: 320px;
          margin-top: 0;
          img {
            min-width: 100%;
          }
        }
      }
      .gallery::-webkit-scrollbar{width: 0;}
      .name {
        font-size: 15px;
        line-height: 148%;
        text-transform: uppercase;
      }
      .link {
        display: block;
        margin-top: 60px;
      }
    }

    #see_also {
      margin: 140px 0;
      .slider {
        margin-top: 60px;
      }
    }
  }

  @include media-breakpoint-down(md) {
    .block_title {
      br {
        display: inline;
      }
    }
    #main_site{
      // .dsgallery{
      //   width: calc(50% + 52vw);
      //   overflow-x: auto;
      //   overflow-y: hidden;
      // }
      // .dsgallery::-webkit-scrollbar{width: 0;}
      .speakers__width{
        width: calc(50% + 50vw);
        overflow-x: auto;
        overflow-y: hidden;
        pointer-events: auto;
      }
      .speakers__width::-webkit-scrollbar{width: 0;}
      .speakers__left,
      .speakers__right{
        display: none;
      }
      .arrow_pr-next{
        display: none;
      }
    }

    #fulcrum {

      .block_title {
        margin-bottom: 40px;
      }

      .row {
        &:last-child {
          [class^=col] {
            margin-top: 40px;

            &:first-child {
              margin-top: 0;
            }
          }
        }
      }
    }

    #why_choose_tnf {
      margin: 80px 0 0;

      .block_title {
        margin-bottom: 60px;
      }

      .why_grid {
        grid-template-columns: repeat(2, calc(50% - 15px));
        grid-template-rows: repeat(4, 185px);
        margin-bottom: 0;
        column-gap: 30px;
        row-gap: 30px;

        >div {

          &[class^=number-] {
            >div {
              font-size: 17px;

              span {
                font-size: 17px;
                line-height: 146%;
                margin-top: 40px;
              }
            }

          }

          &[class^=image-] {
            img {
              width: 100%;
              height: 185px;
            }
          }

          >div {
            position: relative;
          }

          &.number- {
            &1 {
              >div {
                top: 0;
              }
            }

            &2 {
              grid-column: 2;
              grid-row: 2;

              >div {
                top: 0;
              }
            }

            &4 {
              grid-column: 2;
              grid-row: 1;
            }

            &5 {
              grid-column: 1;
              grid-row: 3;

              >div {
                top: 0;
              }
            }
          }

          &.image- {
            &1 {
              >div {
                img {
                  object-position: center bottom;
                }
              }
            }

            &2 {
              grid-column: 1;
              grid-row: 2;

              >div {
                top: 0;

                img {
                  object-position: center top;
                }
              }
            }

            &3 {
              grid-column: 2;
              grid-row: 3;
            }
          }
        }
      }
    }

    #our_mission {
      .block_title {
        margin-bottom: 60px;
      }
      #slidewrapper-our{
        width: calc(50% + 50vw);
        // top: -40px;
        overflow-x: auto;
        overflow-y: hidden;
      }
      #slidewrapper-our::-webkit-scrollbar{width: 0;}
      .fluid-slider{
        >div{
          top: 0;
        }
      }

      .fluid-slider{
        >div{
          img{
            max-width: none;
            min-height: 440px;
          }
        }
      }
    }

    #tnf_grown{
      .slider__scroll{
        width: calc(50% + 50vw);
        padding-top: 10px;
        overflow-x: auto;
        overflow-y: hidden;
      }
      .slider__scroll::-webkit-scrollbar{width: 0;}
    }

  }

  @include media-breakpoint-down(sm) {
