    }

    #why_choose_tnf {
      .why_grid {
        grid-template-rows: repeat(4, 235px);

        >div {
          &[class^=image-] {
            img {
              height: 235px;
            }
          }
        }
      }
    }

    #our_mission {
      > div {
        &:first-child {
          &::after {
            background-image: url("data:image/svg+xml,%3Csvg width='2432' height='2439' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg stroke='%237DE2F4' stroke-linecap='round' stroke-linejoin='round'%3E%3Cpath d='M-84.607 2399.95L-915.856 77.012M3646.96-156.918L-83.3 2400.66l1738.57-3931.48'/%3E%3Cpath d='M4421.24 2485.73l-4505.848-85.78L3714.17 1272.73'/%3E%3Cpath d='M4596.78 4657.99L-84.606 2399.95 4583.61 3516.46'/%3E%3C/g%3E%3C/svg%3E");
            background-position: left bottom 37px;
            background-size: contain;
          }
        }
      }
      .fluid-slider{
        >div{
          img{
            min-height: 300px;
            height: 300px;
          }
        }
      }
    }
  }

