#organizers {
  color: $black;

  .top_image {
    background-image: url(../img/organizers_top.jpg);
  }

  #fresh_solutions {
    margin: 180px 0;

    .block_title {
      margin-bottom: 120px;
    }

    .content_title {
      font-size: 19px;
      line-height: 135%;
      margin-bottom: 80px;
    }

    ul {
      list-style: none;
      padding: 0;

      li {
        display: grid;
        grid-template-columns: 30px auto;
        column-gap: 40px;
        font-size: 19px;
        line-height: 135%;
        padding: 40px 0;
        border-bottom: 1px solid #9094A1;

        div {
          &:first-child {
            color: $blue;
          }
        }

        &:first-child {
          border-top: 1px solid #9094A1;
        }
      }
    }
  }

  #search_decision {
    padding: 338px 0;
    background-color: $blue;
    position: relative;

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-repeat: no-repeat;
      background-position: left bottom;
      background-image: url("data:image/svg+xml,%3Csvg width='1920' height='900' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cmask id='a' maskUnits='userSpaceOnUse' x='-422' y='-3310' width='4058' height='4985'%3E%3Cpath d='M1502.01 1673.67l2133.01-3942.75L1712.2-3309.32-420.815 633.43 1502.01 1673.67z' fill='%230052CB' stroke='%237DE2F4'/%3E%3C/mask%3E%3Cg mask='url(%23a)' stroke='%237DE2F4' stroke-linecap='round' stroke-linejoin='round'%3E%3Cpath d='M-142.923 805.09l-728.446-2405.52M3745.86-2093.62L-108.061 974.287 1815.81-3310.5M4483.19 641.99L-97.222 1059.94 3823.14-600.484'/%3E%3Cpath d='M4621.83 3051.01L-96.7 997.048l4757.06 732.242'/%3E%3C/g%3E%3C/svg%3E");
    }

    .big_title {
      font-size: 47px;
      line-height: 120%;
      letter-spacing: -0.01em;
      color: #fff;
    }
  }

  #partners {
    margin: 180px 0 0;

    .filter-title {
      display: none;
      font-size: 15px;
      line-height: 146%;
      background: #F6F6F6;
      border-radius: 2px;
      padding: 15px 51px 15px 15px;
      margin-bottom: 0;
      position: relative;
      color: #9094A1;

      &::before {
        content: '';
        position: absolute;
        width: 51px;
        height: 100%;
        top: 0;
        right: 0;
        background-color: $blue;
      }

      &::after {
        content: '';
        position: absolute;
        width: 51px;
        height: 100%;
        top: 0;
        right: 0;
        background-position: center;
        background-size: 26px;
        @include white_cross;
        transition: transform .4s;
      }

      &.open {
        &::after {
          transform: rotate(45deg);
          transition: transform .5s;
        }
      }
    }    

    .filter {
      background-color: #F6F6F6;      

      ul {
        padding: 40px 20px 0;
        list-style: none;
        margin: 0;

        li {
          font-size: 19px;
          line-height: 135%;
          margin-bottom: 20px;

          &.active {
            a {
              color: $blue;
            }
          }

          &:last-child {
            margin-bottom: 0;
          }
        }
      }

      .btn {
        margin: 60px 20px 40px;
        width: calc(100% - 40px);
      }
    }

    .title {
      font-size: 27px;
      line-height: 130%;
      color: $blue;
    }

    .text {
      margin: 40px 0 0;
    }

    .partners {
      > div {
        margin-bottom: 180px;
      }
    }

    .cards {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 20px));
      column-gap: 40px;
      row-gap: 40px;
      margin-top: 40px;
      > div {
        padding: 40px;
        border: 1px solid rgba(144, 148, 161, 0.2);
        img {
          height: 80px;
        }
        .name {
          margin: 20px 0 40px;
          max-height: 24px;
        }
        a {
          font-size: 12px;
          padding-left: 18px;
          position: relative;
          &::before {
            content: '';
            position: absolute;
            width: 8px;
            height: 8px;
            left: 0;
            top: calc(50% - 4px);
            background-color: $blue;
          }
        }
      }
    }
  }

}